import { Injectable } from '@angular/core';
import { CanDeactivate } from '@angular/router';
import { AssistantSettingsComponent } from './assistant-settings.component';
import { MatDialog } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';

@Injectable()
export class AssitantSettingsDeactivateGuard
  implements CanDeactivate<AssistantSettingsComponent> {
  constructor(private dialog: MatDialog) {}

  canDeactivate(component: AssistantSettingsComponent) {
    if (component.tabAccessFormRef && component.tabAccessFormRef.dirty) {
      return this.openConfirmDialog();
    } else {
      return true;
    }
  }

  openConfirmDialog() {
    return this.dialog
      .open(ConfirmationDialogComponent, {
        closeOnNavigation: false,
        disableClose: true,
        data: {
          title: 'Confirm',
          data: 'You have some unsaved changes left. Do you wish to continue?'
        }
      })
      .afterClosed();
  }
}
