import { Injectable } from '@angular/core';
import { IFetchMasterDataRequest } from '../models/fetch-master-data.request';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { catchError } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { iif, of } from 'rxjs';
import { ApiResponse } from '@core/models/api-response';
import { IFetchMasterDataResponse } from '../models/fetch-master-data.response';

@Injectable({ providedIn: 'root' })
export class EmrOpthalmologyMasterDataService {


  constructor(private opthoApi: EmrOpthoApiService) { }


  fetchMasterData(payload: IFetchMasterDataRequest) {
    return this.opthoApi.fetchComplaintsMasterData<IFetchMasterDataResponse>(payload)
      .pipe(
        catchError((errorResponse: HttpErrorResponse) => {
          return iif(
            () => errorResponse.status > 0,
            of(<ApiResponse<null>>errorResponse.error),
            of(new ApiResponse<null>(null, 'E400', []))
          );
        })
      );
  }



}
