   /**
   * @description  method for saving the data in erx.
   */
  save() {
    if (this.patientParamData.pastVisitDetails === 'true') {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to save ?`
        },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result) {
          this.saveDrugs();
        }
      });
    } else {
      this.saveDrugs();
    }

  }
   /**
   * @description  method for calling save API.
   */
  saveDrugs() {
  //   const modifiedDate = new Date(this.visitDate).toLocaleDateString();
  // const filteredDate = this.datePipe.transform(modifiedDate, 'yyyy-MM-dd')
    if (this.patientDemographicData.patientStatus === 'IPD') {
      this.tableArray = this.tableArray.filter(obj => {
        return !obj.ipdPrescriptionId || obj.oldDrugId;
      });
    } else {
      this.tableArray = this.tableArray.filter(obj => {
        return !obj.prescriptionId || obj.oldDrugId;
      });
    }
    const {
      isSendPharmacy,
      isPrint,
      instructions,
      internalComment,
      noteToPharmacy,
      empSignature
    } = this.erxform.value;
    const {
      patientStatus,
      patientVisitId,
      patientAdmitId
    } = this.patientDemographicData;
    const payload = {
      orgId: this.userData.orgId,
      authenticatedUserId: this.userData.regId,
      orgGrpId: this.userData.orgGroupId,
      isPrint,
      isSendPharmacy,
      patientStatus,
      doctorId: this.patientParamData.doctorId,
      instructions,
      patientVisitId,
      internalComment,
      noteToPharmacy,
      empSignature,
      tabId: this.ERX,
      patientAdmitId: this.patientDemographicData.patientAdmitId,
      patientId: this.patientDemographicData.patientId,
      tabNm: this.tabNm,
      dataList: this.tableArray,
      visitDt: this.visitDate.toString(),
      // isRequired: this.isCheckboxChecked,
      hosPatientId: this.patientDemographicData.hosPatientId
    };
    this.EmrOphthalmologyApiService.saveDrugs(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.snackBar.open('Data saved successfully', 'undo', {
            duration: 3000
          });
          this.isBeforeSave = true;
          this.fetchPatientPastPrescriptions();
          this.ePrescription();
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', { duration: 3000 });
      }
    );
  }
   /**
   * @description  method for searching the drugs whether the selected drug is brand, combination, generic.
   */
  searchDrug() {
    this.patient$ = concat(
      of([]),
      this.patientInput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(searchText => searchText.length > 2),
        switchMap(search => {
          const payload = {
            drugNm: search,
            authenticatedUserId: `${this.userData.regId}`,
            selectCriteria: this.erxform.get('selectCriteria').value,
            orgId: `${this.userData.orgId}`
          };
          return this.EmrOphthalmologyApiService.fetchDrugs(payload).pipe(
            map(response => {
              if (response.responseCode === 'E200') {
                this.isPaginationMessage = false;

                if (this.erxform.get('selectCriteria').value === '1') {
                  this.drugType = null;

                }
                this.erxform.patchValue({ usageQty: ' '});
                return response.data;
              } else if (response.responseCode === 'E400') {
                if (
                  response.responseMessage ===
                  'UNABLE TO FETCH DATA ,PLEASE ENTER ANOTHER CHARACTERS'
                ) {
                  this.isPaginationMessage = true;
                  // this.snackBar.open('Please enter one more character', 'undo', {
                  //   duration: 6000
                  // });
                }
                // throw new Error('failure');
              }
            })
          );
        })
      )
    );
  }
