   /**
   * @description  method for  fetching the favourites which we added through addto rx.
   */
  fetchFavoriates() {
    const payload: ILoadErxRequest = {
      orgId: `${this.userData.orgId}`,
      authenticatedUserId: `${this.userData.regId}`,
      doctorId: `${this.patientParamData.doctorId}`,
      patientId: this.patientDemographicData.patientId,
      orgGrpId: `${this.userData.orgGroupId}`
    };
    this.emrApiService.fetchFavoriates(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {

          this.matchedFavIndex = -1;
        this.isOnClickVisit = false;
          this.favtableArray = response.data;
          this.favoriteDrugArray = response.data;
          this.favtableArray.forEach(data => {
            this.chosenDrugType = data.drugTypeNm;
            this.chooseDrugType();
            data['chosenDrugType'] = this.chosenDrugType;
          });
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', { duration: 3000 });
      }
    );
  }
    /**
   * @description  method for  getting or added patient prescription list
   */
  fetchPatientPastPrescriptions() {
    const payload = {
      orgId: this.userData.orgId,
      authenticatedUserId: this.userData.regId,
      doctorId: this.patientParamData.doctorId,
      patientId: this.patientDemographicData.patientId,
      orgGrpId: this.userData.orgGroupId,
      patientVisitId: this.patientDemographicData.patientVisitId
    };
    this.emrApiService.fetchPatientPastPrescriptions(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200' && response.data) {
          this.isOnClickVisit = false;
          this.pastPrescriptionData = response.data;
          this.erxform.get('instructions').patchValue(this.pastPrescriptionData.drugList[0].instructions);
          this.erxform.get('internalComment').patchValue(this.pastPrescriptionData.drugList[0].internalComment);
          this.erxform.get('noteToPharmacy').patchValue(this.pastPrescriptionData.drugList[0].noteToPharmacy);

          if (
            this.pastPrescriptionData.drugList &&
            !this.pastPrescriptionData.drugList[0].isCurrentDate
          ) {
            this.newDate = new Date();
            console.log(this.currDate);
            this.currDate = this.datePipe.transform(this.newDate, 'yyyy-MM-dd');
            if (
              this.pastPrescriptionData.drugList[0].visitDt === this.currDate
            ) {
              this.pastPrescriptionData.drugList[0]['isCurrentDate'] = true;
              this.pastPrescriptionData.drugList[0].visitDt = 'Today';
            }
          }
          // console.log(document.getEl)
          // this.onClickVisit(this.pastPrescriptionData.drugList[0]);

          if (
            this.pastPrescriptionData.drugList &&
            this.pastPrescriptionData.drugList.length
          ) {
            this.tableArray = this.pastPrescriptionData.drugList[0].drugsList;
            this.tableArray.forEach(data => {
              this.chosenDrugType = data.drugTypeNm;
              this.chooseDrugType();
              data['chosenDrugType'] = this.chosenDrugType;
            });
            //        this.tableArray.forEach((data ,index)=> {
            //   if(data.usageQty > 1) {
            //     this.drugTypeNm + 's';
            //   }
            //   if(data.drugDurationId>1) {

            //   }

            // })
            this.visitDate = this.pastPrescriptionData.drugList[0].visitDt;
          }
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', { duration: 3000 });
      }
    );
  }
