import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { dateFormat } from 'highcharts';
@ Component({
  selector: 'app-consciousness-add-edit-dialog',
  templateUrl: './consciousness-add-edit-dialog.component.html',
  styleUrls: ['./consciousness-add-edit-dialog.component.scss']
})
export class ConsciousnessAddEditComponent implements OnInit , OnDestroy {

  maxDate = new Date();

  ConsctableData: FormGroup;

  isEdit = false;
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();
  formData: any;
  constructor(
    private readonly fb: FormBuilder,
    @ Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef< ConsciousnessAddEditComponent>,
    private datePipe: DatePipe,
  ) {
    this .isEdit = injectedData.edit;
  }

  ngOnInit() {
    const { required, pattern } = Validators;
    this .ConsctableData = this .fb.group({
      version_id: null,
      emr_default_id: null,
      consciousness_capture_dttm: [new Date(), Validators.required],
      consciousness_state: [null],
      consciousness_score: [null, [required, pattern(/^\d+$/)]]
    });
    if (this .injectedData.rowData) {
      const injectedData = this .injectedData.rowData;
      injectedData.consciousness_capture_dttm = new Date(+injectedData.consciousness_capture_dttm);
      this .ConsctableData
        .patchValue(this .injectedData.rowData);
    }
    this.ConsctableData.get('consciousness_score').disable();

    this.ConsctableData.get('consciousness_state').valueChanges.subscribe(data => {
    if (data) {
      if (data === 'Alert') {
        this.ConsctableData.get('consciousness_score').patchValue('0');
      } else if (data === 'New-onset confusion') {
        this.ConsctableData.get('consciousness_score').patchValue('3');
      } else if (data === 'Responds to voice') {
        this.ConsctableData.get('consciousness_score').patchValue('3');
      } else if (data === 'Responds to pain') {
        this.ConsctableData.get('consciousness_score').patchValue('3');
      } else if (data === 'unresponsive') {
        this.ConsctableData.get('consciousness_score').patchValue('3');
      }
    }
    });
//     this.getTime()
//     .pipe(
//       takeUntil(this.componentDestructionNotifier$)
//     )
//    .subscribe(date => {
//      this.ConsctableData.patchValue({
//       consciousness_capture_dttm : date
//      });
//  });
  }
  // getTime() {
  //   return timer(0, 1000).pipe(
  //     map(_ => new Date()),
  //     takeUntil(
  //       this.componentDestructionNotifier$
  //     )
  //   );
  // }
  add() {
    if (this .ConsctableData.valid) {
      this.formData = this .ConsctableData.value;
      this.formData['consciousness_score'] = this.ConsctableData.get('consciousness_score').value;
      this.formData.consciousness_capture_dttm = this.formData.consciousness_capture_dttm.getTime();
      this .dialogRef.close(this.formData);
    }
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
  close() {
    this.dialogRef.close();
  }

}
