import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { dateFormat } from 'highcharts';
@ Component({
  selector: 'app-temperature-add-edit',
  templateUrl: './temperature-add-edit-dialog.component.html',
  styleUrls: ['./temperature-add-edit-dialog.component.scss']
})
export class TemperatureAddEditComponent implements OnInit, OnDestroy {

  temptableData: FormGroup;

  maxDate = new Date();
  isEdit = false;
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();
  formValue: any;
  constructor(
    private readonly fb: FormBuilder,
    @ Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef< TemperatureAddEditComponent>,
    private datePipe: DatePipe,
  ) {
    this .isEdit = injectedData.edit;
  }

  ngOnInit() {
   this.initTemperatureTableFormGroup();
    if (this .isEdit) {
      const injectedData = this .injectedData.rowData;
      injectedData.temperature_capture_dttm = new Date(+injectedData.temperature_capture_dttm);
      this .temptableData.patchValue(injectedData);
    }
    // this.getTime()
    //    .pipe(
    //      takeUntil(this.componentDestructionNotifier$)
    //    )
    //   .subscribe(date => {
    //     this.temptableData.patchValue({
    //       temperature_capture_dttm : date
    //     });
    // });
  }

  // getTime() {
  //   return timer(0, 1000).pipe(
  //     map(_ => new Date()),
  //     takeUntil(
  //       this.componentDestructionNotifier$
  //     )
  //   );
  // }
  initTemperatureTableFormGroup() {
    const { required, pattern } = Validators;
    this.temptableData = this .fb.group({
      version_id: null,
      emr_default_id: null,
      temperature_capture_dttm: [new Date(), required],
      temperature_timing: [null, required],
      temperature_type: [null, required],
      temperature_nomenlature: [
        null,
        [required, pattern(/(^\d+$)|(^\d+\.\d{2}$)/)]
      ],
      temperature_score: [null, [required, pattern(/(^\d+$)|(^\d+\.\d{2}$)/)]]
    });
    this.temptableData.get('temperature_score').disable();
    this.temptableData.get('temperature_nomenlature').valueChanges.subscribe(data => {
      if (data <= 95) {
        this.temptableData.get('temperature_score').patchValue('3');
        } else if (95.1 <= data && data <= 96.8) {
        this.temptableData.get('temperature_score').patchValue('1');
        } else if (96.9 <= data && data <= 100.4) {
        this.temptableData.get('temperature_score').patchValue('0');
        } else if (100.5 <= data && data <= 102.2) {
        this.temptableData.get('temperature_score').patchValue('1');
        } else if (100.5 <= data && data <= 102.2) {
        this.temptableData.get('temperature_score').patchValue('1');
        } else if (102.3 <= data) {
        this.temptableData.get('temperature_score').patchValue('2');
        }
    });

  }

  add() {
    if (this .temptableData.valid) {
      this.formValue = this .temptableData.value;
      this.formValue['temperature_score'] = this.temptableData.get('temperature_score').value;
      this.formValue.temperature_capture_dttm = this.formValue.temperature_capture_dttm.getTime();
      this.dialogRef.close(this.formValue);
    }
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }

  cancel() {
    this .dialogRef.close();
  }

}
