import {
  Directive,
  ElementRef,
  ContentChild,
  AfterContentInit,
  Injector,
  ViewContainerRef,
  Inject,
  ComponentRef,
  Input
} from '@angular/core';
import { myScriptTriggerDirective } from './myscriptTrigger.directive';
import { Renderer2 } from '@angular/core';
import { ComponentPortal } from '@angular/cdk/portal';
import { MyScriptComponent } from '@shared/components/my-script/my-script.component';
import {
  Overlay,
  ScrollStrategyOptions,
  OverlayRef
} from '@angular/cdk/overlay';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import {  NgControl } from '@angular/forms';
import { SCROLL_CONTAINER, WINDOW } from '@core/services';

@Directive({
  selector: '[myScript]'
})
export class MyScriptDirective implements AfterContentInit {

  @ContentChild(myScriptTriggerDirective)
  triggerEle: myScriptTriggerDirective;

  @ContentChild(NgControl)
  writerNgControl: NgControl;

  @Input()
  myScriptTriggerDisabled: boolean;

  writerOverlayRef: OverlayRef;
  componentDestructionNotifier$ = new Subject();
  componentPortal: ComponentPortal<MyScriptComponent>;

  delta = 0;

  constructor(
    @Inject(WINDOW)
    private readonly window: Window,
    @Inject(SCROLL_CONTAINER)
    private readonly scrollContainer: HTMLElement,
    private readonly eleRef: ElementRef<HTMLElement>,
    private readonly renderer: Renderer2,
    private readonly viewContainer: ViewContainerRef,
    private readonly injector: Injector,
    private readonly overlay: Overlay,
    private readonly scrollStragegyOptions: ScrollStrategyOptions
  ) {}

  ngAfterContentInit() {
    this.createWriterOverlay();
    this.writerPortal();

    if (!this.myScriptTriggerDisabled) {
      this.attachClickOnTriggerElement();
    }
  }

  attachClickOnTriggerElement() {
    const element = this.triggerEle ? this.triggerEle.elementRef.nativeElement : this.eleRef.nativeElement;
    this.renderer.listen(
      element, 'click', _ => {

        this.renderer.setStyle(this.eleRef.nativeElement, 'outline', '3px solid cadetblue');

        const componentRef = this.writerOverlayRef.attach(this.componentPortal);
        const writerInstance = componentRef.instance;

        this.scrollUpIfHostUnderWriter(componentRef);

        writerInstance.formControl = this.writerNgControl.control;
        writerInstance.width = this.eleRef.nativeElement.offsetWidth;

      }
    );
  }


  scrollUpIfHostUnderWriter<T>(componentRef: ComponentRef<T>) {

    const hostBoundingBox = this.eleRef.nativeElement.getBoundingClientRect();
    const writerBoundingBox = componentRef.location.nativeElement.getBoundingClientRect();

    const hostOffsetRelativeToViewPort = hostBoundingBox.top + hostBoundingBox.height;
    const writerOffsetRelativeToViewPort = this.window.innerHeight - writerBoundingBox.height ;

    if (writerOffsetRelativeToViewPort < (hostOffsetRelativeToViewPort + 50)) {
      this.delta = hostOffsetRelativeToViewPort - writerOffsetRelativeToViewPort + 50;
      this.renderer.setStyle(this.scrollContainer, 'padding-bottom', `${this.delta}px`);
      this.scrollContainer.scrollBy({ top: this.delta, behavior: 'smooth' });
    }
  }

  createWriterOverlay() {

    const positionStrategy = this.overlay
      .position()
      .global()
      .bottom('0')
      .left('0');

    this.writerOverlayRef = this.overlay.create({
      disposeOnNavigation: true,
      positionStrategy,
      hasBackdrop: true,
      backdropClass: 'mat-overlay-transparent-backdrop',
      scrollStrategy: this.scrollStragegyOptions.close()
    });

    this.writerOverlayRef
      .backdropClick()
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(_ => {

        this.renderer.setStyle(this.scrollContainer, 'padding-bottom', `0px`);
        this.renderer.setStyle(this.eleRef.nativeElement, 'outline', 'none');

        this.scrollContainer.scrollBy({ top: 0, behavior: 'smooth' });
        this.writerOverlayRef.detach();
      });
  }

  writerPortal() {
    this.componentPortal = new ComponentPortal(MyScriptComponent, this.viewContainer, this.injector);
  }

}
