import { Component, OnInit, Input, OnDestroy, Output, EventEmitter} from '@angular/core';
import { IDynamicComponent } from '../../models/dynamic-component';
import { FormGroup, FormBuilder, Validators} from '@angular/forms';
import { Observable, Subject, of, iif } from 'rxjs';
import { takeUntil, filter, first, concatMap, catchError } from 'rxjs/operators';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { UserService } from '@core/services';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-personal-history',
  templateUrl: './personal-history.component.html',
  styleUrls: ['./personal-history.component.scss']
})
export class PersonalHistoryComponent
  implements OnInit, IDynamicComponent, OnDestroy {
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  componentFetchData$: Observable<any>;

  @Input()
  currentTabId?: number;

  @Input()
  patientDemographicData: any;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  personalHistoryForm: FormGroup;

  componentDestructionNotifier$ = new Subject();


  alcoholTypes = [
    'Beer',
    'Gin',
    'Wine',
    'Cider',
    'Baijiu',
    'Tequila',
    'Rum',
    'Whisky',
    'Singani',
    'Soju',
    'Vodka',
    'Brandy'
  ];

  readonly CURRENT_USER = this.user.getUser();
  patientParamData: any;
  selectedType: any;
  componentData: any;
  message: string;

  constructor(
    private readonly snackbar: MatSnackBar,
    private readonly fb: FormBuilder,
    private readonly user: UserService,
    private readonly dialog: MatDialog,
    private readonly emrOpthoService: EmrOpthoApiService,
    private readonly route: ActivatedRoute
  ) {
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    if (this.parentFormGroup) {
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.personalHistoryForm
      );
    }
    this.initComponentDataFetchChangeHandler();
    this.handleRefresh();
  }

  openDataDeleteConfirmationModal(message: string) {

        if ( this.patientParamData.pastVisitDetails === 'true') {
          this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
        } else {
          this.message = 'Do you want delete personal Histroy component data?';
        }
      
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  makeDeleteRequestObj(formData) {
    const { version_id, emr_default_id } = formData;
    const { regId, orgId, orgGroupId } = this.CURRENT_USER;
    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      // bedId: this.patientDemographicData.bedId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id, version_id
        }
      }
    };
    return request;
  }


  deleteField(payload) {
    return this.emrOpthoService.deleteDataOfDynamicField(payload);
  }

  /**
   * @function defaultRadioBtnSelectMethod
   * @description this method is for default selection of radio buttons after deletion
   */
  defaultRadioBtnSelectMethod() {
    this.personalHistoryForm.get('betel_nut_usage').patchValue(false);
    this.personalHistoryForm.get('betel_leaf_usage').patchValue(false);
    this.personalHistoryForm.get('gutkha_usage').patchValue(false);
    this.personalHistoryForm.get('bowel_function').patchValue('regular');
    this.personalHistoryForm.get('micturition_function').patchValue('normal');
  }

  handleRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .personalHistoryForm.value;
        const { version_id } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDataDeleteConfirmationModal('')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeleteRequestObj(this .personalHistoryForm.value))
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this .personalHistoryForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this .personalHistoryForm.reset();
            this.personalHistoryForm.get('smoking_duration_units').clearValidators();
            this.personalHistoryForm.get('chewing_tobacco_duration').clearValidators();
            this.personalHistoryForm.get('consuming_alcohol_duration').clearValidators();
            this.personalHistoryForm.get('smoking_duration_units').updateValueAndValidity();
            this.personalHistoryForm.get('chewing_tobacco_duration').updateValueAndValidity();
            this.personalHistoryForm.get('consuming_alcohol_duration').updateValueAndValidity();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this .dataReset.emit(true);
            this.defaultRadioBtnSelectMethod();
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }

  isButtonSelected(data) {
    this.selectedType = data;
  }
  initForm() {
    const { required, pattern } = Validators;

    this.personalHistoryForm = this.fb.group({
      alochol_type: null,
      smoking_duration: null,
      smoking_duration_units: null,
      smoking_comments: null,
      chewing_tobacco_duration: null,
      chewing_tobacco_duration_units: null,
      chewing_tobacco_comments: null,
      consuming_alcohol_duration: null,
      consuming_alcohol_duration_units: null,
      consuming_alcohol_comments: null,
      alcohol_frequency: null,
      betel_nut_usage: false,
      betel_leaf_usage: false,
      gutkha_usage: false,
      micturition_remarks: null,
      bowel_function: 'regular',
      diet_type: null,
      micturition_function: 'normal',
      ocular_medicine: null,
      exercise_details: null,
      other_details: null,
      emr_default_id: null,
      version_id: null
    });

    if (this.disabled) {
      this.personalHistoryForm.disable();
    } else {
      this.personalHistoryForm.enable();
    }

    [
      'smoking_duration',
      'consuming_alcohol_duration',
      'chewing_tobacco_duration'
    ].forEach(field => {
      this.personalHistoryForm
        .get(`${field}_units`)
        .valueChanges.pipe(
          filter(Boolean),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe(_ => {
          const control = this.personalHistoryForm.get(field);
          control.setValidators([required, pattern(/^\d*$/)]);
          control.updateValueAndValidity();
        });
    });
  }

  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }

  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.personalHistoryForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
}
