import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ViewContainerRef,
  Type,
  Inject,
  Injector,
  ComponentFactoryResolver
} from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { select, Store } from '@ngrx/store';
import {
  getTabsData,
  IEmrOpthoState
} from '../../reducers';
import * as fromTabsListDataReducer from '../../reducers/tabs-list.reducer';
import { takeUntil, concatMap, map, first, filter, tap, share } from 'rxjs/operators';
import { DoctorSpeciality } from '@core/models/user-session';
import { ActivatedRoute, Router } from '@angular/router';
import { UserService } from '@core/services';
import { COMPONENTS_MAP_TOKEN } from '../../static-tab-settings-component-map';

@Component({
  selector: 'app-tab-settings-wrapper',
  templateUrl: './tab-settings-wrapper.component.html'
})
export class TabSettingsWrapperComponent implements OnInit, OnDestroy {

  @ViewChild('componentViewport', { read: ViewContainerRef })
  componentViewportRef: ViewContainerRef;

  tabsListData$: Observable<fromTabsListDataReducer.State> = this.store.pipe(
    select(getTabsData)
  );
  currentTabId: number;

  componentDestructionNotifier$ = new Subject();
  currentSelectedSpeciality: DoctorSpeciality;

  specialityId: number;

  tabId: string;

  isCurrentTabDynamic: boolean;

  userData = this.user.getUser();

  currentSelectedComponent: Type<any>;

  @ViewChild('viewPort', { read: ViewContainerRef })
  private viewPort: ViewContainerRef;

  paramMap$ = this.activatedRoute.paramMap.pipe(share());

  constructor(
    @Inject(COMPONENTS_MAP_TOKEN) private componentsMap: Map<number, Type<any>>,
    private store: Store<IEmrOpthoState>,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private user: UserService,
    private componentFactoryResolver: ComponentFactoryResolver,
    private injector: Injector
  ) {
    this.specialityId = this.activatedRoute.snapshot.data.specialityId;
  }

  navigateToParent() {
    this.router.navigate(['../../'], {
      relativeTo: this.activatedRoute
    });
  }

  ngOnInit() {
    this.paramsValueChangeListener();
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }

  paramsValueChangeListener() {
    this.paramMap$
      .pipe(
        concatMap(paramMap => {
          const tabId = +paramMap.get('tabId');
          return this.tabsListData$.pipe(
            map(data => {
              return data.tabsList
                .find(tab => tab.tabId === tabId);
            }),
            first()
          );
        }),
        tap(tab => {
          if (!tab) {
            this.navigateToParent();
          }
        }),
        filter(tab => !!tab),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(tab => {
        this.isCurrentTabDynamic = tab.isDynamic;
        this.currentTabId = tab.tabId;
      });
  }
}
