import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-iris',
  templateUrl: './iris.component.html',
  styleUrls: ['./iris.component.scss']
})
export class IrisComponent implements OnInit {

  @Input()
  form: FormGroup;
  iriesSectionForm: FormGroup;

  @Input()
  disabled: boolean;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.iriesSectionForm = this.form;
    if (this.disabled) {
      this.iriesSectionForm.disable();
    } else {
      this.iriesSectionForm.enable();
    }
  }

}
