import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef,
  Inject
} from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { UserService } from '@core/services';
import { UserSession, DoctorSpeciality } from '@core/models/user-session';
import { Store, select } from '@ngrx/store';
import {
  IEmrOpthoState,
  getCurrentSelectedSpeciality,
  getTabsData
} from '../../reducers';
import * as specialityValueChangeReducer from '../../reducers/speciality-value-change.reducer';
import * as fromTabsListDataReducer from '../../reducers/tabs-list.reducer';
import { TabsListService } from './tabs-list.service';
import { ToastrService } from 'ngx-toastr';
import { IFetchDynamicTabsResponse } from './models/fetch-dynamic-tabs-response';
import { IUpdateTabRequest } from './models/update-tab-request';
import {
  IEmrOpthalmologyConstants,
  EMR_OPHTHALOMOLGY_CONSTANTS_TOKEN
} from '../../emr-ophthalmology-constants';
import { UpdateDynamicTabName } from '../../actions/emr-ophthalmology-actions';
import { Router, NavigationExtras } from '@angular/router';

@ Component({
  selector: 'app-tabs-list',
  templateUrl: './tabs-list.component.html',
  styleUrls: ['./tabs-list.component.scss']
})
export class TabsListComponent implements OnInit, OnDestroy {
  tabs: Array< IFetchDynamicTabsResponse>;
  customTabName: string;
  reason: string;
  componentDestructionNotifier$ = new Subject();
  prevTabEditableState: boolean;
  currentSelectedSpeciality: DoctorSpeciality;

  currentSelectedSpeciality$: Observable<
    specialityValueChangeReducer.State
  > = this .store.pipe(select(getCurrentSelectedSpeciality));

  tabsListData$: Observable< fromTabsListDataReducer.State> = this .store.pipe(select(getTabsData));

  @ ViewChild('tabsListWrapper')
  tabsListWrapperElement: ElementRef< HTMLDivElement>;

  isLoadingTabs = false;
   public edit = false;
  readonly TABS_ITEM_HEIGHT = 45;

  ghostElements: Array< { opacity: number; width: number }>;

  user: UserSession = this .userService.getUser();

  constructor(
    private readonly tabsListService: TabsListService,
    private readonly userService: UserService,
    private readonly toastr: ToastrService,
    private readonly store: Store< IEmrOpthoState>,
    @ Inject(EMR_OPHTHALOMOLGY_CONSTANTS_TOKEN)
    private readonly emrOpthoConstants: IEmrOpthalmologyConstants,
    private router: Router
  ) {}

  // getTabLink(tab: IFetchDynamicTabsResponse) {
  //   return this .emrOpthoConstants.TAB_LIST_ROUTER_LINK_MAP[tab.tabId];
  // }

  getRandomRange(min, max) {
    return Math.random() * (max - min) + min;
  }

  fetchTabs() {
    const errorToastr = () =>
      this .toastr.error('Unable to get the tabs', 'Error');

    this .tabsListData$
      .pipe(takeUntil(this .componentDestructionNotifier$))
      .subscribe((data) => {
        if (data.isLoading) {
          this .generateGhostElements();

        } else {
          this .ghostElements = [];
        }
        this .isLoadingTabs = data.isLoading;
        if (data.errorOccurred) {
          errorToastr();
        }
        this .tabs = data.tabsList.filter(obj => {
          return obj.isDynamic === true;
        });
        this .router.navigate(['/emr-ophthalmology/settings/tab/9']);
      });
  }

  generateGhostElements() {
    const tabsListWrapper = this .tabsListWrapperElement.nativeElement;
    const approxItemsCanFitIn = Math.round(
      tabsListWrapper.offsetHeight / this .TABS_ITEM_HEIGHT
    );
    this .ghostElements = Array.from(Array(approxItemsCanFitIn)).map(
      (_, index) => {
        return {
          opacity: 1 - (index * 1) / approxItemsCanFitIn,
          width: this .getRandomRange(100, 200)
        };
      }
    );
  }

  initSpecialityValueChangeHandler() {
    this .currentSelectedSpeciality$
      .pipe(takeUntil(this .componentDestructionNotifier$))
      .subscribe(({speciality }) => {
        this .currentSelectedSpeciality = speciality;
      });
  }

  ngOnDestroy() {
    this .componentDestructionNotifier$.next();
    this .componentDestructionNotifier$.complete();
  }

  ngOnInit() {

    this .initSpecialityValueChangeHandler();
    this .fetchTabs();

  }

  tabListItemTrackingfn(_, item: IFetchDynamicTabsResponse) {
    return item.tabId;
  }

  toggleEditTabName(currentTabId: number) {

    const currentTab = this .getCurrentTab(this .tabs, currentTabId);
    this .prevTabEditableState = currentTab.isEditable;
    this .tabs.forEach(tab => {
      tab.isEditable = false;
    });
    currentTab.isEditable = !this .prevTabEditableState;
    if (!currentTab.isEditable) {
      this .saveTabName(currentTab, this .customTabName, this .reason);
    }
    this .customTabName = currentTab.isEditable ? currentTab.tabNm : null;
  }

  saveTabName(currentTab: IFetchDynamicTabsResponse, newTabName: string, reason: string) {
    const errorToastr = () =>
      this .toastr.error('Unable to save the new tab name', 'Error');
    const payload: IUpdateTabRequest = {
      authenticatedUserId: `${this .user.regId}`,
      orgGrpId: `${this .user.orgGroupId}`,
      orgId: `${this .user.orgId}`,
      specialityId: `${this .currentSelectedSpeciality.specialityId}`,
      tabId: `${currentTab.tabId}`,
      tabNm: newTabName,
      orgTabSpecialityId: currentTab.orgTabSpecialityId,
      comments: reason
    };
    this .tabsListService.updateTabs(payload).subscribe(response => {
      if (response.responseCode === 'E200') {

        const savedTab = this .tabs.find(tab => tab.tabId === currentTab.tabId);
        savedTab.tabNm = newTabName;
        savedTab.orgTabSpecialityId = response.data;
        this .store.dispatch(new UpdateDynamicTabName(savedTab));
      } else {
        errorToastr();
      }
    }, errorToastr);
  }

  private getCurrentTab(tabs: IFetchDynamicTabsResponse[], currentTabId) {
    return tabs.find(tab => tab.tabId === currentTabId);
  }
}
