export const COMPONENTS_ID = {
  COMPLAINTS: 140,
  OPHTHALMIC_HISTORY: 141,
  PAST_MEDICAL_HISTORY: 7,
  FAMILY_HISTORY: 8,
  ALLERGIES: 150,
  PERSONAL_HISTORY: 6,
  CONTACT_LENS_PRESCRIPTION: 144,
  CONTRAST_SENSITIVITY: 147,
  VITALS: 4,
  IOP: 74,
  VISION: 73,
  OLD_GLASSES: 75,
  FINAL_GLASSES: 76,
  DILATED_REFRACTION: 71,
  UNDILATED_REFRACTION: 72,
  SYSTEMIC_HISTORY: 200,
  AUTO_REFRACTION: 101,
  PMT: 142,
  KEROTOMETRY: 143,
  ORTHOPITICS: 146,
  COLOR_VISION: 148,
  SYSTEMATIC_EXAMINATION: 78,
  PHYSCICAL_PARAMETERS: 95,
  EYE_EXAMINATION: 151,
  VISIT_TYPE: 164,
  RETINOSCOPY: 145,
  CANVAS: 0,
};

export const componentIdNameMapFactory = () => COMPONENTS_ID;
export const COMPONENT_ID_NAME_TOKEN = new InjectionToken(
  'component name id map',
  {
    factory: componentIdNameMapFactory,
    providedIn: 'root'
  }
);

export const COMPONENTS_MAP = new Map< number, Type< IDynamicComponent>>()
  .set(COMPONENTS_ID.COMPLAINTS, ComplaintsComponent)
  .set(COMPONENTS_ID.PAST_MEDICAL_HISTORY, PastMedicalHistoryComponent)
  .set(COMPONENTS_ID.OPHTHALMIC_HISTORY, OphthalmicHistoryComponent)
  .set(COMPONENTS_ID.FAMILY_HISTORY, FamilyHistoryComponent)
  .set(COMPONENTS_ID.ALLERGIES, AllergiesComponent)
  .set(COMPONENTS_ID.PERSONAL_HISTORY, PersonalHistoryComponent)
  .set(COMPONENTS_ID.CONTRAST_SENSITIVITY, ContrastSensitivityComponent)
  .set(COMPONENTS_ID.CONTACT_LENS_PRESCRIPTION, ContactLensPrescriptionsComponent)
  .set(COMPONENTS_ID.VITALS, VitalsComponentComponent)
  .set(COMPONENTS_ID.VISION, VisionComponent)
  .set(COMPONENTS_ID.FINAL_GLASSES, FinalGlassesComponent)
  .set(COMPONENTS_ID.OLD_GLASSES, OldGlassesComponent)
  .set(COMPONENTS_ID.DILATED_REFRACTION, DilatedRefractionComponent)
  .set(COMPONENTS_ID.UNDILATED_REFRACTION, UndilatedRefractionComponent)
  .set(COMPONENTS_ID.SYSTEMIC_HISTORY, SystematicHistoryComponent)
