import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-conjectiva',
  templateUrl: './conjectiva.component.html',
  styleUrls: ['./conjectiva.component.scss']
})
export class ConjectivaComponent implements OnInit {

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  conjectivaSectionForm: FormGroup;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.conjectivaSectionForm = this.form;
    if (this.disabled) {
      this.conjectivaSectionForm.disable();
    } else {
      this.conjectivaSectionForm.enable();
    }
  }

}
