import { Action } from '@ngrx/store';
import { DoctorSpeciality } from '@core/models/user-session';
import { IFetchDepartmentsResponse } from '../components/assistants-list/models/fetch-departments-response';
import { IFetchEmployeesEmrResponse } from '../components/assistants-list/models/fetch-employees-emr-response';
import { IFetchDynamicTabsRequest } from '../components/tabs-list/models/fetch-dynamic-tabs-request';
import { IFetchDynamicTabsResponse } from '../components/tabs-list/models/fetch-dynamic-tabs-response';
import { IFetchEmployeesEmrRequest } from '../components/assistants-list/models/fetch-employees-emr-request';
import { IFetchPatientsVisitDetailsRequest } from '@app/modules/emr-ophthalmology/components/landing-page/models/fetch-patients-visit-details-request';
import { IFetchPatientsVisitDetailsResponse } from '@app/modules/emr-ophthalmology/components/landing-page/models/fetch-patients-visit-details-response';

export enum EmrOpthoActionTypes {
  SettingsSpecialtySelectorChanged = '[EMR Settings Page] - Specialty selector value changed',
  SettingsDepartmentSelectorChanged = '[EMR Settings Page Assistant Component] - Department selector value changed',

  FetchTabList = '[EMR Settings Page] - Fetch the tab list for the current speciality',
  FetchTabListSuccess = '[Emr Settings Page - Tabs List Effect] Tabs list successfully fetched',
  FetchTabListError = '[Emr Settings Page - Tab List Effect] Error occurred while fetching the tab lists',
  UpdateDynamicTabName = '[Emr Settings Page] - Updating the dynamic tab name',

  FetchAssistantsList = '[Emr Settings Page - Assistant List Component] Fetch Assistant Lists',
  FetchAssistantsListSuccess = '[Emr Settings Page - Assistant List Component] Successfully fetched assistants list',
  FetchAssistantsListError = '[Emr Settings Page - Assistant List Component] Error occurred while fetching the tab list',

  FetchPatientVisitDetails = '[EMR landing Page Component] - Fetching the patient details',
  FetchPatientVisitDetailsSuccess = '[EMR Landing Page Component] - Successfully fetched the patient visit details',
  FetchPatientVisitDetailsError = '[EMR landing Page Component] - Unable to fetch the patient visit details',

  OpenComponentCustomization = '[EMR Settings Page] - Open sideNav ',
  closeComponentCustomization = '[EMR Settings Page] - Close sideNav'

}

export class SettingsSpecialtySelectorChanged implements Action {
  readonly type = EmrOpthoActionTypes.SettingsSpecialtySelectorChanged;
  constructor(public payload: DoctorSpeciality) {}
}

export class SettingsDepartmentSelectorChanged implements Action {
  readonly type = EmrOpthoActionTypes.SettingsDepartmentSelectorChanged;
  constructor(public payload: IFetchDepartmentsResponse) {}
}

// tab list
export class FetchTabList implements Action {
  readonly type = EmrOpthoActionTypes.FetchTabList;
  constructor(public payload: IFetchDynamicTabsRequest) { }
}
export class FetchTabListSuccess implements Action {
  readonly type = EmrOpthoActionTypes.FetchTabListSuccess;
  constructor(public payload: Array<IFetchDynamicTabsResponse>) { }
}
export class FetchTabListError implements Action {
  readonly type = EmrOpthoActionTypes.FetchTabListError;
  constructor() { }
}
export class UpdateDynamicTabName implements Action {
  readonly type = EmrOpthoActionTypes.UpdateDynamicTabName;
  constructor(public payload: IFetchDynamicTabsResponse) { }
}
/////////////////////

// assistant list
export class FetchAssistantsList implements Action {
  readonly type = EmrOpthoActionTypes.FetchAssistantsList;
  constructor(public payload: IFetchEmployeesEmrRequest) { }
}
export class FetchAssistantsListSuccess implements Action {
  readonly type = EmrOpthoActionTypes.FetchAssistantsListSuccess;
  constructor(public payload: Array<IFetchEmployeesEmrResponse>) { }
}
export class FetchAssistantsListError implements Action {
  readonly type = EmrOpthoActionTypes.FetchAssistantsListError;
  constructor() { }
}
/////////////////////


// landing page
export class FetchPatientVisitDetails implements Action {
  readonly type = EmrOpthoActionTypes.FetchPatientVisitDetails;
  constructor(public payload: IFetchPatientsVisitDetailsRequest) { }
}
export class FetchPatientVisitDetailsSuccess implements Action {
  readonly type = EmrOpthoActionTypes.FetchPatientVisitDetailsSuccess;
  constructor(public readonly payload: IFetchPatientsVisitDetailsResponse) { }
}
export class FetchPatientVisitDetailsError implements Action {
  readonly type = EmrOpthoActionTypes.FetchPatientVisitDetailsError;
}

////////
export class OpenComponentCustomization implements Action {
  readonly type = EmrOpthoActionTypes.OpenComponentCustomization;
  constructor (public payload: number) { }
}

export class CloseComponentCustomization implements Action {
  readonly type = EmrOpthoActionTypes.closeComponentCustomization;
}

export type EmrOpthoActions =
  | SettingsSpecialtySelectorChanged
  | SettingsDepartmentSelectorChanged
  | FetchTabList
  | FetchTabListSuccess
  | FetchTabListError
  | FetchAssistantsList
  | FetchAssistantsListSuccess
  | FetchAssistantsListError
  | FetchPatientVisitDetails
  | FetchPatientVisitDetailsSuccess
  | FetchPatientVisitDetailsError
  | UpdateDynamicTabName
  | OpenComponentCustomization
  | CloseComponentCustomization;
