import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject, of, iif } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-color',
  templateUrl: './color.component.html',
  styleUrls: ['./color.component.scss']
})
export class ColorComponent implements OnInit, IDynamicComponent, OnDestroy {
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: any;

  @Input()
  refreshForm$: Subject<boolean>;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  componentFetchData$: Observable<any>;
  colorComponentForm: FormGroup;
  componentDestructionNotifier$ = new Subject();
  readonly CURRENT_USER = this.user.getUser();
  public anytext;
  public restext;
  color_form_object = {
    Color: [
      {
        type: 'maxlength',
        message: '* Field Data allows upto 500 characters'
      }
    ]
  };
  patientParamData: any;
  componentData: any;
  message: string;
  public updatetext() {
    this.restext = this.anytext;
  }
  constructor(
    private readonly fb: FormBuilder,
    private readonly dialog: MatDialog,
    private readonly user: UserService,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) {}
  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    this.initComponentDataFetchChangeHandler();
    if (this.parentFormGroup) {
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.colorComponentForm
      );
    }
    this.handleRefresh();
  }
  openDataDeleteConfirmationModal(message: string) {
    // if (this.componentData) {
      if (this.componentData.version_id === !null && this.patientParamData.pastVisitDetails) {
        this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
        // }
      } else {
        this.message = 'Do you want delete Color Vision component data?';

}
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'confirm',
          data: this.message
        }
      })
      .afterClosed();
  }
  makeDeleteRequestObject(componentData) {
    // console.log(componentData, 'hhhhhhhhhhhhh');
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }
  onClickCopy() {
    this.colorComponentForm.get('right_color_vision_remarks').patchValue
    (this.colorComponentForm.get('left_color_vision_remarks').value);
    if(this.colorComponentForm.get('left_color_vision_type').value === "leftCp1") {
      this.colorComponentForm.get('right_color_vision_type').patchValue("rightCp1")
    }
    if(this.colorComponentForm.get('left_color_vision_type').value === "leftCp2") {
      this.colorComponentForm.get('right_color_vision_type').patchValue("rightCp2")
    }
    if(this.colorComponentForm.get('left_color_vision_type').value === "leftCp3") {
      this.colorComponentForm.get('right_color_vision_type').patchValue("rightCp3")
    }



  }
  deleteField(payload) {
    return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // if (
    //   payload.data[this.componentId].emr_default_id &&
    //   payload.data[this.componentId].version_id
    // ) {
    //   return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // } else {
    //   this.colorComponentForm.reset();
    // }
  }

  handleRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .colorComponentForm.value;
        const { version_id, child_table } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDataDeleteConfirmationModal('')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeleteRequestObject(this .colorComponentForm.value))
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this .colorComponentForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this .colorComponentForm.reset();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this .dataReset.emit(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }

  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.colorComponentForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
  initForm() {
    const validator = [null, Validators.maxLength(500)];
    this.colorComponentForm = this.fb.group({
      emr_default_id: null,
      version_id: null,
      left_color_vision_remarks : validator,
      right_color_vision_remarks: validator,
      right_color_vision_type: null,
      left_color_vision_type: null
    });

    if (this.disabled) {
      this.colorComponentForm.disable();
    } else {
      this.colorComponentForm.enable();
    }
  }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
