import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { dateFormat } from 'highcharts';
@Component({
  selector: 'app-respiration-add-edit',
  templateUrl: './respiration-add-edit-dialog.component.html',
  styleUrls: ['./respiration-add-edit-dialog.component.scss']
})
export class RespiratoinAddEditDialogComponent implements OnInit, OnDestroy {
  ResptableData: FormGroup;
  maxDate = new Date();
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();
  isEdit = false;
  formData: any;

  constructor(
    private readonly fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef<RespiratoinAddEditDialogComponent>,
    private datePipe: DatePipe,
  ) {
    this.isEdit = injectedData.edit;
  }

  ngOnInit() {
    const { required, pattern } = Validators;
    this.ResptableData = this.fb.group({
      version_id: null,
      emr_default_id: null,
      respiratory_rate_capture_dttm: [new Date(), required],
      respiratory_rate: [null, [required, pattern(/^\d+$/)]],
      respiratory_rate_score: [null, [required, pattern(/^\d+$/)]]
    });
    this.ResptableData.get('respiratory_rate_score').disable();
    this.ResptableData.get('respiratory_rate').valueChanges.subscribe(data => {
      if (data) {
       if (data <= 8 || data >= 25 ) {
        this.ResptableData.get('respiratory_rate_score').patchValue('3');

        } else if (9 <= data && data <= 11) {
        this.ResptableData.get('respiratory_rate_score').patchValue('1');

        } else if (12 <= data && data <= 20) {
        this.ResptableData.get('respiratory_rate_score').patchValue('0');

        } else if (21 <= data && data <= 24) {
        this.ResptableData.get('respiratory_rate_score').patchValue('2');

        }
      }
    });


    if (this.isEdit) {
      const injectedData = this.injectedData.rowData;
      injectedData.respiratory_rate_capture_dttm = new Date(+injectedData.respiratory_rate_capture_dttm);
      this.ResptableData.patchValue(this.injectedData.rowData);
    }
    // this.getTime()
    //    .pipe(
    //      takeUntil(this.componentDestructionNotifier$)
    //    )
    //   .subscribe(date => {
    //     this.ResptableData.patchValue({
    //       respiratory_rate_capture_dttm : date
    //     });
    // });
  }
  // getTime() {
  //   return timer(0, 1000).pipe(
  //     map(_ => new Date()),
  //     takeUntil(
  //       this.componentDestructionNotifier$
  //     )
  //   );
  // }
  add() {
    if (this.ResptableData.valid) {
          this.formData = this.ResptableData.value;
          this.formData['respiratory_rate_score'] = this.ResptableData.get('respiratory_rate_score').value;
      this.formData.respiratory_rate_capture_dttm = this.formData.respiratory_rate_capture_dttm.getTime();
      this.dialogRef.close(this.formData);

    }
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
  close() {
    this.dialogRef.close();
  }

}
