import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-gonio',
  templateUrl: './gonio.component.html',
  styleUrls: ['./gonio.component.scss']
})
export class GonioComponent implements OnInit {
  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;


  goneoSectionForm: FormGroup;

  ngOnInit() {
  const isSettingsPage = true;
    this.initForm();
    if (this.disabled) {
      this.goneoSectionForm.disable();
    } else {
      this.goneoSectionForm.enable();
    }
  }

  initForm() {
    this.goneoSectionForm = this.form;
  }

}
