  /**
   * this is the common function for all componets to submit the form
   * If isUpdating true and update index value greater than -1 will insert the edit details in a table
   * otherwise else condition will work and push the non edited form values to the table
   */
  ngOnInit() {
    this.initComponentDataFetchChangeHandler();
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initControl();
    if (this.parentFormGroup) {
      this.parentFormGroup.addControl(`${this.componentId}`, this.VitalsForm);
    }
    this.handleFormRefresh();
    this.initcomponentFetchDataChangeListener();
    if(this.disabled) {
      this.VitalsForm.disable();
    }else{
      this.VitalsForm.enable();

    }
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }

