  /**
   * getTime function is to take the updated current date and time in a input field in a form
   */
  getTime() {
    return timer(0, 1000).pipe(
      map(_ => this.pipe.transform(new Date(), 'dd/MM/yyyy, HH:mm')),
      takeUntil(merge(this.componentDestructionNotifier$, this.endTimer$))
    );
  }

  openAddEditTemperatureDialog(edit = false, index?: number) {
    const temperatureChildTable = <FormArray>(this.temperatureForm.get('tableData'));

    this.dialog
      .open(TemperatureAddEditComponent, {
        data: {
          edit,
          rowData: edit ? temperatureChildTable.at(index).value : null
        }
      })
      .afterClosed()
      .pipe(filter(Boolean))
      .subscribe(data => {
        if (edit) {
          temperatureChildTable.at(index).patchValue(data);
        } else {
          temperatureChildTable.push(this.fb.group(data));
        }
      });
  }

