  openDeleteDialog(message: string) {
    if (this.patientParamData.pastVisitDetails === 'true') {
      this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
    } else {
      this.message = 'Do you want to delete Vitals component data?';
    }

    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  deleteField(payload) {
    return this.emrOpthoService.deleteDataOfDynamicField(payload);
  }

  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.componentData = componentData;
    }
  }
  makeDeletePayload() {
    const formValue = this.VitalsForm.value;
    const child_table = formValue.child_table
      .filter(row => {
        const rowChildTable = row.tableData;
        return rowChildTable && rowChildTable.every(val => val.version_id);
      })
      .map(row => {
        return {
          childComponentId: row.childComponentId,
          tableData: row.tableData.map(val => ({
            version_id: val.version_id,
            emr_default_id: val.emr_default_id
          }))
        };
      });
    const { regId, orgGroupId, orgId } = this.userData;

    return {
      specialityId: this.specialityId,
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      // bedId: this.patientDemographicData.bedId,
      data: {
        [this.componentId]: {
          version_id: formValue.version_id,
          emr_default_id: formValue.emr_default_id,
          child_table
        }
      }
    };
  }

  deleteRow(i, formGroup: FormGroup, formArrayName: string) {
    const child_component_id = formGroup.get('childComponentId').value;
    const formArray = <FormArray>formGroup.get(formArrayName);
    const row = formArray.at(i);
    if (row) {
      const rowValue = row.value;
      const { version_id, emr_default_id } = rowValue;

      if (version_id) {
        this.openDeleteDialog('Do you want to delete this record?')
          .pipe(
            filter(Boolean),
            concatMap(_ =>
              this.deleteField({
                emr_default_id,
                version_id,
                // patientId: this.patientDemographicData.patientId,
                // patientVisitId: this.patientDemographicData.patientVisitId,
                orgId: this.userData.orgId,
                componentId: child_component_id,
                authenticatedUserId: this.userData.regId
              }).pipe(
                catchError((errorResponse: HttpErrorResponse) => {
                  return iif(
                    () => errorResponse.status > 0,
                    of(<ApiResponse<null>>errorResponse.error),
                    of(new ApiResponse<null>(null, 'E400', []))
                  );
                })
              )
            ),
            takeUntil(this.componentDestructionNotifier$)
          )
          .subscribe(response => {
            if (response.responseCode === 'E200') {
              formArray.removeAt(i);
            } else {
              this.snackbar.open('Unable the delete the row', 'Dismiss', { duration: 3000 });
            }
          });
      } else {
        formArray.removeAt(i);
      }
    }
  }

  handleFormRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .VitalsForm.value;
        const { version_id} = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || tableData.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDeleteDialog('Do you want to delete Vitals component\'s data')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeletePayload())
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            const childTableFormArray = <FormArray>this.VitalsForm.get('child_table');
            const childTableControls = childTableFormArray.controls;
            for (let i = 0, j = childTableControls.length; i < j; i++) {
              const group = <FormGroup>childTableFormArray.at(i);
              const tableData = <FormArray>group.get('tableData');
              while (tableData.controls.length) {
                tableData.removeAt(tableData.controls.length - 1);
              }
            }
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }

