import {
  EmrOpthoActionTypes,
  EmrOpthoActions
} from '../actions/emr-ophthalmology-actions';

export interface State {
  componentId: number;
}

export const INITIAL_STATE: State = {
  componentId: null
};

export function reducer(
  state: State = INITIAL_STATE,
  action: EmrOpthoActions
): State {
  switch (action.type) {
    case EmrOpthoActionTypes.OpenComponentCustomization: {
      return { componentId: action.payload };
    }
    default: {
      return state;
    }
  }
}
