import {
  Component,
  OnInit,
  OnDestroy,
  Input,
  Output,
  EventEmitter
} from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { DatePipe } from '@angular/common';
import { UserService, UtilService, MOMENT } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { takeUntil } from 'rxjs/operators';
import { Subject, Observable } from 'rxjs';
import { IFetchPatientsVisitDetailsRequest } from '@app/modules/emr-ophthalmology/components/landing-page/models/fetch-patients-visit-details-request';
import { Store, select } from '@ngrx/store';
import {
  IEmrOpthoState,
  getLandingPageCurrentSelectedAssistantDetails
} from '@app/modules/emr-ophthalmology/reducers';
import * as fromLandingPageCurrentPatientVisitDetials from '../../reducers/patient-visit-details.reducer';
import { Router, NavigationExtras } from '@angular/router';
import { EMR_OPHTHALOMOLGY_CONSTANTS } from '@app/modules/emr-ophthalmology/emr-ophthalmology-constants';
import { IUpdateOrInsertPatientDilationRequest, IupdateConsultStartDateOfAppointment } from '@app/modules/emr-ophthalmology/components/landing-page/models/insert-or-update-patient-dilation.request';
import { MatSnackBar } from '@angular/material';
import { GlassDetailsService } from '../../services/glass-details.service';

@Component({
  selector: 'app-landing-page',
  templateUrl: './landing-page.component.html',
  styleUrls: ['./landing-page.component.scss']
})
export class LandingPageComponent implements OnInit, OnDestroy {
  @Output()
  patientData: EventEmitter<any> = new EventEmitter();
  @Input()
  onSelectPatientData;

  appointmentList: any;
  allPatientsAppointmentsList = [];
  patientMiscInfoId: any;
  age: number;
  currentPatientVisitDetails$: Observable<
    fromLandingPageCurrentPatientVisitDetials.State
  > = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));

  componentDestroyNotifier$ = new Subject();
  selectedPatientDetails: any;
  date = new Date();
  displayData = false;
  allData = true;
  duePatientsAppointmentsList = [];
  holdAppointments = [];
  holdData = false;
  hideSearchedDetails = false;
  showSearchedDetails = false;
  dueData = false;
  landingPageForm: FormGroup;
  placeholderValue: string;
  menuType: 'op';
  search: string;
  visitEmrDate = new Date();
  searchControl: any;
  searchedPatientData: any = [];
  patientSearchData: any;
  menuKey: any;
  selectedPatient: number;
  selectedSearchPatient: number;
  hideTabs = true;
  fstpatient = [];
  dilation = [
    '5 minutes',
    '10 minutes',
    '15 minutes',
    '20 minutes',
    '25 minutes',
    '30 minutes',
    '40 minutes',
    '45 minutes'
  ];

  selectedDilationTime: string;
  dilationTimer = true;
  fetchPatientVisitDetails: any = [];
  public isEnableSearch = true;
  @Output()
  navigateToDiagnosis = new EventEmitter;
  pastVisitDetails: any;
  bloodPressure: number;
  navigatedVisitId: any;

  constructor(
    private datePipe: DatePipe,
    private EmrOphthalmologyApiService: EmrOpthoApiService,
    private landingPageFormBuilder: FormBuilder,
    private snackBar: MatSnackBar,
    private userService: UserService,
    private utilService: UtilService,
    private readonly store: Store<IEmrOpthoState>,
    private router: Router
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.menuKey = 'OP';
    this.fetchAppointmentsForLandingPage();
    this.initForm();
    this.searchControl = new FormControl('mobileNo');
    this.placeholderValue = 'Enter Your Mobile No';
    this.search = 'mobileNo';
    this.searchControl.valueChanges.subscribe(value => {
      this.search = value;
      this.landingPageForm.patchValue({ patientSearchData: null });
      this.isEnableSearch = true;
      switch (this.search) {
        case 'patientNm':
          this.placeholderValue = 'Enter Your Name';
          break;
        case 'aadhaarNo':
          this.placeholderValue = 'Enter Your aadhar No';
          break;
        case 'hosPatientId':
          this.placeholderValue = 'Enter Your Patient Id';
          break;
        default:
          this.placeholderValue = 'Enter Your Mobile No ';
          break;
      }
    });
  }

  initForm() {
    this.landingPageForm = this.landingPageFormBuilder.group({
      patientSearchData: '',
      menuType: 'op',
      hosPatientId: '',
      dilationPeriod: ''
    });
    this.landingPageForm
      .get('menuType')
      .valueChanges.pipe(takeUntil(this.componentDestroyNotifier$))
      .subscribe(menuType => (this.menuType = menuType));

    this.landingPageForm.get('dilationPeriod').valueChanges.subscribe(val => {
      if (val) {
        this.selectedDilationTime = this.landingPageForm.get('dilationPeriod').value;
        if (this.dilationTimer === true) {
          this.insertOrUpdatePatientDilation();
        }
      }
    });
    this.landingPageForm.get('patientSearchData').valueChanges.subscribe(val => {
      if (val && val.length > 3) {
        this.isEnableSearch = false;
      } else {
        this.isEnableSearch = true;
      }
    });
  }/**
   * @description This method is as common method for the visitLinks
   */

  visitDate() {
    const data = this.searchedPatientData[0].visitList[0];
    const date = this.datePipe.transform(this.visitEmrDate, 'dd/MM/yyyy');
    if (data) {
      if (data.patientStatus === 'IPD') {
        if (data.dischargeDt) {
          this.pastVisitDetails = true;
        } else if (!data.dischargeDt) {
          this.pastVisitDetails = false;
        }

      } else if (data.patientStatus === 'OPD') {
        if (data.visitDt === date) {
          this.pastVisitDetails = false;
        } else {
          this.pastVisitDetails = true;
        }
      } else {
        this.pastVisitDetails = true;
      }
    } else {
      this.pastVisitDetails = false;
    }
  }
  /**
   * @description This method is used to visit link to emr
   */
  visitLinkToEmr() {
    if (this.searchedPatientData.length) {
      if (this.searchedPatientData[0].visitList[0]) {
        this.visitDate();
        console.log(this.fetchPatientVisitDetails);
      }
    } else {
      this.pastVisitDetails = false;
    }

    if (this.userSession.regId === this.fetchPatientVisitDetails.doctorId) {
      this.updateConsultStartDateOfAppointment();
    }

    const navigationExtras: NavigationExtras = {
      queryParams: {
        type: 'emr',
        specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID,
        patientId: this.selectedPatientDetails.patientId,
        doctorId: this.fetchPatientVisitDetails.doctorId,
        key: 'visitDt',
        pastVisitDetails: this.pastVisitDetails,
        visitDate: this.navigatedVisitId.visitDt,
        patientVisitId: this.navigatedVisitId.patientVisitId,

      }
    };
    this.router.navigate(['/emr-ophthalmology/emr'], navigationExtras);
  }
  /**
     * @description This method is used to visit link to cope
     */

  visitLinkToCope() {
    if (this.searchedPatientData.length) {
      if (this.searchedPatientData[0].visitList[0]) {
        this.visitDate();
      }
    } else {
      this.pastVisitDetails = false;
    }

    const navigationExtras: NavigationExtras = {
      queryParams: {
        type: 'cpoe',
        visitDate: this.navigatedVisitId.visitDt,
        patientVisitId: this.navigatedVisitId.patientVisitId,
        specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID,
        doctorId: this.fetchPatientVisitDetails.doctorId,
        patientId: this.selectedPatientDetails.patientId,
        navigate: 'cpoe',
        pastVisitDetails: this.pastVisitDetails

      }
    };
    this.router.navigate(['/emr-ophthalmology/emr'], navigationExtras);
  }
  /**
   * @description This method is used to visit link to erx
   */
  visitLinkToErx() {
    if (this.searchedPatientData.length) {
      if (this.searchedPatientData[0].visitList[0]) {
        this.visitDate();
      }
    } else {
      this.pastVisitDetails = false;
    }
    const navigationExtras: NavigationExtras = {
      queryParams: {
        type: 'erx',
        visitDate: this.navigatedVisitId.visitDt,
        patientVisitId: this.navigatedVisitId.patientVisitId,
        specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID,
        doctorId: this.fetchPatientVisitDetails.doctorId,
        navigate: 'erx',
        patientId: this.selectedPatientDetails.patientId,
        pastVisitDetails: this.pastVisitDetails

      }
    };
    this.router.navigate(['/emr-ophthalmology/emr'], navigationExtras);

  }

  /**
   * @description This method is used to fetch the appointments for the particular doctor in landing Page
   */
  fetchAppointmentsForLandingPage() {
    const payload = {
      authenticatedUserId: `${this.userSession.regId}`,
      currentDt: this.datePipe.transform(this.date, 'yyyy-MM-dd '),
      doctorId: this.userSession.regId,
      orgId: this.userSession.orgId,
      key: this.menuKey,
      orgGrpId: this.userSession.orgGroupId,
      specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID
    };
    this.EmrOphthalmologyApiService.fetchAppointmentsForWaitingRoom(
      payload
    ).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.patientData = response.data;
          this.appointmentList = response.data;
          this.displayData = false;
          if (this.appointmentList.length) {
            this.displayData = true;
          }
          this.allPatientsAppointmentsList = this.appointmentList.allAppointments;
          if (this.allPatientsAppointmentsList.length) {
            this.onSelectPatient(this.allPatientsAppointmentsList[0], 0, 'landingPage');
          }
          this.duePatientsAppointmentsList = this.appointmentList.dueAppointments;
          this.holdAppointments = this.appointmentList.holdAppointments;
          this.appointmentList = this.allPatientsAppointmentsList;
          this.allData = true;
          this.dilationTimer = true;
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('Something Went Wrong', 'undo', { duration: 3000 });
      }
    );
  }

  /**
   * @description This method is used to get the selected patient gender
   */

  getAvatorImg(item) {
    const image = item && item.gender === 'Female' ? 'female-ico.png' : 'male-ico.png';
    return `https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons/${image}`;
  }

  /**
   * @description This method is used to get the selected patient gender
   */
  hideDetails() {
    this.hideSearchedDetails = false;
    this.showSearchedDetails = !this.hideSearchedDetails;
  }
  showDetails() {
    this.hideSearchedDetails = true;
    this.showSearchedDetails = !this.hideSearchedDetails;
  }

  /**
   * @description This method is used to get the selected allpatient list details
   */
  selectedAllPatientsList() {
    this.appointmentList = this.allPatientsAppointmentsList;
    this.allData = true;
    this.dueData = false;
    this.holdData = false;
    this.displayData = false;
  }
  /**
   * @description This method is used to get the selected holdpatient list details
   */
  selectedHoldPatientsList() {
    this.appointmentList = this.holdAppointments;
    this.holdData = true;
    this.dueData = false;
    this.allData = false;
    this.displayData = false;
  }
  /**
   * @description This method is used to get the selected duepatient list details
   */
  selectedDuePatientsList() {
    this.appointmentList = this.duePatientsAppointmentsList;
    this.dueData = true;
    this.allData = false;
    this.holdData = false;
    this.displayData = false;
  }
  /**
   * @description This method is used to get the selected patient details
   */
  ipClick() {
    this.menuKey = 'IP';
    this.allData = true;
    this.dueData = false;
    this.holdData = false;
    this.fetchAppointmentsForLandingPage();
  }

  opClick() {
    this.menuKey = 'OP';
    this.allData = true;
    this.dueData = false;
    this.holdData = false;
    this.fetchAppointmentsForLandingPage();

  }



  /**
   * @description This method is used to get the selected patient details
   */
  onSelectPatient(item, i, type) {
    this.selectedPatientDetails = item;
    if (type === 'landingPage') {
      this.selectedPatient = i;
      this.selectedSearchPatient = null;
      this.hideTabs = true;
    } else {
      this.selectedSearchPatient = i;
      this.selectedPatient = null;
      this.hideTabs = false;
    }
    this.selectedPatientDetails = item;
    const payload: IFetchPatientsVisitDetailsRequest = {
      authenticatedUserId: `${this.userSession.regId}`,
      doctorId: (item && item.doctorId) ? item.doctorId : `${this.userSession.regId}`,
      orgId: this.userSession.orgId,
      patientVisitId: item.patientVisitId,
      patientId: item.patientId,
      specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID
    };
    // this .store.dispatch(new FetchPatientVisitDetails(payload));
    this.EmrOphthalmologyApiService.fetchPatientVisitDetails(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.fetchPatientVisitDetails = response.data;
          console.log(this.fetchPatientVisitDetails);
          this.navigatedVisitId = this.fetchPatientVisitDetails;
          this.displayData = true;
          this.dilationTimer = false;
          this.patientMiscInfoId = this.fetchPatientVisitDetails.patientMiscInfoId;
          this.landingPageForm.patchValue({ dilationPeriod: this.fetchPatientVisitDetails.dilationPeriod });
          this.age = this.utilService
            .parseAgeIndays(this.fetchPatientVisitDetails.age)
            .get('patientAge');
          this.dilationTimer = true;
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something Went Wrong', 'undo', { duration: 3000 });
      }
    );
  }
  /**
  * @description This method is used to get the patch the value to the dilationSelect value
  */
  dilationTypeComparator(o1, o2) {
    return o1 && o2 && o1 === o2;
  }
  /**
   * @description This method is used to get the search the patient
   */
  Patientsearch(search, patientSearchData) {
    const searchData = this.landingPageForm.get('patientSearchData').value;
    if (searchData.length > 2) {
      const payload = {
        authenticatedUserId: this.userSession.regId,
        doctorId: this.userSession.regId,
        orgId: this.userSession.orgId,
        searchKey: search,
        key: searchData
      };
      this.EmrOphthalmologyApiService.searchPatient(payload).subscribe(
        response => {
          if (response.responseCode === 'E200') {
            this.searchedPatientData = response.data;
            this.hideSearchedDetails = true;
            this.landingPageForm.patchValue({ patientSearchData: null });
          } else if (response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'undo', { duration: 3000 });
          }
          this.isEnableSearch = true;
        },
        () => {
          this.snackBar.open('something Went Wrong', 'undo', { duration: 3000 });
        }
      );
    }
  }

  /**
   * @description This method is insert or update the dilation timing
   */
  insertOrUpdatePatientDilation() {
    const payload: IUpdateOrInsertPatientDilationRequest = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.userSession.regId,
      orgId: this.userSession.orgId,
      patientVisitId: this.selectedPatientDetails.patientVisitId,
      specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID,
      patientId: this.selectedPatientDetails.patientId,
      dilationStartTm: this.datePipe.transform(
        this.date,
        'yyyy-MM-dd  hh:mm:ss '
      ),
      dilation: this.selectedDilationTime.substring(0, 2),
      patientMiscInfoId: this.patientMiscInfoId || null
    };
    this.EmrOphthalmologyApiService.insertOrUpdatePatientDilation(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something Went Wrong', 'undo', { duration: 3000 });
      }
    );
  }
  /**
  * @description This method is updateConsultStartDateOfAppointment
  */
  updateConsultStartDateOfAppointment() {
    const payload: IupdateConsultStartDateOfAppointment = {
      authenticatedUserId: this.userSession.regId,
      orgId: this.userSession.orgId,
      patientVisitId: this.selectedPatientDetails.patientVisitId,

    };
    this.EmrOphthalmologyApiService.updateConsultStartDateOfAppointment(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          // this .snackBar.open(response.responseMessage, 'undo', {duration: 3000 });
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', { duration: 3000 });
      }
    );
  }
  /**
  * @description This method is updateConsultStartDateOfAppointment
  */
  visitToEmrPage(item, index) {
    const data = this.datePipe.transform(this.visitEmrDate, 'dd/MM/yyyy');
    if (index === 0) {
      if (item.patientStatus === 'IPD') {
        if (item.dischargeDt) {
          this.pastVisitDetails = true;
        } else {
          this.pastVisitDetails = false;
        }

      } else if (item.patientStatus === 'OPD') {
        if (item.visitDt === data) {
          this.pastVisitDetails = false;
        } else  {
          this.pastVisitDetails = true;
        }
      } else {
        this.pastVisitDetails = true;
      }
    } else {
      this.pastVisitDetails = true;
    }
    const navigationExtras: NavigationExtras = {
      queryParams: {
        doctorId: item.doctorId,
        key: 'visitDt',
        navigate: 'summary',
        patientId: item.patientId,
        pastVisitDetails: this.pastVisitDetails,
        visitDate: item.visitDt,
        patientVisitId: item.patientVisitId,
      }
    };

    this.router.navigate(['/emr-ophthalmology/emr'], navigationExtras);

  }

  /**
  * @description This method is updateConsultStartDateOfAppointment
  */
  onPatientselect(item) {
    this.fetchPatientVisitDetails = item;
    if (this.fetchPatientVisitDetails.visitList.length) {
      this.navigatedVisitId = this.fetchPatientVisitDetails.visitList[0];
    } else {
      this.navigatedVisitId = this.fetchPatientVisitDetails;
    }
    this.displayData = true;
  }
  ngOnDestroy() {
    this.componentDestroyNotifier$.next();
    this.componentDestroyNotifier$.complete();
  }
}
