import { Component, OnInit } from '@angular/core';
import { MatDatepicker } from '@angular/material/datepicker';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserService, EmrApiService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { ToastrService } from 'ngx-toastr';
import * as S3 from 'aws-sdk/clients/s3';
import { FormControl, RequiredValidator } from '@angular/forms';
import { FileuploadService } from '../../services/fileupload.service';
import { Observable } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-files-upload',
  templateUrl: './files-upload.component.html',
  styleUrls: ['./files-upload.component.scss']
})
export class FilesUploadComponent implements OnInit {
  minDate;
  sessionstorage: UserSession;
  fromDate: FormControl;
  toDate: FormControl;
  maxDate = new Date();
  showFileUploader = false;
  documentsData: any = [];
  DatesList = [];
  subscriber;
  apiresponse;
  emptyrecords = false;
  temporaryDocumentData = [];
  fromDateRequired = false;
  public bucket = new S3(this.emrapiservice.getBucketsConfig());
  patientParamData: any;
  constructor(private router: Router,
    private activatedRoute: ActivatedRoute,
    private emropthoapiservice: EmrOpthoApiService,
    private usersession: UserService,
    private toastr: ToastrService,
    private emrapiservice: EmrApiService,
    private filesuploadservice: FileuploadService,
    private dialog: MatDialog,
    private datepipe: DatePipe,
    private snackbar: MatSnackBar
  ) { }

  ngOnInit() {
    this.activatedRoute.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.sessionstorage = this.usersession.getUser();
    this.fetchDocuments();
    this.fromDate = new FormControl('');
    this.toDate = new FormControl('');
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  filesUploaderPage() {
    const navigationExtras: NavigationExtras = {
      queryParams: {
        ...this.patientParamData,
        isEdit: false,
      }
    };
    this.router.navigate(['/emr-ophthalmology/files-uploader'], navigationExtras);
  }
  deleteDocument(Id, url, doctorId,index) {
    doctorId = doctorId.toString();
    if (this.patientParamData.doctorId !== doctorId) {
      this.snackbar.open('You Are Unauthorized to delete', 'UNDO', {
        duration: 1500
      });
    } else {
      const dialogref = this.dialog.open(ConfirmationDialogComponent,
        {
          data: {
            title: 'Confirm',
            data: 'Do you want to delete this Record'
          },
          disableClose: true,
        });
      dialogref.afterClosed().subscribe((result) => {
        if (result) {
          const payload = {

            orgId: this.sessionstorage.orgId,
            authenticatedUserId: this.sessionstorage.regId,
            patientVisitId: this.patientParamData.patientVisitId,
            patientDocumentId: Id,
            deleteReason: null
          };

          this.emropthoapiservice.deletePatientUploadedDocuments(payload).subscribe((response) => {
            if (response.responseCode === 'E400 ') {
              this.snackbar.open('Some Thing Went Wrong', 'Cancel', {
                duration: 1500
              });

            } else if (response.responseCode === 'E200') {
              const bucketdetails = this.emrapiservice.getBucketName();
              this.snackbar.open('Deleted SuccesFully', 'OK', {
                duration: 1500
              });
              const params = {
                Bucket: bucketdetails.bucketURL,
                Key: url.split('/')[4]
              };
              this.bucket.deleteObject(params, (err, data) => {
              });
              this.documentsData.splice(index,1);
            }
          });

        }
      });
    }


  }
  fetchDocuments() {
    let apiResponse;
    const payload = {
      orgId: this.sessionstorage.orgId,
      authenticatedUserId: this.sessionstorage.regId,
      patientId: this.patientParamData.patientId,
      doctorId: this.patientParamData.doctorId,
    };
    this.emropthoapiservice.fetchPatientUploadedDocuments(payload).subscribe((data) => {
      if (data.responseCode === 'E400') {

        this.snackbar.open('something Went wrong', 'Cancel', {
          duration: 1500
        });
      }
      apiResponse = data;
      if (apiResponse.data.length === 0) {
        this.emptyrecords = true;
      }
      else {
        this.emptyrecords = false;
      }
      let tempDocumentData
      for (let i = 0; i <= apiResponse.data.length - 1; i++) {
        tempDocumentData = [...apiResponse.data[i].documentList];

        this.documentsData = this.documentsData.concat(apiResponse.data[i].documentList)

        this.emptyrecords = false
      }



    });
  }

  /**
   *
   * @description edits the document Details
   * * */
  edit(userdata) {
    const navigationExtras: NavigationExtras = {
      queryParams: {
        ...this.patientParamData,
        documentName: userdata,
        isEdit: true,
      }
    };
    this.subscriber = this.filesuploadservice.setData(userdata);
    this.router.navigate(['/emr-ophthalmology/files-uploader'], navigationExtras);
  }
  view(response) {
    const orderRePrint = new Blob([response], {
      type: 'application/pdf'
    });
    const reportURL = URL.createObjectURL(orderRePrint);
    const win = window.open('', '_blank');
    win.location.href = reportURL;
  }
  onChangeDates(fromdate, toDate) {
    const frmDt = this.datepipe.transform(fromdate._validSelected, 'dd-MM-yyyy');
    const toDt = this.datepipe.transform(toDate._validSelected, 'dd-MM-yyyy');
    if (toDate._validSelected !== null && toDate._validSelected < fromdate._validSelected) {

      this.snackbar.open('Check your Dates', 'Cancel', {
        duration: 1500

      });

    }
    const payload = {
      orgId: this.sessionstorage.orgId,
      authenticatedUserId: this.sessionstorage.regId,
      patientId: this.patientParamData.patientId,
      fromDt: frmDt,
      toDt: toDt,
      doctorId: this.patientParamData.doctorId,
    };
    this.emropthoapiservice.fetchPatientUploadedDocuments(payload).subscribe((response) => {
      if (response.responseCode === 'E400') {
        this.snackbar.open('SomeThing WentWrong', 'Cancel', {
          duration: 1500
        });
      } else if (response.responseCode === 'E200') {
        this.apiresponse = response;
        this.minDate = fromdate._validSelected;

        let tempDocumentData
        if (this.apiresponse.data.length === 1) {
          this.documentsData = this.apiresponse.data[0].documentList;
          this.emptyrecords = false
        } else if (this.apiresponse.data.length === 0) {
          this.emptyrecords = true;
        }
        else {

          for (let i = 0; i <= this.apiresponse.data.length - 1; i++) {
            tempDocumentData = [...this.documentsData, ...this.apiresponse.data[i].documentList];
            // this.documentsData.concat(this.apiresponse.data[i].documentList)
            // this.documentsData[i].date = this.apiresponse.data[i].date;
            this.emptyrecords = false
          }
          this.documentsData = tempDocumentData
        }
      }
    });
  }
}
