import { Injectable } from '@angular/core';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { IFetchDepartmentsRequest } from './models/fetch-departments-request';
import { IFetchDepartmentsResponse } from './models/fetch-departments-response';
import { IFetchEmployeesEmrRequest } from './models/fetch-employees-emr-request';
import { IFetchEmployeesEmrResponse } from './models/fetch-employees-emr-response';

@Injectable({
  providedIn: 'root'
})
export class AssistantListService {
  constructor(private readonly emrOpthService: EmrOpthoApiService) {}

  fetchDepartments(payload: IFetchDepartmentsRequest) {
    return this.emrOpthService.fetchDepartments<Array<IFetchDepartmentsResponse>>(payload);
  }

  fetchEmployeesHavingAccessOnEmr(payload: IFetchEmployeesEmrRequest) {
    return this.emrOpthService.fetchEmployeesHavingAccessOnEmr<Array<IFetchEmployeesEmrResponse>>(payload);
  }

}
