import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-pupil',
  templateUrl: './pupil.component.html',
  styleUrls: ['./pupil.component.scss']
})
export class PupilComponent implements OnInit {

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  pupilForm: FormGroup;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.pupilForm = this.form;
    if (this.disabled) {
      this.pupilForm.disable();
    } else {
      this.pupilForm.enable();
    }
  }

}
