import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { TabsListService } from './tabs-list.service';
import { EmrOpthoActionTypes, FetchTabList, FetchTabListSuccess, FetchTabListError } from '../../actions/emr-ophthalmology-actions';
import { mergeMap, map, catchError } from 'rxjs/operators';
import { of } from 'rxjs';

@Injectable()
export class TabListEffects {

  @Effect()
  fetchTabs$ = this.action$
    .pipe(
      ofType<FetchTabList>(EmrOpthoActionTypes.FetchTabList),
      mergeMap(
        (action) => this.tabsListService.getTabs(action.payload)
          .pipe(
            map((response) => {
              if (response.responseCode === 'E200') {
                return new FetchTabListSuccess(response.data);
              } else {
                return new FetchTabListError();
              }
            }),
            catchError(_ => of(new FetchTabListError()))
          )
      )
    );

  constructor(
    private action$: Actions,
    private tabsListService: TabsListService
  ) {}
}
