import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef,
  AfterViewInit
} from '@angular/core';
import { Store, select } from '@ngrx/store';
import {
  IEmrOpthoState,
  getAssistantsData
} from '../../reducers';
import * as fromAssistantListReducer from '../../reducers/assistant-list.reducer';

import { Observable, Subject, fromEvent, of, iif, throwError } from 'rxjs';
import {
  takeUntil,
  switchMap,
  catchError,
  tap,
  filter,
  exhaustMap,
  finalize,
  concatMap,
  map,
  first
} from 'rxjs/operators';
import { ActivatedRoute, Router } from '@angular/router';
import { IFetchEmployeesEmrResponse } from '../assistants-list/models/fetch-employees-emr-response';
import { DoctorSpeciality, UserSession } from '@core/models/user-session';
import {
  AbstractControl,
  FormBuilder,
  FormArray,
  FormGroup,
  NgForm
} from '@angular/forms';
import { AssistantSettingService } from './assistant-settings.service';
import { IEmployeeTabDetailsRequest } from './models/employee-tab-details-request';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import {
  IEmployeeTab,
  IComponentDetails,
  IEmployeeTabDetailsResponse
} from './models/employee-tab-details-response';
import { IInsertOrUpdateEmployeeTabsAndComponentsMappingRequest } from './models/insert-or-update-employee-tabs-and-component-mapping-request';
import { ToastrService } from 'ngx-toastr';
import {
  IFetchTabsPermissionedEmployeeRequest,
  IFetchTabsPermissionedEmployeeObject,
  IFetchTabsPermissionedEmployeeResponse
} from './models/fetch-tabs-permissioned-employee-request';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { IUnMapEmployeeFromTabRequest } from './models/un-map-employees-from-tab.request';

@ Component({
  selector: 'app-assistant-settings',
  templateUrl: './assistant-settings.component.html',
  styleUrls: ['./assistant-settings.component.scss']
})
export class AssistantSettingsComponent
  implements OnInit, OnDestroy, AfterViewInit {
  @ ViewChild('submitBtn')
  submitBtn: ElementRef< HTMLButtonElement>;

  @ ViewChild('tabAccessFormRef')
  tabAccessFormRef: NgForm;

  isSubmitInProgress = false;

  isLoadingTabData = false;
  isDisplayTabsData = false;

  accessToggerControl: AbstractControl;

  componentDestructionNotifier$ = new Subject();

  componentsAccessList = [];
  componentsAccessListForm: FormGroup;

  currentAssistant: IFetchEmployeesEmrResponse;
  currentSpeciality: DoctorSpeciality;

  currentRouteAssistantId: string;

  currentTab: IEmployeeTab;

  tabsAccessForm: FormGroup = this.fb.group({});

  userData: UserSession;
  oldEmployeesSettingsList: IFetchTabsPermissionedEmployeeResponse = [];
  oldEmployeeSettingsSelector: AbstractControl;
  specialityId: number;
  assistantsData$: Observable<fromAssistantListReducer.State> =
    this
      .store
      .pipe(select(getAssistantsData));
  readonly TABS_ACCESS_LIST_FORM_ARRAY_NAME = 'tabsList';
  readonly COMPONENTS_ACCESS_LIST_FORM_ARRAY_NAME = 'componentsList';

  constructor(
    private readonly store: Store<IEmrOpthoState>,
    private readonly activatedRoute: ActivatedRoute,
    private readonly router: Router,
    private readonly userService: UserService,
    private readonly toaster: ToastrService,
    private readonly fb: FormBuilder,
    private readonly dialog: MatDialog,
    private readonly assistantSettingService: AssistantSettingService,
    private snackBar: MatSnackBar,
  ) {
    this.specialityId = this.activatedRoute.snapshot.data.specialityId;
    this.userData = this.userService.getUser();
  }

  accessTogglerChangeHandler() {
    if (this .tabsAccessForm) {
      const doesEmployeeHasEmrAccess = this .accessToggerControl.value;
      if (doesEmployeeHasEmrAccess === true) {
        this.isDisplayTabsData = true;
      } else {
        this.isDisplayTabsData = false;
      }
      const tabsList = (this .tabsAccessForm
        .value as IEmployeeTabDetailsResponse).tabsList;

      const newTabsList = tabsList.map(({ componentsList, ...restTab }) => {
        const _tab: IEmployeeTab = {
          ...restTab,
          ...{ isApplicable: doesEmployeeHasEmrAccess },
          componentsList: []
        };
        const _componentList = componentsList.map(component => {
          const _component = {
            ...component,
            ...{ isApplicable: doesEmployeeHasEmrAccess }
          };
          return _component;
        });
        _tab.componentsList = _componentList;
        return _tab;
      });

      this .tabsAccessForm.patchValue({
        [this.TABS_ACCESS_LIST_FORM_ARRAY_NAME]: newTabsList
      });

      if (this .tabsAccessForm.pristine) {
        this .tabsAccessForm.markAsDirty();
      }

    }
  }

  attachSubmitBtnClickHandler() {
    fromEvent(this .submitBtn.nativeElement, 'click')
      .pipe(
        tap(_ => {
          this .isSubmitInProgress = true;
        }),
        exhaustMap(_ =>
          iif(
            () => this .accessToggerControl.value,
            this .saveTabAndComponentsMappingData(),
            this .unmapEmplyeeFromTabs()
          ).pipe(finalize(() => (this .isSubmitInProgress = false)))
        ),
        takeUntil(this .componentDestructionNotifier$)
      )
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          if (response.data && response.data['tabsList'] ) {
            this .initTabAccessForm(response.data['tabsList']);
          }
          this .tabsAccessForm.markAsPristine();
          this .fetchTabsPermissionedEmployeeRequest();
          this .componentsAccessListForm = null;
          this.snackBar.open('Data saved succesfully', 'undo', {
            duration: 3000
          });
          // this .toaster.success('Tab and components mapping successfully saved', 'Success'
          // );
        }  else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('Something went wrong', 'Dismiss', {
          duration: 3000
        });
      });
  }

  get componentAccessListControls() {
    const componentAccessListFormArray = < FormArray>(
      this .componentsAccessListForm.get(
        this .COMPONENTS_ACCESS_LIST_FORM_ARRAY_NAME
      )
    );
    if (componentAccessListFormArray) {
      return componentAccessListFormArray.controls;
    } else {
      return [];
    }
  }

  get currentSelectedTabName() {
    return this .componentsAccessListForm.get('tabNm').value;
  }

  componentVisibilityTogglerChangeHandler(componentAccessGroup: FormGroup) {
    const componentsListFormArray = < FormArray>componentAccessGroup.parent;
    const componentsList = < Array< IComponentDetails>>(
      componentsListFormArray.value
    );

    const isEveryComponentIsApplicable = componentsList.some(
      component => component.isApplicable
    );
    const parentFormGroup = < FormGroup>componentsListFormArray.parent;

    parentFormGroup.patchValue({ isApplicable: isEveryComponentIsApplicable });

    this .setAccessTogglerState();
  }

  fetchEmployeeTabDetails(
    empId: string,
    specialityId: string,
    oldEmpId: string = null
  ) {
    const payload: IEmployeeTabDetailsRequest = {
      orgId: `${this .userData.orgId}`,
      specialityId,
      authenticatedUserId: `${this .userData.regId}`,
      doctorId: `${this .userData.regId}`,
      empId,
      oldEmpId
    };
    return this .assistantSettingService.fetchEmployeeTabDetails(payload).pipe(
      catchError((errorResponse: HttpErrorResponse) => {
        return iif(
          () => errorResponse.status > 0,
          of(< ApiResponse< null>>errorResponse.error),
          of(new ApiResponse< null>(null, 'E400', []))
        );
      })
    );
  }

  fetchTabsPermissionedEmployeeRequest() {
    const payload: IFetchTabsPermissionedEmployeeRequest = {
      authenticatedUserId: `${this.userData.regId}`,
      doctorId: `${this.userData.regId}`,
      orgId: `${this.userData.orgId}`,
      specialityId: `${this.specialityId}`,
      empId: `${this.currentAssistant.empId}`
    };
    this.assistantSettingService
      .fetchTabsPermissionedEmployee(payload)
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.oldEmployeesSettingsList = response.data;
        } else {
          this.oldEmployeesSettingsList = [];
        }
      });
  }

  getAssistantPlaceholderImage() {
    const assistant = this .currentAssistant;
    // const basePath = 'assets/images';
    // return `${basePath}/emr-${(assistant && assistant.gender) || 'male'}.svg`;
    if (assistant.gender === 'female') {
      const basePath = 'https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons';
      return `${basePath}/female-ico.png`;
      } else {
        const basePath = 'https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons';
        return `${basePath}/male-ico.png`;
      }
  }

  initControls() {
    this .accessToggerControl = this .fb.control('GRANT_ACCESS');
    this .oldEmployeeSettingsSelector = this .fb.control(null);
  }

  isCurrentTabActivated(tabGroup: FormGroup) {
    const currentTabId = tabGroup.value.tabId;
    const selectedTabId =
      this .componentsAccessListForm &&
      this .componentsAccessListForm.value.tabId;
    return currentTabId === selectedTabId;
  }

  get tabsAccessListControls() {
    const tabsAccessListFormArray = < FormArray>(
      this .tabsAccessForm.get(this .TABS_ACCESS_LIST_FORM_ARRAY_NAME)
    );
    if (tabsAccessListFormArray) {
      return tabsAccessListFormArray.controls;
    } else {
      return [];
    }
  }

  initTabAccessForm(tabsList: Array<IEmployeeTab>) {
    this.tabsAccessForm = this.fb.group({});
    const tabAccessList = tabsList.map(tabAccess => {
      const {
        componentsList,
        ...restTabProperties
      } = tabAccess;
      const doctorStaffId = tabAccess.doctorStaffId ?  tabAccess.doctorStaffId : null;
      return this .fb.group({
        doctorStaffId,
        ...restTabProperties,
        componentsList: this .fb.array(
          componentsList.map(
            ({
              staffComponentsPermissionId = null,
              ...restComponentProperties
            }) =>
              this .fb.group({
                staffComponentsPermissionId,
                ...restComponentProperties
              })
          )
        )
      });
    });
    this .tabsAccessForm.addControl(
      this .TABS_ACCESS_LIST_FORM_ARRAY_NAME,
      this .fb.array(tabAccessList)
    );
  }

  initParamsChangeHandler() {
    this .activatedRoute.paramMap
      .pipe(
        concatMap(paramMap => {
          const assistantId = +paramMap.get('assistantId');
          return this.assistantsData$.pipe(
            map(data => {
              if (data.assistantList && data.assistantList.length) {
                  const currentAssistant = data.assistantList
                    .find(assistant => assistant.empId === assistantId);
                return currentAssistant;
              }
            }),
            first()
          );
        }),
        tap(currentAssistant => {
          if (!currentAssistant) {
            this.navigateToSettings();
          }
        }),
        filter(currentAssistant => !!currentAssistant),
        tap(currentAssistant => {
          this.currentAssistant = currentAssistant;
          this.tabsAccessForm = null;
          this.componentsAccessListForm = null;
          this.isLoadingTabData = true;
          this.oldEmployeeSettingsSelector.setValue(null);
          this.fetchTabsPermissionedEmployeeRequest();
        }),
        switchMap((currentAssistant) =>
          this.fetchEmployeeTabDetails(
            `${this.currentAssistant.empId}`,
            `${this.specialityId}`
          ).pipe(
            finalize(() => {
              this .isLoadingTabData = false;
            })
          )
        ),
        takeUntil(this .componentDestructionNotifier$)
      )
      .subscribe(response => {
        if (response && response.responseCode === 'E200') {
          if (response.data.tabsList) {
            this .initTabAccessForm(response.data.tabsList);
            this .setAccessTogglerState();
          }
        } else {
          this .oldEmployeeSettingsSelector.setValue(null);
          this .toaster.error(
            'Unable to fetch tabs and components mapping',
            'Error'
          );
        }
      });
  }

  navigateToSettings() {
    this .router.navigate(['../../'], {
      relativeTo: this .activatedRoute
    });
  }

  ngAfterViewInit() {
    this .attachSubmitBtnClickHandler();
  }

  ngOnDestroy() {
    this .componentDestructionNotifier$.next();
    this .componentDestructionNotifier$.complete();
  }

  ngOnInit() {
    this .initControls();
    // current assistant and speciality must be known before attaching params change handler
    this.initParamsChangeHandler();
  }

  oldEmployeeSettingsSelectorChangeHandler() {
    const currentOldEmp = this .oldEmployeeSettingsSelector
      .value as IFetchTabsPermissionedEmployeeObject;

    this .openConfirmationModal(currentOldEmp.empNm)
      .pipe(
        tap(result => {
          if (!result) {
            this .oldEmployeeSettingsSelector.setValue(null);
          }
        }),
        filter(result => result),
        switchMap(_ =>
          this.fetchEmployeeTabDetails(
            `${this.currentAssistant.empId}`,
            `${this.specialityId}`,
            `${currentOldEmp.oldEmpId}`
          )
        ),
        takeUntil(this .componentDestructionNotifier$)
      )
      .subscribe(response => {
        if (response && response.responseCode === 'E200') {
          this .initTabAccessForm(response.data.tabsList);

          const tabId = this .componentsAccessListForm.value.tabId;
          const tabsListArray = this .tabsAccessForm.get(
            this .TABS_ACCESS_LIST_FORM_ARRAY_NAME
          ) as FormArray;
          const tabIndex = tabsListArray.controls.findIndex(control => {
            const value = < IEmployeeTab>control.value;
            return value.tabId === tabId;
          });

          this .componentsAccessListForm = < FormGroup>tabsListArray.at(tabIndex);
          this .toaster.success(
            `Successfully applied ${currentOldEmp.empNm}'s settings`,
            'Success'
          );
        } else {
          this .toaster.error(
            `Unable to apply ${currentOldEmp.empNm}'s settings`,
            'Error'
          );
        }
      });
  }

  openConfirmationModal(name: string) {
    return this .dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirmation',
          data: `Do you want to import ${name}'s Permissions to ${this.currentAssistant.empNm}`
        }
      })
      .afterClosed();
  }

  saveTabAndComponentsMappingData() {
    const tabsList = (this .tabsAccessForm.value as IEmployeeTabDetailsResponse)
      .tabsList;
    const _tabList = tabsList.map(tab => {
      const { componentsList, ...rest } = tab;
      const _tab = {
        ...rest,
        componentsList: []
      };
      // api needs fieldsList, as an array, to be present for each component even though there are no fields
      const _componentList = componentsList.map(component => {
        component.fieldsList = [];
        if (!_tab.isApplicable) {
          component.isApplicable = false;
        }
        return component;
      });
      _tab.componentsList = _componentList;
      return _tab;
    });

    const payload: IInsertOrUpdateEmployeeTabsAndComponentsMappingRequest = {
      authenticatedUserId: `${this.userData.regId}`,
      empId: `${this.currentAssistant.empId}`,
      orgGrpId: `${this.userData.orgGroupId}`,
      orgId: `${this.userData.orgId}`,
      specialityId: `${this.specialityId}`,
      tabsList: _tabList
    };

    return this .assistantSettingService
      .insertOrUpdateEmployeeTabsAndComponentsMapping(payload)
      .pipe(
        catchError((errorResponse: HttpErrorResponse) => {
          return iif(
            () => errorResponse.status > 0,
            of(< ApiResponse< null>>errorResponse.error),
            of(new ApiResponse< null>(null, 'E400', []))
          );
        })
      );
  }

  saveComponentsAccessListFormGroupInstance(tabSettingGroup: FormGroup) {
    this .componentsAccessListForm = tabSettingGroup;
  }

  setAccessTogglerState() {
    const tabsList = (this .tabsAccessForm.value as IEmployeeTabDetailsResponse)
      .tabsList;
    const isEveryTabApplicable = tabsList.some(tab => tab.isApplicable);
    this .accessToggerControl.patchValue(isEveryTabApplicable);
    if (this .accessToggerControl.value === true) {
      this.isDisplayTabsData = true;
    } else {
      this.isDisplayTabsData = false;
    }
  }

  tabVisibilityTogglerChangeHandler(tabSettingGroup: FormGroup) {
    const isTabVisible = tabSettingGroup.get('isApplicable').value;
    const componentsArray = < FormArray>(
      tabSettingGroup.get(this .COMPONENTS_ACCESS_LIST_FORM_ARRAY_NAME)
    );
    const componentsList = < Array< IComponentDetails>>componentsArray.value;
    componentsArray.patchValue(
      componentsList.map(({ isApplicable, ...rest }) => ({
        isApplicable: isTabVisible,
        ...rest
      }))
    );

    this .setAccessTogglerState();
  }

  unmapEmplyeeFromTabs() {
    const payload: IUnMapEmployeeFromTabRequest = {
      authenticatedUserId: this.userData.regId,
      empId: this.currentAssistant.empId,
      orgId: this.userData.orgId,
      specialityId: this.specialityId
    };
    return this .assistantSettingService.unmapEmplyeeFromTabs(payload).pipe(
      catchError((errorResponse: HttpErrorResponse) => {
        return iif(
          () => errorResponse.status > 0,
          of(< ApiResponse< null>>errorResponse.error),
          of(new ApiResponse< null>(null, 'E400', []))
        );
      })
    );
  }
}
