import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { DoctorSpeciality, UserSession } from '@core/models/user-session';
import { UserService } from '@core/services';
import { AbstractControl, FormBuilder } from '@angular/forms';
import { Subject, Observable } from 'rxjs';
import { takeUntil, first } from 'rxjs/operators';
import { Store, select } from '@ngrx/store';
import { IEmrOpthoState, getCurrentSelectedSpeciality, getSideNavData, getTabsData } from '../../reducers';
import * as currentSelectedSpeciality from '../../reducers/speciality-value-change.reducer';
import * as fromSizenavReducer from '../../reducers/sideNav.reducer';

import {
  SettingsSpecialtySelectorChanged,
  FetchTabList
} from '../../actions/emr-ophthalmology-actions';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { IFetchDynamicTabsRequest } from '../tabs-list/models/fetch-dynamic-tabs-request';
import { MatSidenav } from '@angular/material';
import * as fromTabsListDataReducer from '../../reducers/tabs-list.reducer';
@Component({
  selector: 'app-emr-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit, OnDestroy {
  @ViewChild('sidenav')
  sidenav: MatSidenav;

  componentId: number;

  componentDestructionNotifier$ = new Subject();

  doctorSpecialityList: Array<DoctorSpeciality>;

  specialitySelectorControl: AbstractControl;

  settingsSpecialityId: number = this.activatedRoute.snapshot.data.specialityId;

  tabsSelectorControl: AbstractControl;

  user: UserSession = this.userService.getUser();


  sidenavData$: Observable<fromSizenavReducer.State> = this.store.pipe(select(getSideNavData));

  tabsListData$: Observable<fromTabsListDataReducer.State> = this.store.pipe(
    select(getTabsData)
  );

  currentSelectedSpeciality$: Observable<
    currentSelectedSpeciality.State
  > = this.store.pipe(select(getCurrentSelectedSpeciality));

  constructor(
    private readonly userService: UserService,
    private readonly fb: FormBuilder,
    private readonly store: Store<IEmrOpthoState>,
    private readonly activatedRoute: ActivatedRoute,
    private router: Router
  ) {}

  getDoctorSpecialtyList() {
    this.doctorSpecialityList = this.user.doctorSpecialityList;
  }

  // this method to used to get only the opthalmology specality in selct specality dropDown.

  // getDoctorSpecialtyList() {
  //   this.doctorSpecialityList = this.settingsSpecialityId
  //     ? this.user.doctorSpecialityList.filter(
  //         speciality => speciality.specialityId === this.settingsSpecialityId
  //       )
  //     : this.user.doctorSpecialityList;
  // }

  initControls() {
    this.specialitySelectorControl = this.fb.control(this.doctorSpecialityList[0]);

    this.tabsSelectorControl = this.fb.control('COMPONENTS');
    this.store.dispatch(new SettingsSpecialtySelectorChanged(this.specialitySelectorControl ? this.specialitySelectorControl.value : null));

    this.store.dispatch(this.fetchTabs( this.specialitySelectorControl.value.specialityId));
    // this.router.navigate(['/emr-ophthalmology/this.'])

    this.specialitySelectorControl.valueChanges
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(this.specialtyChangeEventHandler.bind(this));

  }

  initSidenavDataChangeListener() {
    this.sidenavData$.pipe(
      takeUntil(this.componentDestructionNotifier$)
    ).subscribe(data => {
      if (data.componentId) {
        this.componentId = data.componentId;
        this.sidenav.open();
      }
    });
  }
  initSpecialityValueChangeListener() {
    this.currentSelectedSpeciality$
      .pipe(
        first(),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(({ speciality }) => {
        if (speciality) {
          this.specialitySelectorControl
            .patchValue(speciality, { emitEvent: false, onlySelf: true });
        }
      });
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }

  ngOnInit() {
    this.getDoctorSpecialtyList();
    this.initControls();
    this.initSidenavDataChangeListener();
    this.initSpecialityValueChangeListener();
    this.tabsListData$.subscribe(data => {
      console.log(data);
    });
  }

  specialityCompartor(c1: DoctorSpeciality, c2: DoctorSpeciality) {
    return c1 && c2 ? c1.specialityId === c2.specialityId : c1 === c2;
  }

  specialtyChangeEventHandler(specialty: DoctorSpeciality) {
    this.store.dispatch(new SettingsSpecialtySelectorChanged(specialty));
    this.store.dispatch(this.fetchTabs(specialty.specialityId));
  }

  fetchTabs(specialityId) {
    const payload: IFetchDynamicTabsRequest = {
      authenticatedUserId: `${this.user.regId}`,
      orgId: `${this.user.orgId}`,
      specialityId
    };
    return new FetchTabList(payload);
  }

  openSideNav() {
    // this.sidenav.open();
    // this.store.dispatch(
    //   new OpenComponentCustomization(
    //     // {componentId : this.componentId}
    //   )
    // );
  }

  closeSideNav() {
    // this.sideNavRef.close();
    // this.store.dispatch(new CloseComponentCustomization());
  }
  assistantsClick() {
    const navigationExtras: NavigationExtras = {
      queryParams: {
    type: 'assistants'
      }
    };
    this .router.navigate(['/emr-ophthalmology/settings'], navigationExtras);
  }
  componentsClick() {
    const navigationExtras: NavigationExtras = {
      queryParams: {
    type: 'components'
      }
    };
    this .router.navigate(['/emr-ophthalmology/settings/tab/9'], navigationExtras);
  }
}
