import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { GlassDetailsService } from '@app/modules/emr-ophthalmology/services/glass-details.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MatDialog, MatSnackBar } from '@angular/material';
import { IDynamicComponent } from '../../models/dynamic-component';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { UserService } from '@core/services';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { Observable, Subject, iif, of } from 'rxjs';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-final-glasses',
  templateUrl: './final-glasses.component.html',
  styleUrls: ['./final-glasses.component.scss']
})
export class FinalGlassesComponent
  implements OnInit, IDynamicComponent, OnDestroy {
  finalGlassesLensForm: FormGroup;
  public typesOfLensData = [];
  public lensMaterialData = [];
  public lensTintData = [];
  public lensMaterialFrameData = [];
  public isAccordionOpen = false;
  public lensData = [];
  public lensDataArray = [];
  public temModelName;
  public isPlusMinusValues = false;
  public isTableDataDisable = true;
  public rightlensFormData;
  public tableType;
  public plusDatavalue;
  public minusDataValue;
  public selectedLens;
  lensArray = [];
  public lens_form_validations = {
    validate: [
      { type: 'required', message: '* Field Data is required' },
      {
        type: 'maxlength',
        message: '* Fields data cannot be more than 12 characters'
      },
      {
        type: 'pattern',
        message: ' Only Alpha-Numeric,Positive,Negative,Decimal values'
      }
    ],
    advice: [
      {
        type: 'maxlength',
        message: '* Field Data allows upto 250 characters'
      }
    ]
  };
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: any;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  @Input()
  componentFetchData$: Observable<any>;

  componentDestructionNotifier$ = new Subject();

  readonly CURRENT_USER = this.user.getUser();
  patientParamData: any;
  componentData: any;
  message: string;

  constructor(
    private readonly glassesData: GlassDetailsService,
    private readonly fb: FormBuilder,
    private readonly user: UserService,
    private readonly dialog: MatDialog,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.typesOfLensData = this.glassesData.typesOfLensData;
    this.lensMaterialData = this.glassesData.lensMaterialData;
    this.lensTintData = this.glassesData.lensTintData;
    this.lensMaterialFrameData = this.glassesData.lensFrameMaterialData;
    this.initForm();
    if (this.parentFormGroup) {
      // console.log(this.parentFormGroup);
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.finalGlassesLensForm
      );
    }
    this.initComponentDataFetchChangeHandler();
    this.handleRefresh();
  }

  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {


    if ( this.patientParamData.pastVisitDetails === 'true') {
      this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
    } else {
      this.message = 'Do you want delete Final Glasses component data?';
    }

    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }
  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // if (
    //   payload.data[this.componentId].emr_default_id &&
    //   payload.data[this.componentId].version_id
    // ) {
    //   return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // } else {
    //   return this.finalGlassesLensForm.reset();
    // }
  }
  /**
   * @description This method is used for deleting the component date when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this.refreshForm$) {

      const predicate = () => {
        console.log(this.finalGlassesLensForm.value);
        const formValue = this.finalGlassesLensForm.value;
        console.log(formValue + 'pmt');
        const { version_id, child_table } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this.refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this.openDataDeleteConfirmationModal('Do you want to delete Final Glasses component\'s data')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this.makeDeleteRequestObject(this.finalGlassesLensForm.value))
                .pipe(
                  concatMap(request => {
                    return this.deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(<ApiResponse<null>>errorResponse.error),
                          of(new ApiResponse<null>(null, 'E400', []))
                        );
                      }));
                  }
                  )
                ),
              of(new ApiResponse<null>(null, 'E200', [])));
          }),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.finalGlassesLensForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this.finalGlassesLensForm.reset();
            const isFinalGlasses = true;
            this.finalGlassesLensForm.get('is_final_glasses').patchValue(isFinalGlasses);
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this.dataReset.emit(true);
          } else {
            this.snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  initForm() {
    const validator = [
      null,
      [Validators.pattern(/^[-pPnN0-9.+-/']*$/), Validators.maxLength(12)]
    ];
    this.finalGlassesLensForm = this.fb.group({
      emr_default_id: null,
      version_id: null,
      is_final_glasses: true,
      distance_right_sph: validator,
      distance_right_cyl: validator,
      distance_right_axis: validator,
      distance_right_vision: null,
      add_right_sph: validator,
      add_right_cyl: validator,
      add_right_axis: validator,
      add_right_vision: validator,
      near_right_sph: validator,
      near_right_cyl: validator,
      near_right_axis: validator,
      near_right_vision: validator,
      distance_left_sph: validator,
      distance_left_cyl: validator,
      distance_left_axis: validator,
      distance_left_vision: null,
      add_left_sph: validator,
      add_left_cyl: validator,
      add_left_axis: validator,
      add_left_vision: validator,
      near_left_sph: validator,
      near_left_cyl: validator,
      near_left_axis: validator,
      near_left_vision: validator,
      right_lens_advice: null,
      left_lens_advice: null,
      right_type_lens: null,
      left_type_lens: null,
      right_lens_material: null,
      left_lens_material: null,
      right_lens_tint: null,
      left_lens_tint: null,
      right_frame_material: null,
      left_frame_material: null,
      ipd_left: null,
      ipd_right: null
    });

    if (this.disabled) {
      this.finalGlassesLensForm.disable();
    } else {
      this.finalGlassesLensForm.enable();
    }
  }

  /**
   * @description This method is used to open accordion and getting the data from table
   */
  onClickLensFields(modelName, type) {

    if (this.disabled) {
      return;
    }

    this.isAccordionOpen = true;
    this.temModelName = modelName;

    if (type === 'sph' || type === 'leftsph') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.lensData = this.glassesData.distMinusSphData;
        this.tableType = type;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      } else {
        this.minusDataValue = 'plus';
        this.lensData = this.glassesData.distPlusSphData;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
        this.tableType = type;
      }
    }
    if (type === 'cyl' || type === 'leftcyl') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.lensData = this.glassesData.distMinusCylData;
        this.tableType = type;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      } else {
        this.minusDataValue = 'plus';
        this.lensData = this.glassesData.distPlusCylData;
        this.tableType = type;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (type === 'axis' || type === 'leftaxis') {
      this.isPlusMinusValues = false;

      this.lensData = this.glassesData.distAxisData;
      this.tableType = type;
      this.lensDataArray = this.lensData[0].array;
      this.lensArray = this.lensDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'vision' || type === 'leftvision') {
      this.isPlusMinusValues = false;
      this.lensData = this.glassesData.distVaData;
      this.tableType = type;
      this.lensDataArray = this.lensData[0].array;
      this.lensArray = this.lensDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'addsph' || type === 'leftaddsph') {
      this.isPlusMinusValues = false;
      this.lensData = this.glassesData.addSphData;
      this.tableType = type;
      this.lensDataArray = this.lensData[0].array;
      this.lensArray = this.lensDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'nearsph' || type === 'leftnearsph') {
      this.isPlusMinusValues = false;
      this.lensData = this.glassesData.nearSphData;
      this.lensDataArray = this.lensData[0].array;
    }
    if (type === 'nearvision' || type === 'leftnearvision') {
      this.isPlusMinusValues = false;
      this.lensData = this.glassesData.nearVaData;
      this.lensDataArray = this.lensData[0].array;
      this.lensArray = this.lensDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (
      type === 'nearcyl' ||
      type === 'addcyl' ||
      type === 'addaxis' ||
      type === 'nearaxis' ||
      type === 'addvision' ||
      type === 'leftnearcyl' ||
      type === 'leftaddcyl' ||
      type === 'leftaddaxis' ||
      type === 'leftnearaxis' ||
      type === 'leftaddvision'
    ) {
      this.lensDataArray = [];
      this.isAccordionOpen = false;
    }
  }

  /**
   * @description This method is used to open  and close accordion
   */
  onClickDownButton() {
    if (this.disabled) { return; }
    this.isAccordionOpen = !this.isAccordionOpen;
    this.lensDataArray = this.lensData[0].array;
    this.lensArray = this.lensDataArray.reduce((acc, val) => {
      return [...acc, ...val];
    }, []);
  }
  onClickRightButton() {
    if (this.disabled) { return; }
    this.isAccordionOpen = !this.isAccordionOpen;
    this.onClickLensFields('distance_right_sph', 'sph');
  }
  /**
   * @description This method is used to plus table data values
   */
  onClickPlus(plusvalue) {
    if (this.disabled) { return; }
    this.minusDataValue = plusvalue;
    if (this.tableType === 'sph' || this.tableType === 'leftsph') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.distPlusSphData;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'cyl' || this.tableType === 'leftcyl') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.distPlusCylData;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to minus table data values
   */
  // function for  start from here
  onClickMinus(minusvalue) {
    if (this.disabled) { return; }
    // this.isSymbolMinus = true;
    this.minusDataValue = minusvalue;
    if (this.tableType === 'sph' || this.tableType === 'leftsph') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.distMinusSphData;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'cyl' || this.tableType === 'leftcyl') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.distMinusCylData;
        this.lensDataArray = this.lensData[0].array;
        this.lensArray = this.lensDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to get table data values
   */
  onClickLensData(value) {
    if (this.disabled) { return; }
    this[this.temModelName] = value;
    this.finalGlassesLensForm.patchValue({ [this.temModelName]: value });
    // this.finalGlassesrightLensForm.patchValue({ [this.temModelName]: value });
  }
  /**
   * @description This method is used to copy from left lens data to right
   */

  onClickCopyTableData() {

    if (this.disabled) {
      return;
    }

    const leftsph = this.finalGlassesLensForm.get('distance_right_sph').value;
    const leftcyl = this.finalGlassesLensForm.get('distance_right_cyl').value;
    const leftaxis = this.finalGlassesLensForm.get('distance_right_axis').value;
    const leftvision = this.finalGlassesLensForm.get('distance_right_vision')
      .value;
    const leftAddSph = this.finalGlassesLensForm.get('add_right_sph').value;
    const leftAddVision = this.finalGlassesLensForm.get('add_right_vision')
      .value;
    const leftNearSph = this.finalGlassesLensForm.get('near_right_sph').value;
    const leftNearCyl = this.finalGlassesLensForm.get('near_right_cyl').value;
    const leftNearaxis = this.finalGlassesLensForm.get('near_right_axis').value;
    const leftNearvision = this.finalGlassesLensForm.get('near_right_vision')
      .value;
    const leftTypeLens = this.finalGlassesLensForm.get('right_type_lens').value;
    const leftIpd = this.finalGlassesLensForm.get('ipd_right').value;
    const leftLensMaterial = this.finalGlassesLensForm.get(
      'right_lens_material'
    ).value;
    const leftLensTint = this.finalGlassesLensForm.get('right_lens_tint').value;
    const leftLensFrameMaterial = this.finalGlassesLensForm.get(
      'right_frame_material'
    ).value;
    const leftAdvice = this.finalGlassesLensForm.get('right_lens_advice').value;
    this.finalGlassesLensForm.get('distance_left_sph').patchValue(leftsph);
    this.finalGlassesLensForm.get('distance_left_cyl').patchValue(leftcyl);
    this.finalGlassesLensForm.get('distance_left_axis').patchValue(leftaxis);
    this.finalGlassesLensForm
      .get('distance_left_vision')
      .patchValue(leftvision);
    this.finalGlassesLensForm.get('add_left_sph').patchValue(leftAddSph);
    this.finalGlassesLensForm.get('add_left_vision').patchValue(leftAddVision);
    this.finalGlassesLensForm.get('near_left_sph').patchValue(leftNearSph);
    this.finalGlassesLensForm.get('near_left_cyl').patchValue(leftNearCyl);
    this.finalGlassesLensForm.get('near_left_axis').patchValue(leftNearaxis);
    this.finalGlassesLensForm
      .get('near_left_vision')
      .patchValue(leftNearvision);
    this.finalGlassesLensForm.get('left_type_lens').patchValue(leftTypeLens);
    this.finalGlassesLensForm.get('ipd_left').patchValue(leftIpd);
    this.finalGlassesLensForm
      .get('left_lens_material')
      .patchValue(leftLensMaterial);
    this.finalGlassesLensForm.get('left_lens_tint').patchValue(leftLensTint);
    this.finalGlassesLensForm
      .get('left_frame_material')
      .patchValue(leftLensFrameMaterial);
    this.finalGlassesLensForm.get('left_lens_advice').patchValue(leftAdvice);
  }
  /**
   * @description This method is used to refresh the data in the table.
   */
  refreshData() { }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.finalGlassesLensForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
}
