import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  ViewChild,
  ElementRef
} from '@angular/core';
import { Output, EventEmitter } from '@angular/core';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Observable, Subject, of, iif } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-contrast-sensitivity',
  templateUrl: './contrast-sensitivity.component.html',
  styleUrls: ['./contrast-sensitivity.component.scss']
})
export class ContrastSensitivityComponent
  implements OnInit, IDynamicComponent, OnDestroy {
  public currentrightValue;
  currentleftValue;
  public tempValue;
  tableType;
  currentFetchRightValue;
  currentFetchLeftValue;

  lensSensitivityForm: FormGroup;
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  componentFetchData$: Observable<any>;
  @Input()
  patientDemographicData: any;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  @Input()
  currentTabId: any;

  componentDestructionNotifier$ = new Subject();
  readonly CURRENT_USER = this.user.getUser();
  lensSensitivityData = [
    ['2.25', '2.10', '1.95', '1.80', '1.65', '1.50'],
    ['1.35', '1.20', '1.05', '0.90', ' 0.75', '0.60'],
    ['0.45', '0.30', '0.15', '0.00']
  ];
  patientParamData: any;
  componentData: any;
  message: string;

  constructor(
    private readonly fb: FormBuilder,
    private readonly dialog: MatDialog,
    private readonly user: UserService,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) {}

  //   onclickTableData( value, type) {
  //     this.tempValue = value;
  //     this.tableType = type;
  //     if (type === 'right') {
  // this.lensSensitivityForm.get('right_contrast_sensitivity').patchValue(value);
  // if (value) {
  // this.currentrightValue = value;

  // } else {
  // this.currentrightValue = this.lensSensitivityForm.get('right_contrast_sensitivity').value;
  // }
  gettingValuesFromApi() {}

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    this.initComponentDataFetchChangeHandler();
    if (this.parentFormGroup) {
      // console.log(this.parentFormGroup);
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.lensSensitivityForm
      );
    }
    this.gettingValuesFromApi();
    this.handleRefresh();
  }

  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {
    //  if (this.componentData) {
      if ( this.patientParamData.pastVisitDetails === 'true') {
        this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
      } else {
        this.message = 'Do you want to delete Contrast Sensitivity component data?';

    }
    // }
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // if (
    //   payload.data[this.componentId].emr_default_id &&
    //   payload.data[this.componentId].version_id
    // ) {
    //   return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // } else {
    //   this.lensSensitivityForm.reset();
    // }
  }

  /**
   * @description This method is used for deleting the component date when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .lensSensitivityForm.value;
        const { version_id } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDataDeleteConfirmationModal('')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeleteRequestObject(this .lensSensitivityForm.value))
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this .lensSensitivityForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this .lensSensitivityForm.reset();
            this.lensSensitivityForm.get('right_contrast_sensitivity').patchValue(null);
            this.lensSensitivityForm.get('left_contrast_sensitivity').patchValue(null);
            this.currentleftValue = null;
            this.currentrightValue = null;
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this .dataReset.emit(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  onclickTableData(value, type) {

    if (this.disabled) {
      return;
    }

    this.tempValue = value;
    this.tableType = type;
    if (type === 'right') {
      this.lensSensitivityForm
        .get('right_contrast_sensitivity')
        .patchValue(value);
      if (value) {
        this.currentrightValue = value;
      } else {
        this.currentrightValue = this.lensSensitivityForm.get(
          'right_contrast_sensitivity'
        ).value;
      }
    }
    if (type === 'left') {
      this.lensSensitivityForm
        .get('left_contrast_sensitivity')
        .patchValue(value);
      if (value) {
        this.currentleftValue = value;
      } else {
        this.currentleftValue = this.lensSensitivityForm.get(
          'left_contrast_sensitivity'
        ).value;
      }
    }
  }
  initForm() {
    this.lensSensitivityForm = this.fb.group({
      emr_default_id: null,
      version_id: null,
      right_contrast_sensitivity: null,
      left_contrast_sensitivity: null
    });
    if (this.disabled) {
      this.lensSensitivityForm.disable();
    } else {
      this.lensSensitivityForm.enable();
    }
  }

  copyValues() {

    if (this.disabled) {
      return;
    }

    this.lensSensitivityForm
      .get('left_contrast_sensitivity')
      .patchValue(this.currentrightValue);
    if (this.currentrightValue) {
      this.onclickTableData(this.tempValue, 'left');
    }
    this.lensSensitivityForm
      .get('right_contrast_sensitivity')
      .patchValue(this.currentleftValue);
    if (this.currentleftValue) {
      this.onclickTableData(this.tempValue, 'right');
    }
  }
  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.lensSensitivityForm.patchValue(componentData);
      this.componentData = componentData;
      this.currentrightValue = this.lensSensitivityForm.get(
        'right_contrast_sensitivity'
      ).value;
      this.currentleftValue = this.lensSensitivityForm.get(
        'left_contrast_sensitivity'
      ).value;
      if (this.currentrightValue) {
        this.onclickTableData(this.currentrightValue, 'right');
      }
      if (this.currentleftValue) {
        this.onclickTableData(this.currentleftValue, 'left');
      }
    }
  }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
