import { Component, OnInit, Input, ElementRef, ViewChild } from '@angular/core';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { MatSnackBar, MatDatepicker, MatDialogRef } from '@angular/material';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { getLandingPageCurrentSelectedAssistantDetails, IEmrOpthoState } from '@app/modules/emr-ophthalmology/reducers';
import { select, Store } from '@ngrx/store';
import * as fromLandingPagePatientVisitDetails from '../../../reducers/patient-visit-details.reducer';
import { takeUntil } from 'rxjs/operators';
import { DischargeDatePickerModelComponent } from '../discharge-Time-Date/discharge-date-picker-model.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-admit',
  templateUrl: './admit.component.html',
  styleUrls: ['./admit.component.scss']
})
export class AdmitComponent implements OnInit {
  @Input()
 public showdataFromEmr: string;
 @ViewChild('selectFloor') SelectFloor: ElementRef<HTMLInputElement>;
 @ViewChild('selectWard') SelectWard: ElementRef<HTMLInputElement>;
 @ViewChild('selectRoom') SelectRoom: ElementRef<HTMLInputElement>;
 @ViewChild('selectBed') SelectBed: ElementRef<HTMLInputElement>;
@ViewChild('srollableElementTemplateRef') Scrollref: ElementRef<HTMLInputElement>;
  showdata = false;
  AdmitForm: FormGroup;
  BuildingsData: any = [];
  patientsList: any = [];
  WardsList: any = [];
  floors: any = {} ;
  wards: any = [];
  Rooms: any = {};
  Beds: any = {};
  requestFor: string;
  requestType: string;
  typesOfRooms = ['icu', 'ot', 'ward'];
  bedId_Number: any;
  roomType: any;
  insertData: any = [];
  floorId: any;
  wardObj: any;
  isDisabledWard = false;
  currentPatientVisitDetails$: Observable<
  fromLandingPagePatientVisitDetails.State
> = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));
componentDestructionNotifier$ = new Subject();
  patientDemographicdata: any;
  admitDt = new Date();
  patientParamData: any;
  constructor(
    private userService: UserService,
    private emrOpthoService: EmrOpthoApiService,
    private snackBar: MatSnackBar,
    private fb: FormBuilder,
    private readonly store: Store<IEmrOpthoState>,
    private readonly route: ActivatedRoute
    // private readonly dialogRef: MatDialogRef<DischargeDatePickerModelComponent>,

  ) {
    this .userSession = this .userService.getUser();
   }
   private userSession: UserSession;
  showRooms() {
    this.showdata = true;
  }
  initForm() {
    this.AdmitForm = this.fb.group({
      requestedDate: new Date(),
      wardNm: '',
      wardName: null,
      floorNm: '',
      roomNumber: '',
      buildingName: '',
      roomType: '',
      // wardName: ''
      // wardNm: ''

    });
  }

  FetchBuildingsList() {
   const PayLoad = {
      authenticatedUserId: this .userSession.regId,
      orgId: this .userSession.orgId,
    };
    this.emrOpthoService.fetchBuildingsList(PayLoad).subscribe( response => {
      if (response.responseCode === 'E200') {
        this.BuildingsData = response.data;
        // this.floors = this.BuildingsData.floorsList;
        console.log('buidingdata', this.BuildingsData);
      } else if (response.responseCode === 'E400') {
        this .snackBar.open(response.responseMessage, 'undo', {duration: 1000});
      }
    },
    () => {
      this .snackBar.open('somthing went wrong', 'undo', {duration: 1000});
    });
  }

  /** Fetch org wardList function start here */
  selectwardType(wardType) {
    this.isDisabledWard = true;
    const PayLoad = {
       authenticatedUserId: this .userSession.regId,
       orgId: this .userSession.orgId,
       wardType: wardType
     };
     this.emrOpthoService.fetchWardsList(PayLoad).subscribe( response => {
       if (response.responseCode === 'E200') {
         this.WardsList = response.data;
         console.log('buidingdata', this.WardsList);
       } else if (response.responseCode === 'E400') {
         this .snackBar.open(response.responseMessage, 'undo', {duration: 1000});
       }
     },
     () => {
       this .snackBar.open('somthing went wrong', 'undo', {duration: 1000});
     });
   }

  /** Fetch ward list function end here */
   /** onChange method for selectingthe building name */
   onchangeBildingSelection(wardObj) {
    this.wardObj = this.AdmitForm.value;
    }
  InsertRequestForToBeAdmitAndShifting() {
    console.log();
    if (this.showdataFromEmr === 'admit') {
      this.requestFor = 'TOBEADMIT';
      this. requestType = 'TOBEADMIT';
    } else  if (this.showdataFromEmr === 'shift') {
      this.requestFor = 'TOBESHIFT';
      this. requestType = 'TOBESHIFT';
    }
    const   {requestedDate , ...remaining} = this .AdmitForm.value;
    this.admitDt = this.AdmitForm.get('requestedDate').value.getTime();
   const Payload = {
    authenticatedUserId: this.userSession.regId,
    orgId: this.userSession.orgId,
    orgGrpId: this.userSession.orgGroupId,
    hosPatientId: this.patientDemographicdata.hosPatientId,
    doctorId: this.patientParamData.doctorId,
    patientId: this.patientDemographicdata.patientId,
    patientVisitId: this.patientDemographicdata.patientVisitId,
    ...remaining,
    admitDt: this.admitDt,
    wardId: this.wardObj.wardName.wardId,
    wardName: this.wardObj.wardName.wardName,

    // buildingId: this.floors.buildingId,
    // floorId: this.floorId,
    // roomId: this.Beds.roomId,
    // roomType: this.roomType,
    // bedNumber: this.bedId_Number.bedNumber,
    // bedId: this.bedId_Number.bedId,
    requestFor: this.requestFor,
    requestType: this.requestType,
  };
  if (this.patientDemographicdata.bedId) {
  Payload['oldBedId'] = this.patientDemographicdata.bedId,
  Payload['currentBedId'] = this.patientDemographicdata.bedId;
   }
  // this.AdmitForm.reset();
  this.floors = {} ;
  this.wards = [];
  this.Rooms = {};
  this.Beds = {};
  this.emrOpthoService.insertRequestForToBeAdmitAndShifting(Payload).subscribe(response => {
    if (response.responseCode === 'E200') {
      this .snackBar.open(response.responseMessage, 'undo', {duration: 1000});
      this.insertData = response.data;
    } else if (response.responseCode === 'E400') {
      this .snackBar.open(response.responseMessage, 'undo', {duration: 1000});
    }
  },
  () => {
    this .snackBar.open('something went wrong', 'undo', {duration: 1000});
  }
  );
  }
  dataFromBuildings(buil, showRooms) {
    if (showRooms === 'hide') {
   this.SelectFloor.nativeElement.style.display = 'block';
    } else if (showRooms === '') {
      this.SelectFloor.nativeElement.style.display = 'none';
    }
    this.floors = buil;
    this.wards = [];
    this.Rooms = {};
    this.Beds = {};
  }
  dataFromFloors(rooms, roomtype) {
    this.floorId = rooms.floorId;
     if (roomtype === 'icu' || 'ot' || 'ward') {
  //  document.getElementById('selectFloor').style.display = 'block';
   this.SelectWard.nativeElement.style.display = 'block';
    } else if (roomtype === '') {
      this.SelectWard.nativeElement.style.display = 'none';
    }
    this.Rooms = {};
    this.Beds = {};
    this.roomType = roomtype;
  //  const roomtype =  this.AdmitForm.get('roomType').value;
    // this.dataFromIcuOtWard(type)
    // this.wards = rooms;
    // if ((rooms.icuList || rooms.otList || rooms.wardList).length >= 1 && roomtype) {
      if (roomtype === 'icu') {
        this.wards = rooms.icuList;
      } else if (roomtype === 'ot') {
        this.wards = rooms.otList;
      } else if (roomtype === 'ward') {
        this.wards = rooms.wardsList;
      }
  }
  dataFromWards(rooms, showRooms) {
    if (showRooms === 'hide') {
      this.SelectRoom.nativeElement.style.display = 'block';
       } else if (showRooms === '') {
         this.SelectRoom.nativeElement.style.display = 'none';
       }
    this.Beds = {};
    this.Rooms = rooms;
  }
  dataFromRooms(rooms, showRooms) {
    if (showRooms === 'hide') {
      this.SelectBed.nativeElement.style.display = 'block';
       } else if (showRooms === '') {
         this.SelectBed.nativeElement.style.display = 'none';
       }
    this.Beds = rooms;
  }
  dataFromBeds(beds) {
    this.bedId_Number = beds;
  }
  // dataFromIcuOtWard(rooms, type) {
  //   if ((rooms.icuList || rooms.otList || rooms.wardList).length >= 1) {
  //       if (type === 'icu') {
  //         this.wards = rooms.icuList;
  //       }
  //       if (type === 'ot') {
  //         this.wards = rooms.otList;
  //       }
  //       if (type === 'ward') {
  //         this.wards = rooms.wardList;
  //       }
  //   }
  // }
      DateAndTime(date) {
      console.log('admit date', date);
        }
      prev() {
        this.Scrollref.nativeElement.scrollTo({
        left: (this.Scrollref.nativeElement.scrollLeft + 150), behavior: 'smooth' });
      }
      next() {
        this.Scrollref.nativeElement.scrollTo({
        left: (this.Scrollref.nativeElement.scrollLeft - 150), behavior: 'smooth' });
      }
  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.FetchBuildingsList();
    this.initForm();
    this.getPatientVisitDetails();
    console.log('data from emr compo', this.showdataFromEmr);
  }


    // getting patient visit details from the store
    getPatientVisitDetails() {
      this.currentPatientVisitDetails$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(data => {
          if (data.patientVisitDetails) {
          this.patientDemographicdata = data.patientVisitDetails;
            console.log('patient demographic data', data.patientVisitDetails);
          }
        });
    }
}

