import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
@ Component({
  selector: 'app-oxygen-add-edit-dialog',
  templateUrl: './oxygen-add-edit-dialog.component.html',
  styleUrls: ['./oxygen-add-edit-dialog.component.scss']
})
export class OxygenAddEditComponent implements OnInit , OnDestroy {

  isEdit = false;
  Oxyzentabledata: FormGroup;
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();
  maxDate = new Date;
  formData: any;

  constructor(
    private readonly fb: FormBuilder,
    @ Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef< OxygenAddEditComponent>,
    private datePipe: DatePipe,
  ) {
    this .isEdit = injectedData.edit;
  }

  ngOnInit() {
    const { required, pattern } = Validators;
    this .Oxyzentabledata = this .fb.group({
      version_id: null,
      emr_default_id: null,
      oxygen_capture_dttm: [new Date(), required],
      oxygen_rate: [null, [required, pattern(/^\d+$/)]],
      oxygen_score: [null, [required, pattern(/^\d+$/)]]
    });
    this.Oxyzentabledata.get('oxygen_score').disable();

    this.Oxyzentabledata.get('oxygen_rate').valueChanges.subscribe(data => {
      if (data <= 91) {
        this.Oxyzentabledata.get('oxygen_score').patchValue('3');
        } else if (data === '92' || data === '93') {
        this.Oxyzentabledata.get('oxygen_score').patchValue('2');
        } else if (data === '94' || data === '95') {
        this.Oxyzentabledata.get('oxygen_score').patchValue('1');
        } else if (data >= 96) {
        this.Oxyzentabledata.get('oxygen_score').patchValue('0');
        }
    });

    this.Oxyzentabledata.get('oxygen_capture_dttm').valueChanges.subscribe( (val) => {
    console.log(val);
    });
    if (this .isEdit) {
      const injectedData = this .injectedData.rowData;
      injectedData.oxygen_capture_dttm = new Date(+injectedData.oxygen_capture_dttm);
      this .Oxyzentabledata.patchValue(this .injectedData.rowData);
    }
//     this.getTime()
//     .pipe(
//       takeUntil(this.componentDestructionNotifier$)
//     )
//    .subscribe(date => {
//      this.Oxyzentabledata.patchValue({
//       oxygen_capture_dttm : date
//      });
//  });
  }
  // getTime() {
  //   return timer(0, 1000).pipe(
  //     map(_ => new Date()),
  //     takeUntil(
  //       this.componentDestructionNotifier$
  //     )
  //   );
  // }
  add() {
    if (this .Oxyzentabledata.valid) {
      this.formData = this .Oxyzentabledata.value;
      this.formData['oxygen_score'] = this.Oxyzentabledata.get('oxygen_score').value;
      this.formData.oxygen_capture_dttm = this.formData.oxygen_capture_dttm.getTime();
      this.dialogRef.close(this .Oxyzentabledata.value);
    }
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
  close() {
    this .dialogRef.close();
  }
}
