import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-lens',
  templateUrl: './lens.component.html',
  styleUrls: ['./lens.component.scss']
})
export class LensComponent implements OnInit {

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  lensSectionForm: FormGroup;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.lensSectionForm = this.form;
    if (this.disabled) {
      this.lensSectionForm.disable();
    } else {
      this.lensSectionForm.enable();
    }
  }
}
