import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-intraocular',
  templateUrl: './intraocular.component.html',
  styleUrls: ['./intraocular.component.scss']
})
export class IntraocularComponent implements OnInit {

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  introSectionForm: FormGroup;

  ngOnInit() {
    this.initForm();
    if (this.disabled) {
      this.introSectionForm.disable();
    } else {
      this.introSectionForm.enable();
    }
  }

  initForm() {
    this.introSectionForm = this.form;
  }

}
