import {
  EmrOpthoActionTypes,
  EmrOpthoActions
} from '../actions/emr-ophthalmology-actions';
import { IFetchPatientsVisitDetailsResponse } from '../components/landing-page/models/fetch-patients-visit-details-response';

export interface State {
  isLoading: boolean;
  errorOccurred: boolean;
  patientVisitDetails: IFetchPatientsVisitDetailsResponse;
}

export const INITIAL_STATE: State = {
  patientVisitDetails: null,
  errorOccurred: false,
  isLoading: false
};

export function reducer(
  state: State = INITIAL_STATE,
  action: EmrOpthoActions
): State {
  switch (action.type) {
    case EmrOpthoActionTypes.FetchPatientVisitDetails:
      return {
        isLoading: true,
        errorOccurred: false,
        patientVisitDetails: null
      };
    case EmrOpthoActionTypes.FetchPatientVisitDetailsError:
      return {
        isLoading: false,
        errorOccurred: true,
        patientVisitDetails: null
      };
    case EmrOpthoActionTypes.FetchPatientVisitDetailsSuccess:
      return {
        isLoading: false,
        errorOccurred: false,
        patientVisitDetails: {...action.payload}
      };
    default:
      return state;
  }
}
