import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-extraocular',
  templateUrl: './extraocular.component.html',
  styleUrls: ['./extraocular.component.scss']
})
export class ExtraocularComponent implements OnInit {
  squnit;
  leftSqUnit;
  tropiaHozintal;
  sqRightUnit;
  rightSqUnit;
  righttropiaHozintal;

  // righttropiaHozintal;
  // sqRightUnit;
  // rightSqUnit;
  // leftSqUnit;
  // squnit;
  // tropiaHozintal;

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  extraocularSectionForm: FormGroup;

  ngOnInit() {
    this.initForm();

    if (this.disabled) {
      this.extraocularSectionForm.disable();
    } else {
      this.extraocularSectionForm.enable();
    }
  }

  initForm() {
    this.extraocularSectionForm = this.form;
  }

  get left_ems_squint() {
    return this.extraocularSectionForm.get('left_ems_squint');
  }

  get right_ems_squint() {
    return this.extraocularSectionForm.get('right_ems_squint');
  }

  get left_ems_squint_tropia() {
    return this.extraocularSectionForm.get('left_ems_squint_tropia');
  }

  get right_ems_squint_tropia() {
    return this.extraocularSectionForm.get('right_ems_squint_tropia');
  }

  get left_ems_squint_tropia_horizontal() {
    return this.extraocularSectionForm.get('left_ems_squint_tropia_horizontal');
  }

  get right_ems_squint_tropia_horizontal() {
    return this.extraocularSectionForm.get('right_ems_squint_tropia_horizontal');
  }

}
