import { InjectionToken } from '@angular/core';
import { COMPONENTS_ID } from '../emr-opthalmology-component-map';

const differenceCalculator = (obj, compareWithObj) => {
  const final_obj = <any>{};

  if (obj.global_comments !== compareWithObj.global_comments) {
    final_obj.version_id = obj.version_id;
    final_obj.global_comments = obj.global_comments;
    final_obj.emr_default_id = obj.emr_default_id;
  }

  const new_child_table = obj.child_table[0].tableData.reduce((acc, child_table_val) => {

    const index = compareWithObj.child_table[0].tableData
      .findIndex(compareWithObj_val => compareWithObj_val.emr_default_id === child_table_val.emr_default_id);

    if (index > -1) {
      if (JSON.stringify(child_table_val) !== JSON.stringify(compareWithObj.child_table[0].tableData[index])) {
        acc.push(child_table_val);
      }
    } else {
      acc.push(child_table_val);
    }

    return acc;
  }, []);

  if (new_child_table.length) {
    final_obj.child_table = [
      {
        childComponentId: obj.child_table[0].childComponentId,
        tableData: new_child_table
      }
    ];
  }

  return final_obj;
};

const differMap = {
  [COMPONENTS_ID.SYSTEMIC_HISTORY]: differenceCalculator,
  [COMPONENTS_ID.OPHTHALMIC_HISTORY]: differenceCalculator,
  [COMPONENTS_ID.COMPLAINTS]: differenceCalculator,
  [COMPONENTS_ID.PAST_MEDICAL_HISTORY]: differenceCalculator,
  [COMPONENTS_ID.FAMILY_HISTORY]: differenceCalculator
};

const factory = () => differMap;

export const COMPONENT_DATA_DIFF_TOKEN = new InjectionToken(
  'component data difference calculator',
  {
    providedIn: 'root',
    factory
  }
);
