    this.emrHeaderForm.get('dilationPeriod').valueChanges.subscribe(val => {
      if (val) {
        this.selectedDilationTime = this.emrHeaderForm.get(
          'dilationPeriod'
        ).value;
        if (this.dilationTimerValue === true) {
          this.insertOrUpdatePatientDilation();
        }
      }
    });
    this.emrFormGroup = this.fb.group({});
    this.emrHeaderForm.valueChanges.subscribe(data => {
    });
