import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
  Inject,
  OnDestroy
} from '@angular/core';
import { MOMENT, UserService, UtilService } from '@core/services';
import { takeWhile } from 'rxjs/operators';
import { timer, Subject } from 'rxjs';

@Component({
  selector: 'app-landing-page-timer',
  templateUrl: './landing-page-timer.component.html',
  styleUrls: ['./landing-page-timer.component.scss']
})
export class LandingPageTimerComponent implements OnInit, OnDestroy {
  @Input()
  patientData;

  dilationTimerValue: any;
  dilationTime = new Subject();

  stopTimer$ = new Subject();
  componentDestroyed = false;
  userSession: any;

  countDownTimer = {
    hours: 0,
    minutes: 0,
    seconds: 0
  };

  constructor(
    @Inject(MOMENT) private moment,
    private userService: UserService,
    private utilService: UtilService,
  ) {
    this.userSession = this.userService.getUser();
  }

  ngOnInit() {
    this.dilationTimer(this.patientData);
  }

  ngOnDestroy() {
    this.componentDestroyed = true;
  }

  getAvatorImg(item) {
    const image = item && item.gender === 'Female' ? 'female-ico.png' : 'male-ico.png';
    return `https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons/${image}`;
  }

  dilationTimer(item) {
    const duration = this.moment.duration(item.dilation, 'minutes');
    if (item.dilation > 0) {
      timer(0, 1000)
        .pipe(
          takeWhile(_ => duration.asSeconds() > 0 && !this.componentDestroyed))
        .subscribe(() => {
          duration.subtract(1, 'seconds');
          this.countDownTimer.hours = duration.hours();
          this.countDownTimer.minutes = duration.minutes();
          this.countDownTimer.seconds = duration.seconds();
        });
    }

  }
}
