import { Component, OnInit, Input, OnDestroy, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormArray } from '@angular/forms';
import { Observable, Subject, iif, of } from 'rxjs';
import { IDynamicComponent } from '../../models/dynamic-component';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog, MatSnackBar } from '@angular/material';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-vision',
  templateUrl: './vision.component.html',
  styleUrls: ['./vision.component.scss']
})
export class VisionComponent implements OnInit, OnDestroy, IDynamicComponent {

  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: number;

  @Input()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  componentFetchData$: Observable<any>;
  componentDestructionNotifier$ = new Subject();
  data: any = [];

  visionForm: FormGroup;
  showAddList = false;
  visionFormValue = [];
  formvalidator = {
  formValidations : [
    { type: 'required', message: 'Fields data cannot be empty' }
  ],
};
  validator = [null, [ Validators.pattern('^[-a-zA-Z0-9.+-/"]*$'),
  Validators.maxLength(500)]];
  ReDist = [
    '6/4',
    '6/4P',
    '6/5',
    '6/5P',
    '6/6',
    '6/6P',
    '6/9',
    '6/9P',
    '6/12',
    '6/12P',
    '6/18',
    '6/18P',
    '6/24',
    '6/24P',
    '6/36',
    '6/36P',
    '6/60',
    '6/60P',
    'CF1 mts',
    'CF2 mts',
    'CF3 mts',
    'CF30 cms',
    'CFCF',
    'HM',
    'PL+PR Accurate',
    'PL+PR Inaccurate',
    'NO PL',
    '(NI)No Improvement'

  ];
  ReNear = ['N5', 'N6', 'N8', 'N10', 'N12', 'N14', 'N18', 'N24', 'N36', 'N48', 'N60'];
  LeDist = [
    '6/4',
    '6/4P',
    '6/5',
    '6/5P',
    '6/6',
    '6/6P',
    '6/9',
    '6/9P',
    '6/12',
    '6/12P',
    '6/18',
    '6/18P',
    '6/24',
    '6/24P',
    '6/36',
    '6/36P',
    '6/60',
    '6/60P',
    'CF1 mts',
    'CF2 mts',
    'CF3 mts',
    'CF30 cms',
    'CFCF',
    'HM',
    'PL+PR Accurate',
    'PL+PR Inaccurate',
    'NO PL',
    '(NI)No Improvement'
  ];
  LeNear = ['N5', 'N6', 'N8', 'N10', 'N12', 'N14', 'N18', 'N24', 'N36', 'N48', 'N60'];
  isUpdating = false;
  updateIndex = -1;

  vision: any;
  AddTOList: string;

  addItemForm: FormGroup;

  visionFormArray: FormArray;
  userData: UserSession = this.userService.getUser();
  showError: boolean;
  componentData: any;
  patientParamData: any;
  message: string;

  constructor(
    private readonly emrOpthoService: EmrOpthoApiService,
    private readonly formBuilder: FormBuilder,
    private readonly snackbar: MatSnackBar,
    private readonly dialog: MatDialog,
    private readonly userService: UserService,
    private readonly route: ActivatedRoute
  ) { }


  initVisionTableItem() {
    return this.formBuilder.group({
      vision_type: 'Unaided',
      distance_right_eye: null,
      near_right_eye: null,
      distance_left_eye: null,
      near_left_eye: null,
      left_finger_count: 'No' ,
      right_finger_count:  'No',
      emr_default_id: null,
      version_id: null
    });
  }

  initForm() {
    this.addItemForm = this.initVisionTableItem();
    this.visionFormArray = this.formBuilder.array([]);
    this.visionForm = this.formBuilder.group({
      visionFormArray: this.visionFormArray
    });
    if (this.disabled) {
      // this.visionForm.get('vision_type')['checked'] = false;
      this.visionForm.disable();
    } else {
      this.visionForm.enable();
    }
  }
defaultFieldsSelection() {
  this.addItemForm = this.initVisionTableItem();
}

/**
 * @function isButtonClicked
 * @description this method is used to clear the previous selected values when user clicks on different buttons
 */
isButtonClicked() {
  this.addItemForm.get('distance_right_eye').patchValue('');
  this.addItemForm.get('near_right_eye').patchValue('');
  this.addItemForm.get('distance_left_eye').patchValue('');
  this.addItemForm.get('near_left_eye').patchValue('');
  this.addItemForm.get('left_finger_count').patchValue('No');
  this.addItemForm.get('right_finger_count').patchValue('No');
}

  get visionFormTableItems() {
    if (this.visionFormArray) {
      return this.visionFormArray.controls;
    } else {
      return [];
    }
  }

  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.componentData = componentData;
      if (Array.isArray(componentData)) {
        while (this.visionFormArray.length) {
          this.visionFormArray
            .removeAt(this.visionFormArray.length - 1);
        }
        componentData
          .forEach(item => { this.visionFormArray.push(this.formBuilder.group(item)); });
      }
    }
  }

  openDeleteConfirmationModal(message) {

      // if (this.componentData) {
        if ( this.patientParamData.pastVisitDetails) {
          this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
        } else {
          this.message = 'Do you want to delete Vision component data?';
        }
        // }

    return this.dialog.open(ConfirmationDialogComponent, {
      data: {
       title: 'Confirm',
       data: this.message
      }
    }).afterClosed();
  }


  makeComponentDeletePayload() {
    const rows = this.visionFormArray.value;
    const rowsToDelete = rows.filter(row => row.version_id)
      .map(({ version_id, emr_default_id }) => ({ version_id, emr_default_id  }));
    const { orgId, regId, orgGroupId } = this.userData;

    const request = {
      specialityId: this.specialityId,
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: regId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      // bedId: this.patientDemographicData.bedId,
      data: {
        [this.componentId]: rowsToDelete
      }
    };
    return request;
  }


  handleFormRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .visionForm.value;
        const { version_id} = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || tableData.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDeleteConfirmationModal('')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeComponentDeletePayload())
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.isUpdating = false;
            while (this.visionFormArray.length) {
              this.visionFormArray.removeAt(this.visionFormArray.length - 1);
            }
            this.dataReset.emit(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }





  addtolist() {
    this.AddTOList = 'Add To List';
    const vision_type = this.addItemForm.get('vision_type');
    const distance_right_eye = this.addItemForm.get('distance_right_eye');
    const distance_left_eye = this.addItemForm.get('distance_left_eye');
    const near_left_eye = this.addItemForm.get('near_left_eye');
    const left_finger_count = this.addItemForm.get('left_finger_count');
    const right_finger_count = this.addItemForm.get('right_finger_count');
    if (vision_type || distance_right_eye || distance_left_eye ||
      near_left_eye || left_finger_count || right_finger_count) {
        this.showError = false;
        const row = Object.assign({}, this.addItemForm.value);
        if (this.isUpdating && this.updateIndex > -1) {
          this.visionFormArray
            .at(this.updateIndex)
            .patchValue(this.addItemForm.value);
        } else {
          this.visionFormArray
            .push(this.formBuilder.group(row));
        }
        this.updateIndex = -1;
        this.isUpdating = false;
        this.showAddList = false;
        this.addItemForm.reset();
        // this.initVisionTableItem();
        this.defaultFieldsSelection();
      } else {
        this.showError = true;
      }

  }

  // addnew() {
  //   this.AddTOList = 'Add To List';
  //   this.showAddList = true;
  // }

  updateValues(index: number) {
    // debugger;
    this.AddTOList = 'Update List';
    this.updateIndex = index;
    const rowItem = this.visionFormArray.at(index);
    this.addItemForm.patchValue(rowItem.value);
    this.showAddList = true;
    this.isUpdating = true;
  }

  cancelForm() {
    this.showAddList = false;
    this.isUpdating = false;
    this.updateIndex = -1;
    this.addItemForm.reset();
  }

  deleteField(payload) {
    return this.emrOpthoService.deleteDataOfDynamicField(payload);
  }

  /**
   * @todo add popup before removing the components
   */
  deleteRow(index) {

    const row = this.visionFormArray.at(index).value;
    const { version_id, emr_default_id } = row;

    if (version_id) {
      this.openDeleteConfirmationModal('Do you want to delete this row')
      .pipe(
        filter(Boolean),
        concatMap(_ =>
          this.deleteField({
            emr_default_id,
            version_id,
            patientId: this.patientDemographicData.patientId,
            patientVisitId: this.patientDemographicData.patientVisitId,
            orgId: this.userData.orgId,
            componentId: this.componentId,
            authenticatedUserId: this.userData.regId
          }).pipe(catchError((errorResponse: HttpErrorResponse) => {
            return iif(
              () => errorResponse.status > 0,
              of(<ApiResponse<null>>errorResponse.error),
              of(new ApiResponse<null>(null, 'E400', []))
            );
          }))
        ),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.visionFormArray.removeAt(index);
          this.dataReset.emit(true);
        } else {
          this.snackbar.open('Unable to delete the row', 'Dismiss', { duration: 3000 });
        }
      }, _ => this.snackbar.open('Unable to delete the row', 'Dismiss', { duration: 3000 }));
    } else {
      this.visionFormArray.removeAt(index);
    }
  }

  ngOnInit() {
    this.initComponentDataFetchChangeHandler();
    this.handleFormRefresh();
    this.initForm();
    if (this.parentFormGroup) {
      this.parentFormGroup
        .addControl(`${this.componentId}`, this.visionFormArray);
    }
    this.AddTOList = 'Add To List';
    this.showAddList = true;
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
  }

  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
