import { Injectable } from '@angular/core';
import { Actions, ofType, Effect } from '@ngrx/effects';
import {
  EmrOpthoActionTypes,
  FetchPatientVisitDetails,
  FetchPatientVisitDetailsSuccess,
  FetchPatientVisitDetailsError
} from '@app/modules/emr-ophthalmology/actions/emr-ophthalmology-actions';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { of } from 'rxjs';
import { mergeMap, map, catchError, tap } from 'rxjs/operators';
import { IFetchPatientsVisitDetailsResponse } from '@app/modules/emr-ophthalmology/components/landing-page/models/fetch-patients-visit-details-response';

@ Injectable()
export class LandingPageEffects {

  @ Effect()
  fetchPatientVisitDetails$ = this .actions$.pipe(
    ofType< FetchPatientVisitDetails>(EmrOpthoActionTypes.FetchPatientVisitDetails),
    mergeMap(action =>
      this .emrOpthoApiService
        .fetchPatientVisitDetails< IFetchPatientsVisitDetailsResponse>(
          action.payload
        )
        .pipe(
          map(response => {
            if (response.responseCode === 'E200') {
              return new FetchPatientVisitDetailsSuccess(response.data);
            } else {
              return new FetchPatientVisitDetailsError();
            }
          }),
          catchError(_ => of(new FetchPatientVisitDetailsError()))
        )
    )
  );

  constructor(
    private actions$: Actions,
    private emrOpthoApiService: EmrOpthoApiService
  ) {}
}
