@Component({
  selector: 'app-operation',
  templateUrl: './operation.component.html',
  styleUrls: ['./operation.component.scss']
})
export class OperationComponent implements OnInit, AfterViewInit {
  @Input()
  disabled;
  @Input()
  operationDate;
  // @Input()
  // statisComponent$ = Observable as any;
  @Input()
  actionType;
  minDate = new Date();
  surgeryOperationForm: FormGroup;
  userData: UserSession = this.userService.getUser();
  dropDownData: IDATA;
  operationData: IOPERATIONDATA;
  drugDetails = [];
  panelData = [];
  assisantObj = {};
  surgeonObj = {};
  panelImgData = [];
  formData: any;
  preOperationData = true;
  assistantName: any;
  surgeonName: any;

  private userSession: UserSession;
  private readonly SURGERY_TAB = 10;
  @Output() navigateToDiagnosis = new EventEmitter<any>();
  @Output() navigateToErx = new EventEmitter<any>();
  @Output() navigateToCpoe = new EventEmitter<any>();
  currentPatientVisitDetails$: Observable<
    fromLandingPagePatientVisitDetails.State
  > = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));
  componentDestructionNotifier$ = new Subject();
  @ViewChild('displayDrugInfoDialog') displayDrugInfoDialog: TemplateRef<any>;

  @ViewChild('form') form: NgForm;

  // The content inside the noteData is used to instruct the user what is the functionality of a button in the panel.
  noteData = [
    {
      diagNote:
        '(Note: Clicking add diagnosis will redirect to diagnosis tab, Where you can create Diagnosis and Map it to Icd codes)'
    },
    {
      medicNote:
        '(Note: By clicking button it will redirect to erx tab, Where you can create Diagnosis and Map it to Icd Codes)'
    },
    {
      labNote:
        '(Note: By clicking botton it will redirect to CPOE tab, Where you can create diagnosis and Map it to ICD codes)'
    }
  ];
  dialogData: any;
  patientDemographicData: any;
  currentChildTab: string;
  insertOpData: {};
  fetchOpEmrId: any;
  scheduleDate: any;
  patientParamData: any;
  fetchEmrDefaultId: any;
  surgId: any;
  surgnList: any;
  updateSurgeonId: number;
  updateAsstSurgeonId: number;
  preOp = false;
  latestMedication = false;
  labTests = false;
  radiologyTests = false;
  postOp = false;

  public beforeChange($event: NgbPanelChangeEvent) {
    // if ($event.panelId === 'panel-id-one') {
    //   $event.nextState = false;
    // }
  }
  constructor(
    private readonly fb: FormBuilder,
    private readonly userService: UserService,
    private readonly emrService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly dialog: MatDialog,
    private readonly store: Store<IEmrOpthoState>,
    private readonly surgeryService: GlassDetailsService,
    private readonly route: ActivatedRoute
  ) {
    this.userSession = this.userService.getUser();
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.getPatientVisitDetails();
    this.fetchDoctors();
    this.initForm();
    this.fetchOperationSurgeryDetails();

    // this.isSaveButtonClicked();
  }
  ngAfterViewInit() {
      // this.surgeryOperationForm
      //   .get('operation_tm').
      //   valueChanges.subscribe(data => {
      //     this.surgeryOperationForm
      //     .get('operation_tm').patchValue(new Date(this.surgeryService.getOperationData()));
      //   });
  }

  routeTypeComparatorr(o1, o2) {
    if (o2) {
      return o1 && o2 && o1.surgeonNm === o2.surgeonNm;
    }
  }
  Comparatorr(o1, o2) {
    if (o2) {
      return o1 && o2 && o1.assistantSurgeonNm === o2.assistantSurgeonNm;
    }
  }
  comparator(o1, o2) {
    if (o2) {
      return o1 && o2 && o1.anesthetistNm === o2.anesthetistNm;
    }
  }

  viewDetails(type) {
    switch (type) {
      case 'preOp':
        this.preOp = !this.preOp;
        break;
        case 'postOp':
        this.postOp = !this.postOp;
        break;
      case 'latestMedication':
        this.latestMedication = !this.latestMedication;
        break;
      case 'labTests':
        this.labTests = !this.labTests;
        break;
      case 'radiologyTests':
        this.radiologyTests = !this.radiologyTests;
        break;
    }
  }

  /**
   * @description This function hold the formControl.
   * @function initForm
   */
  initForm() {
    this.surgeryOperationForm = this.fb.group({
      operationDttm: new Date(),
      operation_tm: new Date(),
      surgeonNm: null,
      procedureNm: null,
      anesticNm: null,
      isOpNotes: true,
      opNotes: null,
      anesthesiaType: null,
      asstName: null,
      npoRemarks: null,
      ivfRemarks: null,
      bloodLossMl: null,
      urineOutput: null,
      operativeFindings: null,
      radiclesRemarks: null,
      specimensRemarks: null,
      complicationsRemarks: null,
      drains: null,
      dispositionTransferRemarks: null,
      scheduleCheck: true,
      isSurgeonNmRequired: true,
      isAssistantSurgeonNmRequired: true,
      isProcedureNmRequired: true,
      isAnesthetistNmRequired: true,
      isAnesthesiaTypeRequired: true,
      isNpoRequired: true,
      isIvfRequired: true,
      isBloodLossRequired: true,
      isUrinOutputRequired: true,
      isFindingsRequired: true,
      isRadiclesRequired: true,
      isSpecimensRequired: true,
      isDrainsRequired: true,
      isComplicationsRequired: true,
      isDispositionRequired: true
    }
  );
    this.scheduleDate = this.surgeryService.getOperationData();
    this.surgeryOperationForm.get('operation_tm').patchValue(new Date(this.scheduleDate));
    // this.surgeryOperationForm.get('operation_tm').valueChanges.subscribe(data => {
    // this.surgeryService.setOperationData(data);
    // });

    // this.surgeryOperationForm.patchValue({
    //   surgeonNm: {surgeonNm: this.surgeonName },
    //   asstName: { asstName: this.assistantName}
    // });
  }

  // isSaveButtonClicked() {
  //   this.statisComponent$.subscribe(data => {
  //     if (
  //       data &&
  //       data.isSubmitted &&
  //       data.currentTabId === this.SURGERY_TAB &&
  //       data.selectedComponent === 2
  //     ) {
  //       this.saveSurgeryOperationData();
  //     }
  //   });
  // }
  save() {
    if (this.patientParamData.pastVisitDetails === 'true'  ) {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to save ?`
        },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result) {
           this.saveSurgeryOperationData();
        }
      });
    } else {
       this.saveSurgeryOperationData();
    }
  }
  /**
   * @description This function is called when the save button is clicked and data will be inserted.
   * @function saveSurgeryOperationData
   */
  saveSurgeryOperationData() {
    const {
      surgId,
      emplId,
      operationDt,
      ...remaining
    } = this.surgeryOperationForm.value;
    const surgeonId = this.surgeryOperationForm.get('surgeonNm').value
      .surgeonId ? this.surgeryOperationForm.get('surgeonNm').value
      .surgeonId : this.updateSurgeonId;
    const assistantSurgeonId = this.surgeryOperationForm.get('asstName').value
      .assistantSurgeonId ? this.surgeryOperationForm.get('asstName').value
      .assistantSurgeonId : this.updateAsstSurgeonId;
    const anesthetistId = this.surgeryOperationForm.get('anesticNm').value
      ? this.surgeryOperationForm.get('anesticNm').value.empId
      : null;
    const operationDttm = this.surgeryOperationForm
      .get('operationDttm')
      .value.getTime();
    // const anesthesiaType = this.surgeryOperationForm.get('anesthesiaType').value;
    const payload = {
      orgId: this.userData.orgId,
      authenticatedUserId: this.userData.regId,
      patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      emrDefaultId: this.insertOpData ? this.insertOpData : this.fetchEmrDefaultId,
      surgeonId,
      assistantSurgeonId,
      anesthetistId,
      ...remaining,
      operationDttm
      // anesthesiaType,
    };
    this.emrService.insertOrUpdateOperationData(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.snackbar.open('Data saved succesfully', 'undo', {
            duration: 3000
          });
           this.fetchOperationSurgeryDetails();
          this.insertOpData = response.data['emrDefaultId'];
        } else if (response.responseCode === 'E400') {
          this.snackbar.open(response.responseMessage, 'Dismiss', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackbar.open('something went wrong', 'Dismiss', {
          duration: 3000
        });
      }
    );
  }

  /**
   * @description This function fetch the data showed in the dropdown fields in operation tab
   * @function fetchDoctors
   */
  fetchDoctors() {
    const payload: IFETCHDOCTORS = {
      orgId: this.userData.orgId,
      authenticatedUserId: this.userData.regId
    };
    this.emrService.fetchDoctors<IDATA>(payload).subscribe(
      response => {
        if (response.responseCode === 'E200' && response.data) {
          this.dropDownData = response.data;
          this.surgnList = this.dropDownData.surgeonList;
          this.fetchOpEmrId = this.dropDownData['emrDefaultId'];
        } else if (response.responseCode === 'E400') {
          this.snackbar.open('error', response.responseMessage, {
            duration: 3000
          });
        }
      },
      () => {
        this.snackbar.open('something went wrong', 'Dismiss', {
          duration: 3000
        });
      }
    );
  }
  // getting patient visit details from the store
  getPatientVisitDetails() {
    this.currentPatientVisitDetails$
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(data => {
        if (data.patientVisitDetails) {
          this.patientDemographicData = data.patientVisitDetails;
        }
      });
  }
  /**
   * @description This function will fetch the data that will be showed in left side panels
   * @function fetchOperationSurgeryDetails
   */

  fetchOperationSurgeryDetails() {
    const payload: IFETCHOPERATIONSURGERYDETAILS = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId
    };
    this.emrService
      .fetchOperationSurgeryDetails<IOPERATIONDATA>(payload)
      .subscribe(
        response => {
          if (response.responseCode === 'E200') {
            this.surgeryOperationForm.get('operation_tm').patchValue(new Date(this.surgeryService.getOperationData()));
            this.operationData = response.data;
            this.surgeryOperationForm.patchValue(this.operationData);
            this.fetchEmrDefaultId = this.operationData['emrDefaultId'];
            this.updateSurgeonId = this.operationData['surgeonId'];
            this.updateAsstSurgeonId = this.operationData['assistantSurgeonId'];
            // this.anesthetistId = this.operationData[''];
            if (
              this.operationData.labList &&
              this.operationData.labList.length
            ) {
              this.displayLabData(this.operationData.labList[0]);
            }
            if (
              this.operationData.imagingList &&
              this.operationData.imagingList.length
            ) {
              this.displayImagingData(this.operationData.imagingList[0]);
            }
            if (
              this.operationData.drugList &&
              this.operationData.drugList.length
            ) {
              this.showData(this.operationData.drugList[0]);
            }
            this.surgeryOperationForm.patchValue({
              surgeonNm: { surgeonNm: this.operationData['surgeonNm'] },
              asstName: {
                assistantSurgeonNm: this.operationData['assistantSurgeonNm']
              },
              anesticNm: {
                anesthetistNm: this.operationData['anesthetistNm']
              }
            });
            this.assisantObj['surgeonNm'] = this.operationData['surgeonNm'];
            this.assisantObj['surgeonId'] = this.operationData['surgeonId'];
            this.assisantObj['anesthetistNm'] = this.operationData['anesthetistNm'];
            this.surgeonObj[
              'assistantSurgeonNm'
            ] = this.surgeryOperationForm.get('assistantSurgeonNm') && this.surgeryOperationForm.get('assistantSurgeonNm').value
              ? this.surgeryOperationForm.get('assistantSurgeonNm').value[
                  'surgeonNm'
                ]
              : null;
            this.surgeonObj['assistantSurgeonId'] = this.operationData[
              'assistantSurgeonId'
            ];
            this.surgeryService.setOperationStatus(
              this.operationData['surgeonNm']
            );
            this.surgeryService.setOperationAssistant(
              this.operationData['assistantSurgeonNm']
            );
            // this.mainArr = this.operationData.drugList[0];
            // this.panelData = this.operationData.labList[0];
          } else if (response.responseCode === 'E400') {
            this.snackbar.open('error', response.responseMessage, {
              duration: 3000
            });
          }
        },
        () => {
          this.snackbar.open('something went wrong', 'Dismiss', {
            duration: 3000
          });
        }
      );
  }
  changeSurgeon() {
    this.surgeryService.setOperationStatus(
      this.surgeryOperationForm.get('surgeonNm').value.surgeonNm
    );
  }
  changeAssistant() {
    this.surgeryService.setOperationAssistant(
      this.surgeryOperationForm.get('asstName').value.asstName
    );
  }

  visitLinkToOtherTab(type) {
    if (type === 'diagnosis') {
      this.navigateToDiagnosis.emit(null);
    }
    if (type === 'erx') {
      this.navigateToErx.emit(null);
    }
    if (type === 'cpoe') {
      this.navigateToCpoe.emit(null);
    }
  }
  /**
   * This is for navigation one tab to another
   */
  visitDiagnosis() {
    this.navigateToDiagnosis.emit(null);
  }
  visitErx() {
    this.navigateToErx.emit(null);
  }
  visitCpoe() {
    this.navigateToCpoe.emit(null);
  }

  /**
   * @description This method is used to display daywise date when the date button is clicked.
   * @function showData
   */
  showData(obj) {
    this.drugDetails = obj;
  }
  /**
   * @description This method is used to display the data when the date button is clicked
   * @function displayDrugInfo
   */
  displayDrugInfo(obj1) {
    this.dialogData = obj1;
    this.dialog.open(this.displayDrugInfoDialog);
  }
  /**
   * @description This method is used to close the modal dialog
   * @function close
   */
  close() {
    this.dialog.closeAll();
  }
  /**
   * @description TThis method is used to display drug info when the date button is clicked
   * @function displayLabData
   */
  displayLabData(obj) {
    this.panelData = obj;
  }
  /**
   * @description This method is used to display drug info when the date button is clicked
   * @function displayImagingData
   */
  displayImagingData(objImg) {
    this.panelImgData = objImg;
  }
}
