import {
  Component,
  OnInit,
  OnDestroy,
  TemplateRef,
  ViewContainerRef,
  Type,
  Inject,
  ElementRef,
  ViewChild,
  AfterViewInit,
  ComponentRef,
  ComponentFactoryResolver,
  Injector,
  Input,
  Output,
  EventEmitter,
  SimpleChanges,
  OnChanges
} from '@angular/core';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import {
  takeUntil,
  tap,
  filter,
  exhaustMap,
  switchMap,
  first
} from 'rxjs/operators';
import { Subject, Observable, fromEvent } from 'rxjs';
import * as fromTabsListDataReducer from '../../reducers/tabs-list.reducer';

import { Store, select } from '@ngrx/store';
import { IEmrOpthoState, getTabsData } from '../../reducers';
import { DynamicTabSettingsService } from './dynamic-tab-settings.service';
import { FormControl, FormBuilder, AbstractControl } from '@angular/forms';
import {
  IFetchComponentObject,
  IFetchComponentsResponse
} from './models/fetch-components-response';
import {
  OverlayRef,
  ConnectedPosition,
  Overlay,
  ScrollStrategyOptions
} from '@angular/cdk/overlay';
import { TemplatePortal } from '@angular/cdk/portal';
import { COMPONENTS_MAP_TOKEN } from '../../emr-opthalmology-component-map';
import { IFetchDynamicTabsResponse } from '../tabs-list/models/fetch-dynamic-tabs-response';
import { IShiftComponentRequest } from './models/shift-component-request';
import { ToastrService } from 'ngx-toastr';
import { IFetchDynamicTabsRequest } from '../tabs-list/models/fetch-dynamic-tabs-request';
import {
  FetchTabList,
  OpenComponentCustomization
} from '../../actions/emr-ophthalmology-actions';
import { IFetchComponentsRequest } from './models/fetch-components-request';
import { IDynamicComponent } from '../../models/dynamic-component';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';

@Component({
  selector: 'app-dynamic-tab-settings',
  templateUrl: './dynamic-tab-settings.component.html',
  styleUrls: ['./dynamic-tab-settings.component.scss']
})
export class DynamicTabSettingsComponent
  implements OnInit, OnDestroy, OnChanges, AfterViewInit {
  @ViewChild('drownDownTogglerBtnReference')
  drownDownTogglerBtnReference: ElementRef;

  @ViewChild('dropdownTemplate')
  dropdownTemplateRef: TemplateRef<DynamicTabSettingsComponent>;

  @ViewChild('componentViewport', { read: ViewContainerRef })
  componentViewportRef: ViewContainerRef;

  @ViewChild('saveBtnRef')
  saveBtnRef: ElementRef;

  @ViewChild('cancelBtnRef')
  cancelBtnRef: ElementRef;

  @ViewChild('visibilityTogglerBtnRef')
  visibilityTogglerBtn: ElementRef;

  componentDestructionNotifier$ = new Subject();

  userData: UserSession = this.userService.getUser();

  tabsListData$: Observable<fromTabsListDataReducer.State> = this.store.pipe(
    select(getTabsData)
  );

  componentsList: IFetchComponentsResponse;
  componentSelectorControl: FormControl;
  typeSelectorControl: FormControl;
  shiftBtnDropdownTogglerActive = false;
  dropDownOverlayRef: OverlayRef;
  currentSelectedComponent: Type<IDynamicComponent>;
  shiftTabSelector: AbstractControl; // will list the tab where the current component has to be shifted
  dynamicTabs: Array<IFetchDynamicTabsResponse> = [];

  @Input()
  specialityId: number;

  @Input()
  currentTabId: number;
  isCustamizationData;

  @Output()
  navigateToParent: EventEmitter<boolean> = new EventEmitter();
  customizeComponentOpenCondition: IFetchComponentObject;

  constructor(
    @Inject(COMPONENTS_MAP_TOKEN)
    private readonly componentsMap: Map<number, Type<IDynamicComponent>>,
    private readonly store: Store<IEmrOpthoState>,
    private readonly userService: UserService,
    private readonly overlay: Overlay,
    private readonly dialog: MatDialog,
    private readonly toastr: ToastrService,
    private readonly fb: FormBuilder,
    private readonly dynamicTabSettingsService: DynamicTabSettingsService,
    private readonly scrollStragegyOptions: ScrollStrategyOptions,
    private readonly hostViewContainerRef: ViewContainerRef,
    private readonly componentFactoryResolver: ComponentFactoryResolver,
    private readonly injector: Injector,
    private readonly snackBar: MatSnackBar
  ) {}

  attachDropDownTogglerClickHandler() {
    fromEvent(this.drownDownTogglerBtnReference.nativeElement, 'click')
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(_ => this.toggleTabSelectorDropdown());
  }

  toggleTabSelectorDropdown() {
    this.shiftBtnDropdownTogglerActive = !this.shiftBtnDropdownTogglerActive;
    if (this.shiftBtnDropdownTogglerActive) {
      this.makeDropDown();
    } else {
      this.deattachDropdown();
    }
  }

  componentListTrackingFn(_, item: IFetchComponentObject) {
    return item.componentId;
  }

  customizeComponentOpenConditionMethod(component) {
    this.isCustamizationData = component.isCustamization;
  }

  customizeComponent() {
    const component: IFetchComponentObject = this.componentSelectorControl
      .value;
    this.store.dispatch(new OpenComponentCustomization(component.componentId));
  }

  createDropDownPortal() {
    const position: ConnectedPosition = {
      originX: 'start',
      originY: 'bottom',
      overlayX: 'start',
      overlayY: 'top'
    };
    const positionStrategy = this.overlay
      .position()
      .flexibleConnectedTo(this.drownDownTogglerBtnReference)
      .setOrigin(this.drownDownTogglerBtnReference)
      .withDefaultOffsetX(0)
      .withDefaultOffsetY(10)
      .withPositions([position]);

    this.dropDownOverlayRef = this.overlay.create({
      disposeOnNavigation: true,
      positionStrategy,
      hasBackdrop: true,
      backdropClass: 'mat-overlay-transparent-backdrop',
      scrollStrategy: this.scrollStragegyOptions.close()
    });

    this.dropDownOverlayRef
      .backdropClick()
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(() => {
        this.toggleTabSelectorDropdown();
      });
  }

  createConfirmDialog(message: string) {
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: message
        }
      })
      .afterClosed();
  }

  toggleComponentApplicability(isApplicable: boolean = false) {
    const component: IFetchComponentObject = this.componentSelectorControl
      .value;
    const payload: IShiftComponentRequest = {
      authenticatedUserId: `${this.userData.regId}`,
      componentId: component.componentId,
      docComponentId: `${component && component.docComponentId}`,
      isApplicable,
      isShift: 'false',
      orgGrpId: `${this.userData.orgGroupId}`,
      orgId: `${this.userData.orgId}`,
      specialityId: `${this.specialityId}`,
      tabId: `${this.currentTabId}`
    };
    return this.dynamicTabSettingsService.shiftComponent(payload);
  }

  deattachDropdown() {
    this.dropDownOverlayRef.detach();
  }

  ngOnInit() {
    this.initControls();
    this.initComponentSelectorValueChangeListener();

    this.createDropDownPortal();

    this.initSaveBtnClickHandler();
    this.initCancelBtnClickHandler();
    this.initVisibilityTogglerBtnClickHandler();

    this.initTabsListValueChangeHandler();
  }

  initCancelBtnClickHandler() {
    fromEvent(this.cancelBtnRef.nativeElement, 'click')
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(() => {
        this.navigateToParent.emit(true);
      });
  }

  initVisibilityTogglerBtnClickHandler() {
    fromEvent(this.visibilityTogglerBtn.nativeElement, 'click')
      .pipe(
        exhaustMap(_ =>
          this.createConfirmDialog(
            'Are you sure you want to disable this component?'
          )
        ),
        filter(result => result),
        switchMap(_ => {
          const currentSelectedComponent = this.componentSelectorControl
            .value as IFetchComponentObject;
          return this.toggleComponentApplicability(
            !currentSelectedComponent.isApplicable
          );
        }),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          const currentSelectedComponent = this.componentSelectorControl
            .value as IFetchComponentObject;
          if (currentSelectedComponent) {
            const currentUpdatedComponent = this.componentsList.find(
              component =>
                component.componentId === currentSelectedComponent.componentId
            );
            if (currentUpdatedComponent) {
              currentUpdatedComponent.isApplicable = !currentUpdatedComponent.isApplicable;
            }
          }
        } else {
          this.toastr.error('Unable to disable the component', 'Error!');
        }
      });
  }

  initSaveBtnClickHandler() {
    fromEvent(this.saveBtnRef.nativeElement, 'click')
      .pipe(
        takeUntil(this.componentDestructionNotifier$),
        tap(_ => {
          if (!this.shiftTabSelector.value) {
            this.snackBar.open('Please select a tab', 'Dismiss', {
              duration: 3000
            });
          }
        }),
        filter(_ => this.shiftTabSelector.value),
        exhaustMap(_ =>
          this.createConfirmDialog(
            `Do you want to move this component to the ${
// tslint:disable-next-line: no-bitwise
              this.shiftTabSelector.value.tabNm.toUpperCase()
            } tab`
          )
        ),
        filter(result => result),
        switchMap(_ => this.shiftComponent(this.shiftTabSelector.value.tabId))
      )
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.snackBar.open(`Component was successfully moved to ${
                this.shiftTabSelector.value.tabNm
              } tab`, 'undo', {
            duration: 3000
          });
          // this.toastr.success(
          //   `Component was successfully moved to ${
          //     this.shiftTabSelector.value.tabNm
          //   } tab`
          // );
          const index = this.componentsList.findIndex(
            component =>
              component.componentId ===
              this.componentSelectorControl.value.componentId
          );
          if (index > -1) {
            this.componentsList.splice(index, 1);
            this.componentSelectorControl.setValue(this.componentsList[0]);
          }
        } else {
          this.toastr.error(
            `Unable to shift the component to ${
              this.shiftTabSelector.value.tabNm.toUpperCase()
            } tab`
          );
        }
      });
  }

  shiftComponent(tabId: number) {
    const component: IFetchComponentObject = this.componentSelectorControl
      .value;
    const payload: IShiftComponentRequest = {
      authenticatedUserId: `${this.userData.regId}`,
      componentId: component.componentId,
      docComponentId: `${component && component.docComponentId}`,
      isApplicable: true,
      isShift: 'true',
      orgGrpId: `${this.userData.orgGroupId}`,
      orgId: `${this.userData.orgId}`,
      specialityId: `${this.specialityId}`,
      tabId: `${tabId}`
    };
    return this.dynamicTabSettingsService.shiftComponent(payload);
  }

  initComponentSelectorValueChangeListener() {
    this.componentSelectorControl.valueChanges
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe((component: IFetchComponentObject) => {
        if (component) {
          this.makeAndInitComponent(component.componentId);
          this.shiftTabSelector.setValue(null);
          if (this.shiftBtnDropdownTogglerActive) {
            this.toggleTabSelectorDropdown();
          }
        }
      });
  }

  makeAndInitComponent(componentId: number) {
    this.currentSelectedComponent = this.componentsMap.get(componentId);
    const componentRef = this.makeComponent<IDynamicComponent>(
      this.currentSelectedComponent
    );
    if (componentRef) {
      this.initDynamicComponent(componentRef, componentId, this.specialityId, true);
    }
  }

  initDynamicComponent(
    componentRef: ComponentRef<IDynamicComponent>,
    componentId: number,
    specialityId: number,
    disabled: boolean = false
  ) {
    if (componentRef) {
      componentRef.instance.componentId = componentId;
      componentRef.instance.specialityId = specialityId;
      componentRef.instance.disabled = disabled;
    }
  }

  initControls() {
    this.typeSelectorControl = this.fb.control('');
    this.componentSelectorControl = this.fb.control(null);
    this.shiftTabSelector = this.fb.control(null);

    this.shiftTabSelector.valueChanges
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(_ => {
        if (this.shiftBtnDropdownTogglerActive) {
          this.toggleTabSelectorDropdown();
        }
      });
  }

  initTabsListValueChangeHandler() {
    this.tabsListData$
      .pipe(
        first(),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(data => {
        if (data.tabsList) {
          this.dynamicTabs = data.tabsList.filter(tab => tab.isDynamic);
        }
      });
  }

  fetchListOfComponents(tabId) {
    const errorToaster = () =>
      this.toastr.error('Unable to fetch the components list', 'Error!');
    const payload: IFetchComponentsRequest = {
      orgId: `${this.userData.orgId}`,
      orgGrpId: `${this.userData.orgGroupId}`,
      authenticatedUserId: `${this.userData.regId}`,
      tabId,
      specialityId: this.specialityId
    };
    return this.dynamicTabSettingsService
      .fetchListOfComponents(payload)
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.isCustamizationData = '';
          this.componentsList = response.data;
          console.log(this.componentsList);
          this.isCustamizationData = '';
          this.isCustamizationData = this.componentsList[0].isCustamization;
          console.log(this.isCustamizationData + 'data');
        } else {
          this.componentsList = [];
          errorToaster();
        }
        this.componentSelectorControl.setValue(
          this.componentsList.length ? this.componentsList[0] : null
        );
      }, errorToaster);
  }

  listOfTabsData() {
    const payload: IFetchDynamicTabsRequest = {
      orgId: `${this.userData.orgId}`,
      authenticatedUserId: `${this.userData.regId}`,
      specialityId: `${this.specialityId}`
    };
    this.store.dispatch(new FetchTabList(payload));
  }

  makeDropDown() {
    const dropdownPortal = new TemplatePortal(
      this.dropdownTemplateRef,
      this.hostViewContainerRef
    );
    this.dropDownOverlayRef.attach(dropdownPortal);
  }

  makeComponent<T>(component: Type<T>): ComponentRef<T> {
    if (component) {
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(
        component
      );
      const componentRef = componentFactory.create(this.injector);
      const view = componentRef.hostView;
      this.componentViewportRef.clear();
      this.componentViewportRef.insert(view);
      return componentRef;
    } else {
      this.componentViewportRef.clear();
    }
  }

  tabListItemTrackingfn(_, item: IFetchDynamicTabsResponse) {
    return item.tabId;
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.currentTabId) {
      this.fetchListOfComponents(this.currentTabId);
    }
  }

  ngAfterViewInit() {
    this.attachDropDownTogglerClickHandler();
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next(null);
    this.componentDestructionNotifier$.complete();
  }
}
