import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';

@Component({
  selector: 'app-fundus',
  templateUrl: './fundus.component.html',
  styleUrls: ['./fundus.component.scss']
})
export class FundusComponent implements OnInit {
  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  fundusSectionForm: FormGroup;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.fundusSectionForm = this.form;
    if (this.disabled) {
      this.fundusSectionForm.disable();
    } else {
      this.fundusSectionForm.enable();
    }
  }

}
