import { Component, OnInit, Input, OnDestroy, Output, EventEmitter } from '@angular/core';
import {
  FormGroup,
  FormBuilder,
  Validators,
  FormControlName,
  FormArray
} from '@angular/forms';
import { DatePipe } from '@angular/common';
import { Observable, Subject, merge, iif, of } from 'rxjs';
import { keyValuesToMap } from '@angular/flex-layout/extended/typings/style/style-transforms';
import { from } from 'rxjs';
import { interval, timer } from 'rxjs';
import { takeUntil, map, filter, concatMap, catchError } from 'rxjs/operators';
import { IDynamicComponent } from '../../models/dynamic-component';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-iop',
  templateUrl: './iop.component.html',
  styleUrls: ['./iop.component.scss']
})
export class IopComponent implements OnInit, IDynamicComponent, OnDestroy {
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  currentTabId: number;

  @Input()
  patientDemographicData: any;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  componentFetchData$: Observable<any>;

  // for timer in Form
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();

  iopForm: FormGroup;
  showAddList = false;
  iopFormValue = [];
  isUpdating = false;
  updateIndex = -1;

  iopFormArray: FormArray;

  formvalidation = {
    formValidations: [
      { type: 'maxlength', message: 'Fields data cannot be more than 50 digits' },
      { type: 'pattern', message: 'Enter valid data' },
      { type: 'required', message: '* field is required' }
    ],
    SubmitValidations: [
      { type: 'required', message: 'Fields data cannot be empty' },
    ]
  };
  Numbervalidator = [null, [Validators.pattern('^[0-9]*$'), Validators.maxLength(5)]];
  Textvalidator = [null, [Validators.pattern('^[a-zA-Z]*$'), Validators.maxLength(50)]];
  AddToList = '';

  addItemForm: FormGroup;

  userData: UserSession = this.userService.getUser();
  showError: boolean;
  patientParamData: any;
  componentData: any;
  message: string;

  constructor(
    private iopformBuilder: FormBuilder,
    private readonly emrOpthoService: EmrOpthoApiService,
    private dialog: MatDialog,
    private datePipe: DatePipe,
    private snackbar: MatSnackBar,
    private userService: UserService,
    private route: ActivatedRoute
  ) {}

  //  timer function
  getTime() {
    return timer(0, 1000).pipe(
      map(_ => this.datePipe.transform(new Date(), 'dd/MM/yyyy, HH:mm')),
      takeUntil(
        merge(this.componentDestructionNotifier$, this.endTimer, this.endTimer)
      )
    );
  }

  openDeleteConfirmationModal(message) {
    if ( this.patientParamData.pastVisitDetails) {
      this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
    } else {
      this.message = 'Do you want delete Iop component data?';
    }
    return this.dialog.open(ConfirmationDialogComponent, {
      data: {
       title: 'Confirm',
       data: this.message
      }
    }).afterClosed();
  }
  initTableItemFormGroup() {
    return this.iopformBuilder.group({
      iop_tm: [{ value: null, disabled: true }],
      iop_method: this.Textvalidator,
      iop_right: this.Numbervalidator,
      iop_left: this.Numbervalidator,
      emr_default_id: null,
      version_id: null
    });
  }
  initForm() {
    this.addItemForm = this.initTableItemFormGroup();
    this.iopFormArray = this.iopformBuilder.array([]);
    this.iopForm = this.iopformBuilder.group({
      iopFormArray: this.iopFormArray
    });
  }

  get iopFormArrayControls() {
    if (this.iopFormArray) {
      return this.iopFormArray.controls;
    } else {
      return [];
    }
  }

  addtolist(undefined) {
    this.AddToList = 'Add To List';
    const iop_method = this.addItemForm.get('iop_method');
    const iop_right = this.addItemForm.get('iop_right');
    const iop_left = this.addItemForm.get('iop_left');
      if (iop_method.value || iop_right.value || iop_left.value) {
        this.showError = false;
          if (this.isUpdating && this.updateIndex > -1) {
            this.iopFormArray
              .at(this.updateIndex)
              .patchValue(this.addItemForm.value);
          } else {
            this.iopFormArray.push(this.iopformBuilder.group(this.addItemForm.getRawValue()));
          }
          this.updateIndex = -1;
          this.isUpdating = false;
          this.showAddList = false;
          this.addItemForm.reset ();
      } else {
        this.showError = true;
      }
     }

  // addnew() {
  //   this.addItemForm.reset();
  //   this.AddToList = 'Add To List';
  //   this.showAddList = true;
  //   this.getTime().subscribe(date => {
  //     const control = this.addItemForm.get('iop_tm');
  //     if (control) {
  //       control.patchValue(date);
  //     }
  //   });
  // }

  deleteField(payload) {
    return this.emrOpthoService.deleteDataOfDynamicField(payload);
  }

  deleteRow(i) {
    const row = this.iopFormArray.at(i).value;
    const { emr_default_id, version_id } = row;
    if (version_id) {
      this.openDeleteConfirmationModal('')
      .pipe(
        filter(Boolean),
        concatMap(_ =>
          this.deleteField({
            emr_default_id,
            version_id,
            // patientId: this.patientDemographicData.patientId,
            // patientVisitId: this.patientDemographicData.patientVisitId,
            orgId: this.userData.orgId,
            componentId: this.componentId,
            authenticatedUserId: this.userData.regId
          }).pipe(catchError((errorResponse: HttpErrorResponse) => {
            return iif(
              () => errorResponse.status > 0,
              of(<ApiResponse<null>>errorResponse.error),
              of(new ApiResponse<null>(null, 'E400', []))
            );
          }))
        ),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          this.iopFormArray.removeAt(i);
          this.dataReset.emit(true);
        } else {
          this.snackbar.open('Unable to delete the row', 'Dismiss', { duration: 3000 });
        }
      }, _ => this.snackbar.open('Unable to delete the row', 'Dismiss', { duration: 3000 }));
    } else {
      this.iopFormArray.removeAt(i);
    }
  }

  updateValues(index) {
    this.addItemForm.reset();
    this.AddToList = 'Update List';
    this.updateIndex = index;
    const row = <FormGroup>this.iopFormArray.at(index);
    this.addItemForm.patchValue(row.getRawValue());
    this.showAddList = true;
    this.isUpdating = true;
  }

  cancelForm() {
    this.showAddList = false;
    this.isUpdating = false;
    this.updateIndex = -1;
    this.addItemForm.reset();
    this.endTimer.next();
  }

  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  makeComponentDeletePayload() {
    const rows = this.iopFormArray.value;
    const rowsToDelete = rows.filter(row => row.version_id)
      .map(({ version_id, emr_default_id }) => ({ version_id, emr_default_id  }));
    const { orgId, regId, orgGroupId } = this.userData;

    const request = {
      specialityId: this.specialityId,
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      // bedId: this.patientDemographicData.bedId,
      data: {
        [this.componentId]: rowsToDelete
      }
    };
    return request;
  }

  handleFormRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .iopForm.value;
        const { version_id} = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || tableData.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDeleteConfirmationModal('Do you want to delete iop component\'s data')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeComponentDeletePayload())
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.iopForm.reset();
            this.iopFormArray.controls = [];
            this .dataReset.emit(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.componentData = componentData;
      if (Array.isArray(componentData)) {
        while (this.iopFormArray.length) {
          this.iopFormArray.removeAt(this.iopFormArray.length - 1);
        }
        componentData.forEach(item => {
          this.iopFormArray.push(this.iopformBuilder.group(item));
        });
      }
    }
  }

  ngOnInit() {
    // this.addnew();
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    this.handleFormRefresh();
    this.initComponentDataFetchChangeHandler();
    if (this.parentFormGroup) {
      this.parentFormGroup.addControl(`${this.componentId}`, this.iopFormArray);
    }
    this.addItemForm.reset();
    this.AddToList = 'Add To List';
    this.showAddList = true;
    this.getTime().subscribe(date => {
      const control = this.addItemForm.get('iop_tm');
      if (control) {
        control.patchValue(date);
      }
    });
  }

  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
