import { Component, OnInit, EventEmitter, Output, ViewChild, TemplateRef, Input } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { UserService } from '@core/services';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserSession } from '@core/models/user-session';
import { MatSnackBar, MatDialog } from '@angular/material';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, Subject } from 'rxjs';
import { Store, select } from '@ngrx/store';
import { IEmrOpthoState, getLandingPageCurrentSelectedAssistantDetails } from '@app/modules/emr-ophthalmology/reducers';
import * as fromLandingPagePatientVisitDetails from '../../../../reducers/patient-visit-details.reducer';
import { takeUntil } from 'rxjs/operators';
import { subject } from 'aws-sdk/clients/sns';
import { Transform } from 'stream';
import { DatePipe } from '@angular/common';
import { GlassDetailsService } from '@app/modules/emr-ophthalmology/services/glass-details.service';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';

@Component({
  selector: 'app-pre-operation',
  templateUrl: './pre-operation.component.html',
  styleUrls: ['./pre-operation.component.scss']
})
export class PreOperationComponent implements OnInit {
  preOperationForm: FormGroup;
  private readonly SURGERY_TAB = 10;
  // @Input()
  // statisComponent$ = Observable as any;
  @Input()
  disabled: boolean;
  @Output() navigateToDiagnosis = new EventEmitter<any>();
  @Output() navigateToErx = new EventEmitter<any>();
  @Output() navigateToCpoe = new EventEmitter<any>();

  @ViewChild('displayDrugInfoDialog') displayDrugInfoDialog: TemplateRef<any>;
  // public preop = true;
  // public latemed = false;
  // public latestlab = false;
  preOperationDataResponseData: any = [];
  patientDemographicData: any;
  minDate = new Date();
  preOperationDetails: any = [];
  drugListsData: any = [];
  labListData: any = [];
  imaginingData: any = [];
  preOp = false;
  latestMedication = false;
  labTests = false;
  radiologyTests = false;
  currentPatientVisitDetails$: Observable<
    fromLandingPagePatientVisitDetails.State
    > = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));
  componentDestructionNotifier$ = new Subject();
  staticComponent$ = new Subject();
  dialogData: any;
  private preOperationData = true;
  activeIds: string[];
  noteData = [
    {
      diagNote:
        ' Clicking add diagnosis will redirect to diagnosis tab, Where you can create Diagnosis and Map it to Icd codes'
    },
    {
      medicNote:
        ' By clicking button it will redirect to erx tab, Where you can create Diagnosis and Map it to Icd Codes'
    },
    {
      labNote:
        ' By clicking botton it will redirect  to CPOE tab, Where you can create diagnosis and Map it to ICD codes'
    }
  ];
  formDate: string;
  preOpVersionId: any;
  patientParamData: any;
  constructor(
    private preOperationFormBuilder: FormBuilder,
    private EmrOphthalmologyApiService: EmrOpthoApiService,
    private userService: UserService,
    private snackBar: MatSnackBar,
    private routerService: Router,
    private readonly dialog: MatDialog,
    private readonly surgeryService: GlassDetailsService,
    private readonly route: ActivatedRoute,
    // private transform: Transform,
    private datePipe: DatePipe,
    private readonly store: Store<IEmrOpthoState>
  ) {
    this.userSession = this.userService.getUser();
  }
  private userSession: UserSession;

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.getPatientVisitDetails();
    this.initForm();
    this.fetchPreOpertionDetails();
    this.activeIds = ['panel-1', 'panel-2'];
    // this.isSaveButtonClicked();
    // this.accordian();
  }
  /**
* @description This method is used to hide and show the accordain
*/
  viewDetails(type) {
    switch (type) {
      case 'preOp':
        this.preOp = !this.preOp;
        break;
      case 'latestMedication':
        this.latestMedication = !this.latestMedication;
        break;
      case 'labTests':
        this.labTests = !this.labTests;
        break;
      case 'radiologyTests':
        this.radiologyTests = !this.radiologyTests;
        break;
    }
  }


  /**
* @description This method is used to get the patientData from the store
*/
  getPatientVisitDetails() {
    this.currentPatientVisitDetails$
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(data => {
        if (data.patientVisitDetails) {
          this.patientDemographicData = data.patientVisitDetails;
        }
      });
  }
  initForm() {
    this.preOperationForm = this.preOperationFormBuilder.group({
      planOperationDttm: new Date(),
      preOperationDttm: [new Date()],
      isPreIndicationsRequired: true,
      preOperationIndications: null,
      planProcedureNm: null,
      isPlanProcedureNmRequired: true,
      isBloodGroupRequired: true,
      isBloodArranged: true,
      noBloodUnits: null,
      isNoBloodUnitsRequired: true,
      preNpoRemarks: null,
      isPreNpoRequired: true,
      preIvfRemarks: null,
      isPreIvfRequired: true,
      isConsentTaken: true,
      isConsentTakenRequired: true,
      consentRelation: null,
      consentBy: null,
      isConsentByRequired: true,
      consentRemarks: null,
      isConsentTypeRequired: true,
      isConsentRemarksRequired: true,
      isConsentRelationRequired: true,
      consentType: null,
      antibioticsOrdered: null,
      isAntibioticsOrderedRequired: true,
      isBloodArrangedRequired: true,
      isPlanOperationDttmRequired: true,
      bloodGrpNm: '',
      instructions: null,
      internalComment: null,
      noteToPharmacy: null,
      versionId: null,
      emrDefaultId: null
    });
     this.preOperationForm.valueChanges.subscribe((value) => {
    this.surgeryService.setOperationData(new Date(value.planOperationDttm).getTime());
  });

  }

pastVisitDetails() {
  if (this.patientParamData.pastVisitDetails === 'true' ) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: {
        title: 'Confirm',
        data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to save ?`
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.insertOrUpdatePreOperationData();
      }
    });
  } else {
    this.insertOrUpdatePreOperationData();
  }
}

  /**
  * @description This method is used to insert or update the insertOrUpdatePreOperationData
  */
  insertOrUpdatePreOperationData() {
    const { planOperationDtt, preOperationDtt, ...remaining } = this.preOperationForm.value;
    const planOperationDttm = this.preOperationForm.get('planOperationDttm').value.getTime();
    const preOperationDttm = this.preOperationForm.get('preOperationDttm').value.getTime();

    const payload = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      ...remaining,
      planOperationDttm,
      preOperationDttm,
      versionId: this.preOperationDataResponseData.versionId ? this.preOperationDataResponseData.versionId : this.preOpVersionId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId,
      emrDefaultId: this.preOperationDataResponseData.emrDefaultId,
    };
    this.EmrOphthalmologyApiService.insertOrUpdatePreOperationData(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.snackBar.open('Data saved succesfully', 'undo', {
            duration: 3000
          });
          this.fetchPreOpertionDetails();
          this.preOperationDataResponseData = response.data;
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something Went Wrong', 'undo', { duration: 3000 });
      }
    );
  }
  // changeDate() {
  //   this.surgeryService.setOperationData(this.preOperationForm.get('preOperationDttm').value.getTime());
  // }

  /**
* @description This method is used to fetch the PreOpertionDetails
*/
  fetchPreOpertionDetails() {
    const payload = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId
    };
    this.EmrOphthalmologyApiService.fetchPreOpertionDetails(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.preOperationDataResponseData = response.data;
          this.preOpVersionId = this.preOperationDataResponseData.versionId;
          this.preOperationForm.patchValue(response.data);

          const data = this.preOperationDataResponseData.preOperationDttm;
          const data2 = new Date(data);
          const preOperationData = this.preOperationDataResponseData.planOperationDttm ;
          const preOpData = new Date(preOperationData);
          this.formDate = this.datePipe.transform(new Date(), 'dd/MM/yyyy');
          // console.log(this.formDate);
          if (this.preOperationDataResponseData.planOperationDttm) {
            this.preOperationForm.get('planOperationDttm').patchValue(preOpData);
          } else {
            this.preOperationForm.get('planOperationDttm').patchValue( new Date());

          }
          if (this.preOperationDataResponseData.preOperationDttm) {
            this.preOperationForm.get('preOperationDttm').patchValue( data2 );
          } else {
            this.preOperationForm.get('preOperationDttm').patchValue( new Date());
          }
            this.surgeryService.setOperationData(this.preOperationDataResponseData.planOperationDttm);

          this.preOperationDetails = response.data;
          if (this.preOperationDetails.labList && this.preOperationDetails.labList.length) {
            this.selectedLabDate(this.preOperationDetails.labList[0]);
          }
          if (this.preOperationDetails.imagingList && this.preOperationDetails.imagingList.length) {
            this.selectedimagingDate(this.preOperationDetails.imagingList[0]);
          }
          if (this.preOperationDetails.drugList && this.preOperationDetails.drugList.length) {
            this.selectedDate(this.preOperationDetails.drugList[0]);
          }
          // console.log(this.preOperationDetails);
          if (this.preOperationDetails.drugList) {
            this.drugListsData = this.preOperationDetails.drugList[0];

          }

        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', { duration: 1000 });
        }
      },
      () => {
        this.snackBar.open('something Went Wrong', 'undo', { duration: 1000 });
      }
    );
  }

  /**
* @description This method is used toget the selected date from the drugList
*/
  selectedDate(date) {
    this.drugListsData = date;
  }

  /**
 * @description This method is used toget the selected date from the labList
 */
  selectedLabDate(date) {
    this.labListData = date;
    // console.log(this.labListData);
  }
  /**
  * @description This method is used toget the selected date from the imaging
  */
  selectedimagingDate(date) {
    this.imaginingData = date;
    // console.log(this.labListData);
  }

  /**
  * @description This method is used for the dialog for a particular selected data
  */
  displayDrugInfrmation(obj1) {
    this.dialogData = obj1;
    this.dialog.open(this.displayDrugInfoDialog);
  }
  close() {
    this.dialog.closeAll();
  }

  visitLinkToOtherTab(type) {
    if (type === 'diagnosis') {

      this.navigateToDiagnosis.emit(null);
      // console.log('data');
    }
    if (type === 'erx') {
      this.navigateToErx.emit(null);
    }
    if (type === 'cpoe') {
      this.navigateToCpoe.emit(null);

    }
  }


}
