import { InjectionToken } from '@angular/core';
import { StaticTabSettingsComponent } from './components/static-tab-settings/static-tab-settings.component';
import { CpoeHeaderComponent } from './components/cpoe/components/cpoe-header/cpoe-header.component';

export const TAB_ID = {
  EMR_OPTHO: 1,
  ERX: 6,
  EXAMINATION: 3,
  CPOE: 4,
  FINAL_DIAGNOSIS: 5,
  HISTORY_TAB: 9,
  CPOE_TAB : 4,
  SURGERY_TAB: 10,
  SUMMARY_TAB: 7
};
export const tabIdNameMapFactory = () => TAB_ID;
export const TAB_ID_NAME_TOKEN = new InjectionToken('tab name id map', {
  factory: tabIdNameMapFactory,
  providedIn: 'root'
});

export const COMPONENTS_MAP = new Map()
  .set(TAB_ID.EMR_OPTHO, StaticTabSettingsComponent)
  .set(TAB_ID.ERX, StaticTabSettingsComponent)
  .set(TAB_ID.CPOE, CpoeHeaderComponent)
  .set(TAB_ID.SUMMARY_TAB, StaticTabSettingsComponent)
  .set(TAB_ID.FINAL_DIAGNOSIS, StaticTabSettingsComponent)
  .set(TAB_ID.SURGERY_TAB, StaticTabSettingsComponent);

export const componentsMapFactory = () => COMPONENTS_MAP;
export const COMPONENTS_MAP_TOKEN = new InjectionToken('components maps', {
  factory: componentsMapFactory,
  providedIn: 'root'
});
