import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserService } from '@core/services/user.service';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { Subscription, Observable, Subject } from 'rxjs';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { ToastrService } from 'ngx-toastr';
import { DatePipe } from '@angular/common';
import { UtilService } from '@core/services';
import { LabServicesSaveRequest } from '@app/modules/emr-ophthalmology/components/cpoe/components/models/lab-services-save-request';
import { LabServicesStatusRequest } from '@app/modules/emr-ophthalmology/components/cpoe/components/models/lab-services-status-request';
import { UserSession } from '@core/models/user-session';
import { FormGroup, FormBuilder } from '@angular/forms';
import {
  getLandingPageCurrentSelectedAssistantDetails,
  IEmrOpthoState,
  getTabsData
} from '@app/modules/emr-ophthalmology/reducers';
import { select, Store } from '@ngrx/store';
import * as fromLandingPagePatientVisitDetails from '../../../../reducers/patient-visit-details.reducer';
import { takeUntil, share } from 'rxjs/operators';
import * as fromTabsListDataReducer from '../../../../reducers/tabs-list.reducer';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog, MatSnackBar } from '@angular/material';

@Component({
  selector: 'app-radiology',
  templateUrl: './radiology.component.html',
  styleUrls: ['./radiology.component.scss']
})
@AutoUnsubscribe()
export class RadiologyComponent implements OnInit, OnDestroy {
  @Input()
  statisComponent$ = Observable as any;
  @Input()
  typeOfAction ;
  public labTestsAndPanels;
  eventTargetData: boolean | null;
  public radiobuttons = [];
  private type: string;
  public userdata: UserSession;
  private testList = [];
  private panelList = [];
  public servicesList = [];
  public servicesListforSearch = [];
  payload = {};
  public serviceType = 'testType';
  public selectedServiceListImaging = [];
  selectedDateimaging: any  = [];
  selectedDate: any = [];
  selectedDefaultDate;
  paramsSubscription: Subscription;
  public searchText = '';
  public formGroup: FormGroup;
  private currentTabId: number;
  private patientDemographicData: any;
  private isDuplicate: boolean;
  public selectedIndex;
  labPanelsAndTestsData;
  dialogRef;
  currentPatientVisitDetails$: Observable<
    fromLandingPagePatientVisitDetails.State
    > = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));
  componentDestructionNotifier$ = new Subject();
  tabsListData$: Observable<fromTabsListDataReducer.State> = this.store.pipe(
    select(getTabsData)
  );
  paramMap$ = this.activatedRoute.paramMap.pipe(share());
  patientParamData: Params;
  constructor(
    private activatedRoute: ActivatedRoute,
    private emrApiService: EmrOpthoApiService,
    private toastr: ToastrService,
    private userService: UserService,
    private datePipe: DatePipe,
    private utilService: UtilService,
    private readonly store: Store<IEmrOpthoState>,
    private formBuilder: FormBuilder,
    public dialog: MatDialog,
    private snackBar: MatSnackBar,
    private route: ActivatedRoute
  ) {
    this.userdata = this.userService.getUser();
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.getPatientVisitDetails();
    this.fetchTestStatus();
    this.initForm();
    this.activatedRoute.queryParams.subscribe((params: Params) => {
      this.type = params['type'];
    });
    this.radiobuttons = [
      'View All',
      // 'Biochemistry',
      // 'Clinical Pathology',
      // 'Hematology',
      // 'Microbiology',
      // 'Serology'
    ];
    this.fetchLabAndRadiologyServices(null);
  }

  saveRadiologyData() {
    if (this.patientParamData.pastVisitDetails === 'true') {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to save ?`
        },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result) {
          this.insertTestsAndPanels();
        }
      });
    } else {
      this.insertTestsAndPanels();
    }
  }
  addToList(obj) {
    if (obj.panelId) {
      this.fetchLabPanelsAndTests(obj);

    }

    if (this.eventTargetData === true || this.eventTargetData === false) {
      if (this.eventTargetData === true) {
        obj.isRequired = true;
        console.log(obj);
      } else if (this.eventTargetData === false) {
        obj.isRequired = false;
        console.log(obj);
      }
    } else {
      obj.isNew = true;
      obj.isRequired = true;
      obj.orderDates = this.datePipe.transform(new Date(), 'yyyy-MM-dd');
      if (this.serviceType === 'testType') {
        obj.type = 'Test';
      } else {
        obj.type = 'Panel';
      }
      if (this.selectedServiceListImaging && this.selectedServiceListImaging.length) {
      //  this.isDuplicate = this.selectedServiceListImaging.every(function (val) {
      //     return val.panelId === obj.panelId;
      // });
        if (this.selectedServiceListImaging.includes(obj)) {
          // if (this.isDuplicate === true) {
          this.snackBar.open(` ${obj.type} already selected `, 'undo', {
            duration: 3000
          });
          return;
        } else {
          this.selectedServiceListImaging.push(obj);
        }
      } else {
        this.selectedServiceListImaging.push(obj);
      }
    }
  }
  deleteService(service, i) {
    if (this.patientParamData.pastVisitDetails === 'true' && service.analysisRequestTestId) {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`
        },
        disableClose: true
      });
    } else {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: 'Do you want delete test/panel ?'
        },
        disableClose: true
      });
    }

    this.dialogRef.afterClosed().subscribe(result => {
      if (result) {
        // if (service.isNew) {
        //   this.selectedServiceListImaging = this.selectedServiceListImaging.filter(data => {
        //     return (
        //       data.testId !== service.testId || data.panelId !== service.panelId
        //     );
        //   });
        // } else {
          if (!service.testResult) {
          if ((service.testId && service.analysisRequestTestId) || (service.panelId && service.analysisRequestTestId)) {
            const payload = {
            orgId: this.userdata.orgId,
            hospitalPatientId: this.patientDemographicData.hosPatientId,
            patientVisitId: this.patientDemographicData.patientVisitId,
            authenticatedUserId: this.userdata.regId,
            panelId: service.panelId,
            testId: service.testId,
            doctorId: this.patientParamData.doctorId,
            testType: 'IMAGING',
          };
          this.emrApiService
            .deleteDoctorAnalysisRequestTestsAndPanels(payload)
            .subscribe(
              (response: any) => {
                if (response.responseCode === 'E200') {
                  this.fetchTestStatus();
                  this.selectedServiceListImaging = this.selectedServiceListImaging.filter(
                    data => {
                      return (
                        data.testId !== service.testId ||
                        data.panelId !== service.panelId
                      );
                    }
                  );
                  this.toastr.success(response.responseMessage);
                } else if (response.responseCode === 'E400') {
                  this.toastr.error(response.responseMessage);
                }
              },
              () => {
                this.toastr.error('Failure', 'Something went wrong');
              }
            );
        } else {
          this.selectedServiceListImaging.splice(i, 1);
        }
      } else {
        this.snackBar.open('Result Already Generated So, Test/Panel Not Deleted ', 'undo', {
          duration: 3000
        });
      }
      }
    });
  }

  summaryRecord(selectedService, event) {
    console.log(selectedService);
    if (selectedService.analysisRequestTestId) {
      if (event.target.checked) {
        // const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        //   data: { title: 'Confirm',
        //    data: 'Are You Sure Want To Map this record ?' },
        //   disableClose: true
        // });
        // dialogRef.afterClosed().subscribe(result => {
          // if (result) {
            const payload = {
              orgId: this.userdata.orgId,
              hospitalPatientId: this.patientDemographicData.hosPatientId,
              patientVisitId: this.patientDemographicData.patientVisitId,
              authenticatedUserId: this.userdata.regId,
              panelId: selectedService.panelId,
              testId: selectedService.testId,
              isRequired: 'true',
              doctorId: this.patientParamData.doctorId,
              testType: 'IMAGING',
            };
            this.emrApiService
              .deleteDoctorAnalysisRequestTestsAndPanels(payload)
              .subscribe(
                (response: any) => {
                  if (response.responseCode === 'E200') {
                  } else if (response.responseCode === 'E400') {
                    this.snackBar.open(response.responseMessage, 'undo', {
                      duration: 3000
                    });
                  }
                },
                () => {
                  this.snackBar.open('something went wrong', 'undo', {
                    duration: 3000
                  });
                }
              );
          // }
        // });
      } else if (!event.target.checked) {
        // const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        //   data: {
        //     title: 'Confirm',
        //     data: 'Are You Sure Want To unMap this record from summary ?'
        //   },
        //   disableClose: true
        // });
        // dialogRef.afterClosed().subscribe(result => {
          // if (result) {
            const payload = {
              orgId: this.userdata.orgId,
              hospitalPatientId: this.patientDemographicData.hosPatientId,
              patientVisitId: this.patientDemographicData.patientVisitId,
              authenticatedUserId: this.userdata.regId,
              panelId: selectedService.panelId,
              testId: selectedService.testId,
              isRequired: 'false',
              doctorId: this.patientParamData.doctorId,
              testType: 'IMAGING',
            };
            this.emrApiService
              .deleteDoctorAnalysisRequestTestsAndPanels(payload)
              .subscribe(
                (response: any) => {
                  if (response.responseCode === 'E200') {
                  } else if (response.responseCode === 'E400') {
                    this.snackBar.open(response.responseMessage, 'undo', {
                      duration: 3000
                    });
                  }
                },
                () => {
                  this.snackBar.open('something went wrong', 'undo', {
                    duration: 3000
                  });
                }
              );
          // }
        // });
      }
    } else {
      console.log('data');
      if (event.target.checked) {
        this.eventTargetData = true;
        this.addToList(selectedService);
      } else if (!event.target.checked) {
        this.eventTargetData = false;
        this.addToList(selectedService);
      }
    }
  }





  initForm() {
    this.formGroup = this.formBuilder.group({
      isRequired: true,
      serviceType: 'testType',
      searchText: null
    });
  }
  fetchLabAndRadiologyServices(data) {
    const payload = {
      orgId: this.userdata.orgId,
      authenticatedUserId: this.userdata.regId,
      type: 'IMAGING',
      investigationDepartment: null
    };
    if (data && data !== 'View All') {
      payload.investigationDepartment = data;
    }
    this.emrApiService.fetchLaboratoryAndRadilogyTestsPanels(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.labTestsAndPanels = response.data;
          console.log(this.labTestsAndPanels);
          this.onSelectingTestsOrPanels(this.serviceType);
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure', 'Something went wrong');
      }
    );
  }

  fetchTestStatus() {
    const payload: LabServicesStatusRequest = {
      orgId: this.userdata.orgId,
      orgGrpId: this.userdata.orgGroupId,
      patientVisitId: this.patientParamData.patientVisitId,
      authenticatedUserId: this.userdata.regId,
      testType: 'IMAGING',
      patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId
    };
    this.emrApiService.fetchTestStatus(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.selectedDateimaging = response.data.imagingList;
          if (response.data.imagingList && response.data.imagingList.length) {
            this.selectedDate = [this.selectedDateimaging[0]];
             this.selectedServiceListImaging = this.selectedDate;
             this.selectedDefaultDate = this.selectedDate;
            } else {
              this.selectedServiceListImaging = [];
              this.selectedDefaultDate = [];
              this.selectedDate = [];
            }
          // this.selectedDate = [this.selectedDateimaging[0]];
          // this.selectedServiceListImaging = this.selectedDate;
        } else if (response.responseCode === 'E400') {
          this.toastr.error(response.responseMessage);
        }
      },
      () => {
        this.toastr.error('Failure', 'something went wrong');
      }
    );
  }

  selectedDateImaging(item) {
    this.selectedServiceListImaging = [item];
    console.log(item);
  }
  // getting patient visit details from the store
  getPatientVisitDetails() {
    this.currentPatientVisitDetails$
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(data => {
        if (data.patientVisitDetails) {
          this.patientDemographicData = data.patientVisitDetails;
          this.patientDemographicData.age = this.utilService
            .parseDob(this.patientDemographicData.age)
            .get('patientAge');
        }
      });
  }
  insertTestsAndPanels() {
    console.log(this.selectedServiceListImaging);
    // this.selectedServiceListImaging.shift();
    // GroupBy on type
    const groupedData = this.utilService.groupBy(
      this.selectedServiceListImaging,
      'type'
    );
    if (groupedData) {
      this.testList = groupedData['Test'] || [];
      this.panelList = groupedData['Panel'] || [];
    }
    const payload: LabServicesSaveRequest = {
      orgId: this.userdata.orgId,
      orgGrpId: this.userdata.orgGroupId,
      doctorId: this.patientParamData.doctorId,
      hospitalPatientId: this.patientDemographicData.hosPatientId,
      patientId: this.patientDemographicData.patientId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      authenticatedUserId: this.userdata.regId,
      testList: this.testList,
      panelList: this.panelList,
      testType: 'IMAGING',
      tabId: 4,
      tabNm: 'CPOE',

    };
    this.emrApiService.insertTestsAndPanels(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
          this.snackBar.open('Data saved succesfully', 'undo', {
            duration: 3000
          });
          this.fetchTestStatus();
          // this.toastr.success(response.responseMessage);
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'undo', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Dismiss', {duration: 3000});
      }
    );
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
  onSelectingTestsOrPanels(data) {
    this.servicesList = [];
    this.serviceType = data;
    if (this.serviceType === 'testType') {
      this.servicesList = this.labTestsAndPanels.testList;
      this.servicesListforSearch = this.servicesList;
      this.servicesList.map(service => {
        service.isexpand = false;
        service.isPanel = false;
      });
    } else {
      this.servicesList = this.labTestsAndPanels.panelList;
      if (this.servicesList) {
        // this .servicesList = this .servicesList.concat(
        //   this .servicesList,
        //   this .servicesList,
        //   this .servicesList
        // );
        this.servicesList.map(service => {
          service.isexpand = false;
          service.isPanel = true;
        });
        this.servicesListforSearch = this.servicesList;
      }
    }
  }
  searchServices() {
    this.searchText = this.formGroup.get('searchText').value;
    console.log('searchText', this.searchText);
    if (this.searchText) {
      this.servicesList = this.servicesList.filter(selectedService => {
        return (selectedService.loincNm
          ? selectedService.loincNm
          : selectedService.panelName
        )
          .toLowerCase()
          .includes(this.searchText.toLowerCase());
      });
    } else {
      this.servicesList = this.servicesListforSearch;
    }
  }
  showOrHide(service, index) {
    const isExpand = service.isexpand;
    this.servicesList[index].isexpand = isExpand;
    service.isexpand = !service.isexpand;
    console.log(service.isExpand);
    this.selectedIndex = index;
    console.log('data');
    if (service.isexpand) {
      this.fetchLabPanelsAndTests(service);
    }
  }
  fetchLabPanelsAndTests(service) {
    const payload = {
      orgId: this.userdata.orgId,
      orgGrpId: this.userdata.orgGroupId,
      key: 'IMAGING',
      authenticatedUserId: this.userdata.regId,
      panelId: service.panelId,
      panelNm: service.panelNm
    };
    this.emrApiService.fetchLabPanelsAndTests(payload).subscribe((response) => {
      if (response.responseCode === 'E200') {
        this.labPanelsAndTestsData = response.data;
        service.testList = this.labPanelsAndTestsData.testList1;
      } else if (response.responseCode === 'E400') {
        this.snackBar.open(response.responseMessage, 'Undo', {duration: 3000});
      }
    },
    () => {
      this.snackBar.open('Something went wrong', 'undo', {duration: 3000});
    }
    );
  }
}
