import {
  Component,
  OnInit,
  Input,
  Injector,
  ViewContainerRef,
  Inject,
  Type,
  Renderer2,
  ViewChild,
  OnChanges,
  SimpleChanges,
  ContentChild,
  ElementRef,
  AfterContentInit,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { IComponentList } from '../emr/models/fetch-emr-tabs-response';
import { ComponentFactoryResolver } from '@angular/core';
import { COMPONENTS_MAP_TOKEN } from '../../emr-opthalmology-component-map';
import { IDynamicComponent } from '../../models/dynamic-component';
import { FormGroup } from '@angular/forms';
import { fromEvent, Subject, Observable } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-emr-optho-dynamic-component-container',
  templateUrl: './dynamic-component-container.html',
  styleUrls: ['./dynamic-content-container.scss']
})
export class EmrOpthoDynamicComponentContainerComponent implements OnInit, OnChanges, OnDestroy, AfterContentInit {

  @ContentChild('refreshHandler')
  refreshBtnHandler: ElementRef<HTMLElement>;

  @ViewChild('viewContainer', { read: ViewContainerRef })
  viewContainer: ViewContainerRef;

  isComponentClassAvailable: boolean;

  @Input()
  componentDef: IComponentList;

  @Input()
  parentFormGroup: FormGroup;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  componentFetchData$: Observable<any>;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: number;

  @Output()
  dataReset = new EventEmitter();

  componentDestructionNotifier$ = new Subject();
  refreshFormNotifier$: Subject<boolean> = new Subject();

  componentInst: IDynamicComponent;

  constructor(
    private readonly componentFactoryResolver: ComponentFactoryResolver,
    private readonly injector: Injector,
    private readonly renderer: Renderer2,
    @Inject(COMPONENTS_MAP_TOKEN)
    private readonly componentsMap: Map<number, Type<IDynamicComponent>>
  ) {}

  ngAfterContentInit() {
    this.refreshButtonHandler(this.refreshBtnHandler.nativeElement);
  }

  refreshButtonHandler(element: HTMLElement) {
    fromEvent(element, 'click')
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(_ => {
        this.refreshFormNotifier$.next(true);
      });
  }

  ngOnInit() {
    this.makeComponent(this.componentDef);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (this.componentInst) {
      this.componentInst.componentId = this.componentDef.componentId;
      this.componentInst.isParentFormSubmitted = this.isParentFormSubmitted;
      this.componentInst.specialityId = this.specialityId;
      this.componentInst.currentTabId = this.currentTabId;
    }
  }

  makeComponent(component: IComponentList) {
    this.viewContainer.clear();
    const componentClass = this.componentsMap.get(component.componentId);
    if (componentClass) {
      this.isComponentClassAvailable = true;

      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(componentClass);
      const componentRef = componentFactory.create(this.injector);

      this.componentInst = componentRef.instance;
      componentRef.instance.componentId = component.componentId;
      componentRef.instance.specialityId = this.specialityId;
      componentRef.instance.parentFormGroup = this.parentFormGroup;
      componentRef.instance.isParentFormSubmitted = this.isParentFormSubmitted;
      componentRef.instance.componentFetchData$ = this.componentFetchData$;
      componentRef.instance.refreshForm$ = this.refreshFormNotifier$;
      componentRef.instance.patientDemographicData = this.patientDemographicData;
      componentRef.instance.currentTabId = this.currentTabId;

      if (componentRef.instance.dataReset) {
        componentRef
          .instance
          .dataReset
          .pipe(takeUntil(this.componentDestructionNotifier$))
          .subscribe(result => this.dataReset.emit(result));
      }

      this.renderer.setStyle(componentRef.location.nativeElement, 'margin-bottom', '20px');
      this.renderer.setStyle(componentRef.location.nativeElement, 'display', 'block');
      this.viewContainer.insert(componentRef.hostView);
    }
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
}
