import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  ElementRef,
  ViewChild
} from '@angular/core';
import { FormGroup, FormBuilder, FormControl, FormArray } from '@angular/forms';
import { concat, Observable, Subject, of } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  filter,
  switchMap,
  map,
  takeUntil
} from 'rxjs/operators';
import { UtilService, UserService } from '@core/services';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserSession } from '@core/models/user-session';
import { ToastrService } from 'ngx-toastr';
import { IFETCHSEARCHDISEASEREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-search-disease.request';
import { IFETCHDISEASECHAPTERANDSUBCHAPTERREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-disease-chapter-and-subChapters.request';
import { IFETCHDISEASECHAPTERSFORCHAPTERSREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-disease-chapters-for-chapters.request';
import { IFETCHDISEASECHAPTERSFORSUBCHAPTERSREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-disease-chapters-for-subChapters.request';
import { IFETCHCOMMONLYUSEDDIAGNOSISREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-commonly-used-diagnosis.request';

import { IDELETEDIAGNOSISREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/delete-final-diagnosis.request';
import { IFETCHDISEASEREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-diseases.request';
import { IINSERTUPDATEFINALDIAGNOSISREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/insert-update-final-diagnosis.request.ts';
import { DatePipe } from '@angular/common';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { MatDialog, MatSnackBar } from '@angular/material';
import { EMR_OPHTHALOMOLGY_CONSTANTS } from '@app/modules/emr-ophthalmology/emr-ophthalmology-constants';
import { IFETCHPATIENTDIAGNOSISREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/fetch-patient-diagnosis.request';
import { IDELETEDCUSTOMISEDIAGNOSISREQUEST } from '@app/modules/emr-ophthalmology/components/final-diagnosis/models/delete-customized-diagnosis.request.ts';
import { getLandingPageCurrentSelectedAssistantDetails, IEmrOpthoState } from '@app/modules/emr-ophthalmology/reducers';
import * as fromLandingPagePatientVisitDetails from '../../reducers/patient-visit-details.reducer';
import { select, Store } from '@ngrx/store';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-final-diagnosis',
  templateUrl: './final-diagnosis.component.html',
  styleUrls: ['./final-diagnosis.component.scss']
})
export class FinalDiagnosisComponent implements OnInit, OnDestroy {
  @Input()
  statisComponent$ = Observable as any;
  private readonly FINAL_DIAGNOSIS = 5;
  private tabNm = 'FINAL_DIAGNOSIS';
  finalDiagnosisForm: FormGroup;
  patient$: Observable<any>;
  patientInput$ = new Subject<string>();
  seacrhControl = new FormControl();
  slectedDisease;
  diseaseChapterAndSubchapter;
  selectedChapterAndSubchapter: any = [];
  enableApisData = false;
  openChildData = false;
  customiseDiagnosis = true;
  viewChildData = false;
  isFirst = false;
  enableAddButton = true;
  searchDiseaseOrAddDiagnosisData: any = [];
  patientDemographicData: any;
  commonDiagnosis: any = [];
  diagnosisData: any;
  arr1 = [];
  serviceName = [];
  innerArray = [];
  addButtonDisable = false;
  checkedValue = false;
  customChaptersData: any = [];
  customSubChaptersData: any = [];
  customDisease: any = [];
  selectedIcdCode = false;
  chapterNm;
  subChapterNm: any;
  subLevelData: any = [];
  patientDiagnosisDataTransferArray: any = [];
  customisedDoctorDiagnosisData: any = [];
  date = new Date();
  isReadOnly = true;
  addData: any = [];
  downleveldata: any = [];
  _index: any;
  showSubLevel1 = false;
  diseasesNm: any;
  sublevelData: any = [];
  sublevel1Data: any = [];
  sublevelIndex: any;
  editAndHideSave = false;
  editableData;
  public isCollapsed = {};
  customMasterDataEdit: any;
  sublevel1Index: any;
  sublevel2Data: {};
  sublevel2Index: any;
  CanPushToSubchapter = false;
  CommmonData: any;
  ipdNumber: number;
  addtolist: boolean;
  makePayloadData: any = [];
  newArray: any = [];
  subChapterValue: string;
  IsShowSaveButton: boolean;
  currentPatientVisitDetails$: Observable<
    fromLandingPagePatientVisitDetails.State
  > = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));
  componentDestructionNotifier$ = new Subject();
  eventTargetData: boolean;
  dataObj;
  addbutton = false;
  searchApi = false;
  saveItem: any;
  patientParamData: any;
  dialogRef: any;
  data: any;

  constructor(
    private finalDiagnosisFormBuilder: FormBuilder,
    private EmrOphthalmologyApiService: EmrOpthoApiService,
    private userService: UserService,
    private utilService: UtilService,
    private datePipe: DatePipe,
    public dialog: MatDialog,
    private readonly store: Store<IEmrOpthoState>,
    private snackBar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) {
    this.userSession = this.userService.getUser();
  }

  private userSession: UserSession;

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    this.getPatientVisitDetails();
    this.fetchPatientDiagnosis();
    this.fetchCommonlyUsedDiagnosis();
  }

  initForm() {
    this.finalDiagnosisForm = this.finalDiagnosisFormBuilder.group({
      slectDisease: '',
      subChapterNm: '',
      chapterNm: '',
      diseases: '',
      checkValue: '',
      customChapterNm: '',
      customSubChapterNm: '',
      value: '',
      isRequired: true
    });
    this.finalDiagnosisForm.get('slectDisease').valueChanges.subscribe(val => {
      if (val) {
        this.slectedDisease = val;
        this.fetchDiseaseChaptersAndSubChapters();
        this.enableAddButton = false;
        this.searchApi = true;
      }
    });
    this.finalDiagnosisForm
      .get('customChapterNm')
      .valueChanges.subscribe(val => {
        if (val) {
          this.chapterNm = val;
          this.fetchDiseaseSubChapters();
        }
      });
    this.finalDiagnosisForm
      .get('customSubChapterNm')
      .valueChanges.subscribe(val => {
        if (val) {
          this.subChapterNm = val;
          this.fetchDiseases();
        }
      });
    this.finalDiagnosisForm.get('value').valueChanges.subscribe(val => {
      if (val) {
        this.customMasterDataEdit = val;
      }
    });
  }

  save() {
    if (this.patientParamData.pastVisitDetails === 'true') {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to save ?`
        },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result) {
          this.insertOrUpdateFinalDiagnosis();
        }
      });
    } else {
      this.insertOrUpdateFinalDiagnosis();
    }

  }

  // getting patient visit details from the store
  getPatientVisitDetails() {
    this.currentPatientVisitDetails$
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(data => {
        if (data.patientVisitDetails) {
          this.patientDemographicData = data.patientVisitDetails;
        }
      });
  }

  /// RightSidePart

  /**
   * @description This method is used to Fech the patient regarding diagnosis data
   */

  fetchPatientDiagnosis() {
    const payload: IFETCHPATIENTDIAGNOSISREQUEST = {
      authenticatedUserId: this.userSession.regId,
      orgId: this.userSession.orgId,
      doctorId: this.patientParamData.doctorId,
      patientVisitId: this.patientDemographicData.patientVisitId
    };
    this.EmrOphthalmologyApiService.fetchPatientDiagnosis(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.commonDiagnosis = response.data;
          if (this.commonDiagnosis.length) {
            this.selectedChapterAndSubchapter = this.commonDiagnosis;
            this.ipdNumber = this.commonDiagnosis[0].opdNumber || null;
          }
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('Something went wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }

  /**
   * @description This method is used to serachDiseases in the ng-select(rightSide)
   */

  searchDisease() {
    this.patient$ = concat(
      of([]),
      this.patientInput$.pipe(
        debounceTime(200),
        distinctUntilChanged(),
        filter(searchText => searchText.length > 3),
        switchMap(search => {
          const payload: IFETCHSEARCHDISEASEREQUEST = {
            diseaseNm: search,
            authenticatedUserId: this.userSession.regId,
            doctorId: this.patientParamData.doctorId,
            orgId: this.userSession.orgId
          };
          return this.EmrOphthalmologyApiService.searchDisease(payload).pipe(
            map(response => {
              if (response.responseCode === 'E200') {
                this.searchDiseaseOrAddDiagnosisData = response.data;
                if (this.searchDiseaseOrAddDiagnosisData.length) {
                  this.enableApisData = false;
                  this.enableAddButton = false;
                  this.selectedIcdCode = false;
                  this.customSubChaptersData = [];
                  this.customChaptersData = [];
                  this.customDisease = [];
                  this.addbutton = true;
                  this.finalDiagnosisForm.patchValue([]);
                  // this.searchApi = true;
                } else {
                  this.enableApisData = true;
                  this.enableAddButton = true;
                  this.customSubChaptersData = [];
                  this.customChaptersData = [];
                  this.customDisease = [];
                  this.addbutton = false;
                  this.searchApi = false;
                  this.finalDiagnosisForm.patchValue([]);
                  this.fetchDiseaseChapters();
                }
                return response.data;
              } else if (response.responseCode === 'E400') {
                throw new Error('failure');
              }
            })
          );
        })
      )
    );
  }

  /**
   * @description This method is used to select the checkbox and call the fetchChapters Api
   */

  customData() {
    const data = document.getElementById('icd');
    if (data['checked'] === true) {
      this.selectedIcdCode = true;
      this.fetchDiseaseChapters();
    } else {
      this.enableApisData = true;
      this.selectedIcdCode = false;
    }
  }

  /**
   * @description This method is used to select the chapters and subchapters and diseses if the serched disease in found
   */

  fetchDiseaseChaptersAndSubChapters() {
    const payload: IFETCHDISEASECHAPTERANDSUBCHAPTERREQUEST = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      chapterId: this.slectedDisease.chapterId,
      subChapterId: this.slectedDisease.subChapterId,
      diseasesNm: this.slectedDisease.diseasesNm
    };
    this.EmrOphthalmologyApiService.fetchDiseaseChaptersAndSubChapters(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.dataObj = response.data;
          this.finalDiagnosisForm.patchValue(response.data);
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('Something Went Wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }
  /**
   * @description This method is used to call the subandsunchapters
   */
  addToList(item) {
    this.searchApi = false;
    this.customiseDiagnosis = true;
    if (this.eventTargetData === true || this.eventTargetData === false) {
      if (this.eventTargetData === true) {
        item.isRequired = true;
        this.eventTargetData = null;
      } else if (this.eventTargetData === false) {
        item.isRequired = false;
      }
    } else {
      this.dataObj.isRequired = true;
      this.selectedChapterAndSubchapter.push(this.dataObj);
      this.finalDiagnosisForm.patchValue({ slectDisease: '', subChapterNm: '', chapterNm: '', diseases: '', });
    }
  }


  /**
   * @description This method is used to fetchChapters when the  custom selected diseases is selected the
   */

  fetchDiseaseChapters() {
    this.data = this.slectedDisease.diseasesNm;
    const payload: IFETCHDISEASECHAPTERSFORCHAPTERSREQUEST = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      customDiagnosis: this.data
    };
    this.EmrOphthalmologyApiService.fetchDiseaseChapters(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.customChaptersData = response.data;
          this.addbutton = false;
          this.customDisease = [];
        } else if (response.responseCode === 'E400') {
          this.snackBar.open('failure', response.responseMessage, {duration : 3000});
        }
      },
      () => {
        this.snackBar.open('failure', 'Something Went Wrong', {duration : 3000});
      }
    );
  }
  /**
   * @description This method is used to fetchSubChapters when the  custom selected diseases is selected the
   */
  fetchDiseaseSubChapters() {
    const payload: IFETCHDISEASECHAPTERSFORSUBCHAPTERSREQUEST = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      chapterId: this.chapterNm.chapterId,
      customDiagnosis: this.data,
      chapterNm: this.chapterNm.chapterNm
    };
    this.EmrOphthalmologyApiService.fetchDiseaseSubChapters(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.customSubChaptersData = response.data;
          this.customDisease = [];
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('Something Went Wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }

  /**
   * @description This method is used to fetch the diseases after the chapter subchapter
   */
  fetchDiseases() {
    const payload: IFETCHDISEASEREQUEST = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      subChapterId: this.subChapterNm.subChapterId,
      customDiagnosis: this.data,
      chapterId: this.chapterNm.chapterId,
      chapterNm: this.chapterNm.chapterNm,
      subChapterNm: this.subChapterNm.subChapterNm
    };
    this.EmrOphthalmologyApiService.fetchDiseases(payload).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.customDisease = response.data;
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('Something Went Wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }

  /**
   * @description This method is used to fetch the  ,level1,level2,level3,level4 diseseas afetr selecting diseases
   */
  IsChecked(check) {
    const addbtn = <HTMLInputElement>document.getElementById('isDisabled');
    if (check.target.checked) {
      addbtn.disabled = false;
      this.addToSubList();
    }
    if (!check.target.checked) {
      addbtn.disabled = true;
    }
  }
  addToSubList() {
    
    this.selectedIcdCode = false;
    this.addtolist = true;
    this.customiseDiagnosis = false;
    this.enableApisData = false;
    this.searchApi = false;
    if (this.CommmonData.isExistSubLevel === false) {
      this.CommmonData.isRequired = true;
      this.selectedChapterAndSubchapter.push(this.CommmonData);
      this.CanPushToSubchapter = true;
    }
    this.finalDiagnosisForm.patchValue({slectDisease : ''});
  }
  selectedSubchapter() {
    const subChapter = <HTMLInputElement>(
      document.getElementById('selectsubchapter')
    );
    this.subChapterValue = subChapter.value;
  }
  fetchDiseasesLevelsData(level, commonData, index, event) {
    if (event) {
      const addbtn = <HTMLInputElement>document.getElementById('isDisabled');
      if (event.target.checked) {
        addbtn.disabled = false;
      }
      if (!event.target.checked) {
        addbtn.disabled = true;
      }
    }
    this.CommmonData = commonData;
    if (level === 'mainlevel') {
      this._index = index;
    }
    if (level === 'sublevel') {
      this.sublevelIndex = index;
    }
    if (level === 'sublevel1') {

      this.sublevel1Index = index;
    }
    if (level === 'sublevel2') {
      this.sublevel2Index = index;
    }
    if (!commonData.diseaseLevel1Nm) {
    }
    if (
      commonData.isExistSubLevel === true ||
      commonData.isExistSublevel === true
    ) {
      this.CanPushToSubchapter = !this.CanPushToSubchapter;
      this.diseasesNm = commonData.diseasesNm;
      let payload;
      if (level === 'mainlevel') {
        payload = {
          authenticatedUserId: this.userSession.regId,
          orgId: this.userSession.orgId,
          doctorId: this.patientParamData.doctorId,
          customDiagnosis: this.data,
          chapterId: this.chapterNm.chapterId,
          chapterNm: this.chapterNm.chapterNm,
          subChapterNm: this.subChapterNm.subChapterNm,
          subChapterId: this.subChapterNm.subChapterId,
          diseasesId: commonData.diseasesId,
          diseasesNm: commonData.diseasesNm
        };
      }
      if (level === 'sublevel') {
        payload = {
          authenticatedUserId: this.userSession.regId,
          orgId: this.userSession.orgId,
          doctorId: this.patientParamData.doctorId,
          customDiagnosis: this.data,
          chapterId: this.chapterNm.chapterId,
          chapterNm: this.chapterNm.chapterNm,
          subChapterNm: this.subChapterNm.subChapterNm,
          subChapterId: this.subChapterNm.subChapterId,
          diseasesId: commonData.diseasesId,
          diseasesNm: commonData.diseasesNm,
          diseaseLevel1Nm: commonData.diseaseLevel1Nm,
          diseaseLevel1Id: commonData.diseaseLevel1Id
        };
      }
      if (level === 'sublevel1') {
        payload = {
          authenticatedUserId: this.userSession.regId,
          orgId: this.userSession.orgId,
          doctorId: this.patientParamData.doctorId,
          customDiagnosis: this.data,
          chapterId: this.chapterNm.chapterId,
          chapterNm: this.chapterNm.chapterNm,
          subChapterNm: this.subChapterNm.subChapterNm,
          subChapterId: this.subChapterNm.subChapterId,
          diseasesId: commonData.diseasesId,
          diseasesNm: commonData.diseasesNm,
          diseaseLevel1Nm: commonData.diseaseLevel1Nm,
          diseaseLevel1Id: commonData.diseaseLevel1Id,
          diseaseLevel2Nm: commonData.diseaseLevel2Nm,
          diseaseLevel2Id: commonData.diseaseLevel2Id
        };
      }
      if (level === 'sublevel2') {
        payload = {
          authenticatedUserId: this.userSession.regId,
          orgId: this.userSession.orgId,
          doctorId: this.patientParamData.doctorId,
          customDiagnosis: this.data,
          chapterId: this.chapterNm.chapterId,
          chapterNm: this.chapterNm.chapterNm,
          subChapterNm: this.subChapterNm.subChapterNm,
          subChapterId: this.subChapterNm.subChapterId,
          diseasesId: commonData.diseasesId,
          diseasesNm: commonData.diseasesNm,
          diseaseLevel1Nm: commonData.diseaseLevel1Nm,
          diseaseLevel1Id: commonData.diseaseLevel1Id,
          diseaseLevel2Nm: commonData.diseaseLevel2Nm,
          diseaseLevel2Id: commonData.diseaseLevel2Id,
          diseaseLevel3Nm: commonData.diseaseLevel3Nm,
          diseaseLevel3Id: commonData.diseaseLevel3Id
        };
      }
      this.EmrOphthalmologyApiService.fetchDiseasesLevelsData(
        payload
      ).subscribe(
        response => {
          if (response.responseCode === 'E200') {
            // this.customDisease = response.data;
            if (level === 'mainlevel') {
              this.downleveldata = this.customDisease[
                this._index
              ].downleveldata = response.data;
            }
            if (level === 'sublevel') {
              this.sublevelData = this.customDisease[this._index].downleveldata[
                this.sublevelIndex
              ].sublevelData = response.data;
            }
            if (level === 'sublevel1') {
              this.sublevel1Data = this.customDisease[
                this._index
              ].downleveldata[this.sublevelIndex].sublevelData[
                this.sublevel1Index
              ].sublevel1Data = response.data;
            }
            if (level === 'sublevel2') {
              this.sublevel2Data = this.customDisease[
                this._index
              ].downleveldata[this.sublevelIndex].sublevelData[
                this.sublevel1Index
              ].sublevel1Data[this.sublevel2Index].sublevel2Data =
                response.data;
            }
            // this.downleveldata = response.data;
          } else if (response.responseCode === 'E400') {
            this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
          }
        },
        () => {
          this.snackBar.open('Something Went Wrong', 'Dismiss', { duration: 3000 });
        }
      );
    } else {
      return [];
    }
  }

  /**
   * @description This method is used to delete the the record based on the condition
   */

  deleteFinalDiagnosis(item, index) {
    if (this.patientParamData.pastVisitDetails === 'true' && item.patientDiseasesId) {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data:  `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`
        },
        disableClose: true
      });
    } else {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: 'Do you want to delete diagnosis ?'
        },
        disableClose: true
      });
    }
    this.dialogRef.afterClosed().subscribe(result => {
      if (result) {
        if (item.patientDiseasesId) {
          const payload: IDELETEDIAGNOSISREQUEST = {
            authenticatedUserId: this.userSession.regId,
            doctorId: this.patientParamData.doctorId,
            orgId: this.userSession.orgId,
            patientDiseasesId: item.patientDiseasesId,
            versionId: item.versionId,
            patientVisitId: this.patientDemographicData.patientVisitId
          };
          this.EmrOphthalmologyApiService.deleteFinalDiagnosis(
            payload
          ).subscribe(
            response => {
              if (response.responseCode === 'E200') {
                this.selectedChapterAndSubchapter.splice(index, 1);
                if (!this.selectedChapterAndSubchapter.length) {
                  this.patientDiagnosisDataTransferArray = [];
                }
              } else if (response.responseCode === 'E400') {
                this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
              }
            },
            () => {
              this.snackBar.open('Something Went Wrong', 'Dismiss', { duration: 3000 });
            }
          );
        } else {
          this.selectedChapterAndSubchapter.splice(index, 1);
          if (!this.selectedChapterAndSubchapter.length) {
            this.patientDiagnosisDataTransferArray = [];
          }
        }
      }
    });
  }

  /**
   * @description This method is used to save the record the all records
   */

  insertOrUpdateFinalDiagnosis() {
    this.makePayloadData = this.selectedChapterAndSubchapter;
    const payload: IINSERTUPDATEFINALDIAGNOSISREQUEST = {
      authenticatedUserId: this.userSession.regId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      doctorId: this.patientParamData.doctorId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId,
      hosPatientId: this.patientDemographicData.hosPatientId,
      visitDt: this.datePipe.transform(this.date, 'yyyy-MM-dd '),
      finalDiagnosisList: this.makePayloadData,
      opdNumber: this.ipdNumber

    };
    this.EmrOphthalmologyApiService.insertOrUpdateFinalDiagnosis(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.snackBar.open('Data saved succesfully', 'undo', {
            duration: 3000
          });
          this.commonDiagnosis = response.data;
          this.ipdNumber = this.commonDiagnosis.opdNumber;
          this.makePayloadData = [];
          this.fetchPatientDiagnosis();
          this.fetchCommonlyUsedDiagnosis();
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }

  /**
   * @description This method is used get the selected item from the input checkbox
   */

  patientDiagnosisDataTransfer(item, event , i) {
    this.saveItem = item;

    if (event.target.checked) {
      this.patientDiagnosisDataTransferArray.push(item);
    } else if (!event.target.checked) {
      const index = this.patientDiagnosisDataTransferArray.findIndex(val => {
        return val.diseasesNm === item.diseasesNm;
      });
      this.patientDiagnosisDataTransferArray.splice(index, 1);
    }
  }

  /**
   * @description This method is used to save the particular selected record in the checkbox and it dispaly in the left side
   */
  saveAndAddTodiagnosisList() {

    const payload = {
      finalDiagnosisList: this.patientDiagnosisDataTransferArray,
      authenticatedUserId: this.userSession.regId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      doctorId: this.patientParamData.doctorId,
      isMasterdataCustomized: true,
      specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID
    };
    this.EmrOphthalmologyApiService.insertOrUpdateCommonlyUsedDiagnosis(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          // this.addData = response.data;
          // this.diagnosisData[this.diagnosisData.length - 1].diagnosisList.push(this.saveItem);
          // this.customisedDoctorDiagnosisData = this.addData.finalDiagnosisList;
          this.patientDiagnosisDataTransferArray = [];
          this.fetchCommonlyUsedDiagnosis();
          this.fetchPatientDiagnosis();
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }

  ////// endofrightPart

  /// leftsidePart

  /**
   * @description This method is used to fetch the commonly used diagnosis dat(masterData)
   */

  fetchCommonlyUsedDiagnosis() {
    const payload: IFETCHCOMMONLYUSEDDIAGNOSISREQUEST = {
      authenticatedUserId: this.userSession.regId,
      orgId: this.userSession.orgId,
      doctorId: this.patientParamData.doctorId,
      specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID,
    };
    this.EmrOphthalmologyApiService.fetchCommonlyUsedDiagnosis(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.diagnosisData = response.data;
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Dismiss', { duration: 3000 });
      }
    );
  }

  /**
   * @description This method is used to fetch the delete the custom record data
   */

  deleteCustomizedDiagnosis(commonData, item, i, index) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: {
        title:
          'Confirm',
        data: 'Do you to want delete this diagnosis ?'
      },
      disableClose: true
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result === true) {
        const payload: IDELETEDCUSTOMISEDIAGNOSISREQUEST = {
          authenticatedUserId: this.userSession.regId,
          orgId: this.userSession.orgId,
          orgGrpId: this.userSession.orgGroupId,
          doctorId: this.patientParamData.doctorId,
          commonDiagnosisId: item.commonDiagnosisId
        };
        this.EmrOphthalmologyApiService.deleteCustomizedDiagnosis(
          payload
        ).subscribe(
          response => {
            if (response.responseCode === 'E200') {
              this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
              this.fetchCommonlyUsedDiagnosis();
            } else if (response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'Dismiss', { duration: 3000 });
            }
          },
          () => {
            this.snackBar.open('something went wrong', 'Dismiss', { duration: 3000 });
          }
        );
      }
    });
  }

  /**
   * @description This method is used to fetch the insert or update the master data on the left side
   */

  insertOrUpdateMasterDataFinalDiagnosis(item) {
    console.log(item);
    const obj = [
      {
        chapterId: item.chapterId,
        chapterNm: item.chapterNm,
        commonDiagnosisId: item.commonDiagnosisId,
        diseaseCode: item.diseaseCode,
        diseasesNm: this.customMasterDataEdit,
        diseasesId: item.diseasesId,
        isMasterdataCustomized: item.isMasterdataCustomized,
        versionId: item.versionId
      }
    ];
    const payload = {
      authenticatedUserId: this.userSession.regId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      doctorId: this.patientParamData.doctorId,
      finalDiagnosisList: obj
    };
    this.EmrOphthalmologyApiService.insertOrUpdateFinalDiagnosis(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.commonDiagnosis = response.data;
          this.fetchCommonlyUsedDiagnosis();
        } else if (response.responseCode === 'E400') {
          this.snackBar.open('error', response.responseMessage);
        }
      },
      () => {
        this.snackBar.open('failure', 'something went wrong');
      }
    );
  }
  /**
   * @description This method is used to edit the data in masterData
   */

  editTabName(item, commonData, index) {
    const data = this.diagnosisData[13];
    this.isReadOnly = false;
    const diagnosisData = data.diagnosisList[index];
    diagnosisData.isReadOnly = false;
    diagnosisData.isSave = true;
    this.diagnosisData[13].diagnosisList[index] = diagnosisData;
    this.editAndHideSave = !this.editAndHideSave;
  }
  /**
   * @description This method is used to hide and show the MasterData from left side
   */
  showChildDiagnosisData(commonData, i) {
 
    if (commonData.isExpand === false) {
      commonData.isExpand = true;
    } else if (commonData.isExpand === true) {
      commonData.isExpand = false;
    }
    this.diagnosisData[i] = commonData;
  }
/**
   * @description This method is used to masterData check and uncheck
   */
  masterDataEdit(item, event) {

    if (event.target.checked) {
      if (this.selectedChapterAndSubchapter.length) {
        this.selectedChapterAndSubchapter.forEach(element => {
          if (element.diseasesNm === item.diseasesNm) {
            this.snackBar.open('This Diagnosis already added to patient', 'undo', { duration: 3000 });
          } else {
            item.isRequired = true;
            this.selectedChapterAndSubchapter.push(item);
          }
        });
      } else {
        item.isRequired = true;
        this.selectedChapterAndSubchapter.push(item);
      }


    } else if (!event.target.checked) {
      const index = this.selectedChapterAndSubchapter.findIndex(val => {
        return val.diseasesNm === item.diseasesNm;
      });
      this.selectedChapterAndSubchapter.splice(index, 1);
    }
  }

  summaryRecord(selectedService, event) {
    if (selectedService.patient) {
      if (event.target.checked) {
        const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
          data: {
            title: 'Confirm',
            data: 'Are you sure want to map this record ?'
          },
          disableClose: true
        });
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            const payload = {
              orgId: this.userSession.orgId,
              hospitalPatientId: this.patientDemographicData.hosPatientId,
              patientVisitId: this.patientDemographicData.patientVisitId,
              authenticatedUserId: this.userSession.regId,
              panelId: selectedService.panelId,
              testId: selectedService.testId,
              isRequired: 'true',
              doctorId: this.patientParamData.doctorId
            };
            this.EmrOphthalmologyApiService
              .deleteFinalDiagnosis(payload)
              .subscribe((response: any) => {
                if (response.responseCode === 'E200') {
                  this.snackBar.open(response.responseMessage, 'undo', {duration: 3000});
                  if (!this.selectedChapterAndSubchapter.length) {
                    this.patientDiagnosisDataTransferArray = [];
                  }
                } else if (response.responseCode === 'E400') {
                  this.snackBar.open(response.responseMessage, 'Dismiss', {
                    duration: 3000
                  });
                }
              },
                () => {
                  this.snackBar.open('something went wrong', 'Dismiss', { duration: 3000 });
                }
              );
          }
        });
      } else if (!event.target.checked) {
        const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
          data: {
            title: 'Confirm',
            data: 'Are you sure want to unMap this record from summary ?'
          },
          disableClose: true
        });
        dialogRef.afterClosed().subscribe(result => {
          if (result) {
            const payload = {
              orgId: this.userSession.orgId,
              hospitalPatientId: this.patientDemographicData.hosPatientId,
              patientVisitId: this.patientDemographicData.patientVisitId,
              authenticatedUserId: this.userSession.regId,
              panelId: selectedService.panelId,
              testId: selectedService.testId,
              isRequired: 'false',
              doctorId: this.patientParamData.doctorId
            };
            this.EmrOphthalmologyApiService
              .deleteFinalDiagnosis(payload)
              .subscribe((response: any) => {
                if (response.responseCode === 'E200') {
                  if (!this.selectedChapterAndSubchapter.length) {
                    this.patientDiagnosisDataTransferArray = [];
                  }
                } else if (response.responseCode === 'E400') {
                  this.snackBar.open(response.responseMessage, 'Dismiss', {
                    duration: 3000
                  });
                }
              },
                () => {
                  this.snackBar.open('something went wrong', 'Dismiss', { duration: 3000 });
                }
              );
          }
        });
      }
    } else {
      if (event.target.checked) {
        this.eventTargetData = true;
        this.addToList(selectedService);
      } else if (!event.target.checked) {
        this.eventTargetData = false;
        this.addToList(selectedService);
      }

    }
  }

  saveAndEditButtonHide() {
    this.editAndHideSave = !this.editAndHideSave;
  }
  ngOnDestroy() { }
}
