import {
  EmrOpthoActionTypes,
  EmrOpthoActions
} from '../actions/emr-ophthalmology-actions';
import { IFetchEmployeesEmrResponse } from '../components/assistants-list/models/fetch-employees-emr-response';

export interface State {
  isLoading: boolean;
  errorOccurred: boolean;
  assistantList: Array<IFetchEmployeesEmrResponse>;
  currentSelectedAssistantId: number;
}

export const INITIAL_STATE: State = {
  assistantList: [],
  errorOccurred: false,
  isLoading: false,
  currentSelectedAssistantId: null
};

export function reducer(
  state: State = INITIAL_STATE,
  action: EmrOpthoActions
): State {

  switch (action.type) {
    case EmrOpthoActionTypes.FetchAssistantsList: {
      return {
        assistantList: [],
        isLoading: true,
        errorOccurred: false,
        currentSelectedAssistantId: null
      };
    }
    case EmrOpthoActionTypes.FetchAssistantsListError: {
      return {
        assistantList: [],
        isLoading: false,
        errorOccurred: true,
        currentSelectedAssistantId: null
      };
    }
    case EmrOpthoActionTypes.FetchAssistantsListSuccess: {
      return {
        assistantList: action.payload?action.payload.map(assistant => ({ ...assistant })):[],
        isLoading: false,
        errorOccurred: false,
        currentSelectedAssistantId: state.currentSelectedAssistantId
      };
    }
    default: {
      return state;
    }
  }
}
