import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SettingsComponent } from '@app/modules/emr-ophthalmology/components/settings/setttings.component';
import { EmrComponent } from '@app/modules/emr-ophthalmology/components/emr/emr.component';
import { EMR_OPHTHALOMOLGY_CONSTANTS } from './emr-ophthalmology-constants';
import { SystematicHistoryComponent } from './components/systematic-history/systematic-history.component';
import { LandingPageComponent } from '@app/modules/emr-ophthalmology/components/landing-page/landing-page.component';
import { AssistantSettingsComponent } from './components/assistant-settings/assistant-settings.component';
import { SettingsEmptyComponent } from './components/settings-empty/settings-empty.component';
import { AssitantSettingsDeactivateGuard } from './components/assistant-settings/assistant-settings-deactive-guard.service';
import { TabSettingsWrapperComponent } from './components/tab-settings-wrapper/tab-settings-wrapper.component';
import { AllergiesComponent } from './components/allergies/allergies.component';
import { FinalGlassesComponent } from './components/final-glasses/final-glasses.component';
import { IopComponent } from './components/iop/iop.component';
import { VisionComponent } from './components/vision/vision.component';
import { KeratometryComponent } from './components/keratometry/keratometry.component';
import { RetinoscopyComponent } from './components/retinoscopy/retinoscopy.component';
import { SystematicExaminationComponent } from './components/systematic-examination/systematic-examination.component';
import { PmtComponent } from '@app/modules/emr-ophthalmology/components/pmt/pmt.component';
import { ColorComponent } from './components/color/color.component';
import { OrthopticsComponent } from './components/orthoptics/orthoptics.component';
import { PhysicalParametersComponent } from './components/physical-parameters/physical-parameters.component';

import { AutoRefractionComponent } from '@app/modules/emr-ophthalmology/components/auto-refraction/auto-refraction.component';

import { EyeExaminationComponent } from './components/eye-examination/eye-examination.component';
import { OldGlassesComponent } from './components/old-glasses/old-glasses.component';
import { DilatedRefractionComponent } from './components/dilated-refraction/dilated-refraction.component';
import { UndilatedRefractionComponent } from './components/undilated-refraction/undilated-refraction.component';
import { ContactLensPrescriptionsComponent } from './components/contact-lens-prescriptions/contact-lens-prescriptions.component';
import { LaboratoryComponent } from '@app/modules/emr-ophthalmology/components/cpoe/components/laboratory/laboratory.component';
import { CpoeHeaderComponent } from '@app/modules/emr-ophthalmology/components/cpoe/components/cpoe-header/cpoe-header.component';
import { OphthalmologyComponent } from '@app/modules/emr-ophthalmology/components/cpoe/components/ophthalmology/ophthalmology.component';
import { RadiologyComponent } from '@app/modules/emr-ophthalmology/components/cpoe/components/radiology/radiology.component';
import { AppearenceComponent } from '@app/modules/emr-ophthalmology/components/appearence/appearence.component.ts';

import { ERXComponent } from './components/erx/erx.component';
import { FinalDiagnosisComponent } from '@app/modules/emr-ophthalmology/components/final-diagnosis/final-diagnosis.component';
import { SurgeryComponent } from '@app/modules/emr-ophthalmology/components/surgery/surgery.component';
import { PreOperationComponent } from '@app/modules/emr-ophthalmology/components/surgery/components/pre-operation/pre-operation.component';
import { OperationComponent } from '@app/modules/emr-ophthalmology/components/surgery/components/operation/operation.component';
import { PostOperationComponent } from '@app/modules/emr-ophthalmology/components/surgery/components/post-operation/post-operation.component';
import { CanvasComponent } from './components/canvas/canvas.component';
import { SummaryComponent } from '@app/modules/emr-ophthalmology/components/summary/summary.component';
import { FileUploaderComponent } from './components/file-uploader/file-uploader.component';
// import { AdmitComponent } from './components/admit/admit.component';
const settingsRoutes: Routes = [
  {
    path: 'tab/:tabId',
    component: TabSettingsWrapperComponent
  },
  {path:'files-uploader',
  component:FileUploaderComponent,
  },
];

const emrOpthChildRoutes: Routes = [
  {
    path: 'emr-landing-page',
    data: {
      title: 'Emr Landing Page'
    },
    component: LandingPageComponent
  },
  {
    path: 'keratometry',
    component: KeratometryComponent
  },
  {path:'files-uploader',
  component:FileUploaderComponent,
  },

  {
    path: 'FinalDiagnosis',
    component: FinalDiagnosisComponent
  },
  {
    path: 'retinoscopy',
    component: RetinoscopyComponent
  },
  {
    path: 'iop',
    component: IopComponent
  },
  {
    path: 'vision',
    component: VisionComponent
  },
  {
    path: 'pmt-component',
    component: PmtComponent
  },
  {
    path: 'auto-refraction',
    component: AutoRefractionComponent
  },
  {
    path: 'emr',
    component: EmrComponent,
  },
  {
    path: 'systematic-history',
    component: SystematicHistoryComponent
  },
  {
    path: 'allergies',
    component: AllergiesComponent
  },
  // {
  //   path: 'Admit',
  //   component: AdmitComponent
  // },
  {
    path: 'final-glasses',
    component: FinalGlassesComponent
  },
  {
    path: 'old-glasses',
    component: OldGlassesComponent
  },
  {
    path: 'dilated-refraction',
    component: DilatedRefractionComponent
  },
  {
    path: 'undilated-refraction',
    component: UndilatedRefractionComponent
  },
  {
    path: 'color',
    component: ColorComponent
  },
  {
    path: 'orthoptics',
    component: OrthopticsComponent
  },
  {
    path: 'erx',
    component: ERXComponent
  },
  {
    path: 'physical-parameters',
    component: PhysicalParametersComponent
  },
  {
    path: 'eye-examination',
    component: EyeExaminationComponent
  },
  {
    path: 'app-appearence',
    component: AppearenceComponent
  },
  {
    path: 'systematic-examination',
    component: SystematicExaminationComponent
  },
  {
    path: 'lenseprescription',
    component: ContactLensPrescriptionsComponent
  },
  {
    path: 'canvas',
    component: CanvasComponent
  },
  {
    path: 'cpoe',
    component: CpoeHeaderComponent,
    children: [
      {
        path: 'opthamology',
        component: OphthalmologyComponent
      },
      {
        path: 'laboratory',
        component: LaboratoryComponent
      },
      {
        path: 'radiology',
        component: RadiologyComponent
      }
    ]
  },
  {
    path: 'summary',
    component: SummaryComponent
  },
  {
    path: 'surgery',
    component: SurgeryComponent,
    children: [
      {
        path: 'pre-operation',
        component: PreOperationComponent
      },
      {
        path: 'opeartion',
        component: OperationComponent
      },
      {
        path: 'post-operation',
        component: PostOperationComponent
      }
    ]
  },
  // {
  //   path: 'erx',
  //   component: ERXComponent
  // },
  {
    path: ' ',
    redirectTo: 'settings',
    pathMatch: 'prefix'
  }
];

const emrOpthalmologyRoutes: Routes = [
  {
    path: '',
    data: {
      specialityId: EMR_OPHTHALOMOLGY_CONSTANTS.SPECIALITY_ID
    },
    children: [
      {
        path: 'settings',
        component: SettingsComponent,
        data: {
          title: 'EMR Ophthalmology Settings'
        },
        children: [
          ...settingsRoutes,
          {
            path: 'assistant/:assistantId',
            component: AssistantSettingsComponent,
            canDeactivate: [AssitantSettingsDeactivateGuard]
          },
          {
            path: '**',
            component: SettingsEmptyComponent
          }
        ]
      },
      ...emrOpthChildRoutes
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(emrOpthalmologyRoutes)],
  exports: [RouterModule]
})
export class EmrOpthalmologyRoutingModule {}
