  openDeleteConfirmationModal(message: string) {
    if (this.patientParamData.pastVisitDetails === 'true') {
      this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
    } else {
      this.message = 'Do You Want Delete  Eye Examination Component Data?';
    }
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  makeDeletePayload() {
    const formValue = this.eyeExaminationForm.value;
    const child_table = formValue.child_table
      .filter(row => {
        const rowChildTable = row.tableData;
        return rowChildTable && rowChildTable.every(val => val.version_id);
      })
      .map(row => {
        return {
          childComponentId: row.childComponentId,
          tableData: row.tableData.map(val => ({
            version_id: val.version_id,
            emr_default_id: val.emr_default_id
          }))
        };
      });
    const { regId, orgGroupId, orgId } = this.userData;

    return {
      specialityId: this.specialityId,
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      // bedId: this.patientDemographicData.bedId,
      data: {
        [this.componentId]: {
          version_id: formValue.version_id,
          emr_default_id: formValue.emr_default_id,
          child_table
        }
      }
    };
  }

  handleRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .eyeExaminationForm.value;
        const { version_id, child_table: [ { tableData } ] } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        doDelete = doDelete || tableData.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this.openDeleteConfirmationModal('')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeletePayload())
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this .eyeExaminationForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this.resetForm();
            this.dataReset.next(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  resetForm() {
    this.eyeExaminationForm.patchValue({
      version_id: null,
      emr_default_id: null,
      left_appendages_eyelids_chalazion: null,
      left_appendages_eyelids_ptosis: null,
      left_appendages_eyelids_swelling: null,
      left_appendages_eyelids_entropion: null,
      left_appendages_eyelids_ectropion: null,
      left_appendages_eyelids_mass: null,
      left_appendages_eyelids_meibomitis: null,
      left_appendages_eyelashes_trichiasis: null,
      left_appendages_eyelashes_dystrichiasis: null,
      left_appendages_lacrimal_sac_swelling: null,
      left_appendages_lacrimal_sac_regurgitation: null,
      left_appendages_syringing: null,
      left_appendages_remarks: null,
      right_appendages_eyelids_chalazion: null,
      right_appendages_eyelids_ptosis: null,
      right_appendages_eyelids_swelling: null,
      right_appendages_eyelids_entropion: null,
      right_appendages_eyelids_ectropion: null,
      right_appendages_eyelids_mass: null,
      right_appendages_eyelids_meibomitis: null,
      right_appendages_eyelashes_trichiasis: null,
      right_appendages_eyelashes_dystrichiasis: null,
      right_appendages_lacrimal_sac_swelling: null,
      right_appendages_lacrimal_sac_regurgitation: null,
      right_appendages_syringing: null,
      right_appendages_remarks: null,
    });

    const childTableFormArray = <FormArray>this.eyeExaminationForm.get('child_table');
    const childTableControls = childTableFormArray.controls;

    for (let i = 0, j = childTableControls.length; i < j; i++) {
      const group = <FormGroup>childTableFormArray.at(i);
      const tableData = <FormArray>group.get('tableData');
      for (let k = 0, l = tableData.controls.length; k < l; k++) {
        tableData.at(k).reset();
      }
    }

  }

