import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { MatSnackBar, MatDialog } from '@angular/material';

// import { GlassDetailsService } from '@app/modules/emr-ophthalmology/services/glass-details.service';
@Component({
  selector: 'app-discharge-date-picker-model',
  templateUrl: './discharge-date-picker-model.component.html',
  styleUrls: ['./discharge-date-picker-model.component.scss']
})
export class DischargeDatePickerModelComponent implements  OnInit, OnDestroy {
  dischargeComponentForm: FormGroup;
  maxDate = new Date();
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();
  isEdit = false;
  formData: any;

  constructor(
    private readonly fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef<DischargeDatePickerModelComponent>,
    private readonly snackBar: MatSnackBar,


    private datePipe: DatePipe,
    // public serviceFile: GlassDetailsService,
    public emrOpthoService: EmrOpthoApiService
  ) {
    // this.isEdit = injectedData.edit;
    this.dischargeComponentForm = this.fb.group({
      dischargeDateAndTime: new Date()
    });
  }
  ngOnInit() {
    const { required, pattern } = Validators;
  }
  dischargeRequestFn(data) {
    if (this.dischargeComponentForm.valid) {
      this.formData = this.dischargeComponentForm.value;
      this.formData.dischargeDateAndTime = this.formData.dischargeDateAndTime.getTime();
      this.dialogRef.close(this.formData);
       this.snackBar.open('Discharge request generated for this patient', 'Dismiss', { duration: 3000 });

    }
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
  close() {
    this.dialogRef.close();
  }
}
