import { Component, OnInit, ElementRef, ViewChild, Console } from '@angular/core';
import { FormGroup, FormBuilder, Validators, FormControl } from '@angular/forms';
import { MatDatepicker } from '@angular/material/datepicker';
import { EmrApiService, UtilService, UserService } from '@core/services';

import * as S3 from 'aws-sdk/clients/s3';
import { Subscription, from } from 'rxjs';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { SessionStorageService } from 'ngx-webstorage';
import { UserSession } from '@core/models/user-session';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';
import { DatePipe } from '@angular/common';

import { ToastrService } from 'ngx-toastr';
import { MatSnackBar } from '@angular/material';
import { FileuploadService } from '../../services/fileupload.service';
@Component({
  selector: 'app-file-uploader',
  templateUrl: './file-uploader.component.html',
  styleUrls: ['./file-uploader.component.scss']
})
export class FileUploaderComponent implements OnInit {
  updatepayload
  formGroupinvalid = false;
  uploadTag = 'New ';
  filetypeValidator = false;
  filesizeValidator = false;
  @ViewChild('files') file: ElementRef;
  @ViewChild('files') fileuploading: ElementRef;
  userdata: UserSession;
  formGroup: FormGroup;
  documentsCategory = [];
  fileurl;
  ConvertedDate;
  fileName;
  temp: any = [];
  date;
  toDt;
  editData;
  convdate;
  showFileIcon = true;
  showFilename = false;
  awssubscription: Subscription;
  url;
  isSharable;
  public bucket = new S3(this.emrapiservice.getBucketsConfig());
  patientParamData: any;
  routegranted: boolean;
  constructor(private formbuilder: FormBuilder,
    private emrapiservice: EmrApiService,
    private utilService: UtilService,
    private emropthoservice: EmrOpthoApiService,
    private sessionstorage: UserService,
    private activatedRoute: ActivatedRoute,
    private datePipe: DatePipe,
    private filesuploadservice: FileuploadService,
    private router: Router,
    private toastr: ToastrService,
    private snackbar: MatSnackBar
  ) {
  }
  ngOnInit() {
    this.isSharable = new FormControl(true),
      this.initform();
    this.activatedRoute.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.userdata = this.sessionstorage.getUser();
    if (this.patientParamData.isEdit === 'true') {
      this.isedit()
      this.formGroupinvalid = true;
      this.uploadTag = 'Edit';
    } else {
      const payload = {
        orgId: this.userdata.orgId,
        authenticatedUserId: this.userdata.regId,
        masterType: 'Document Type',
      };
      this.emropthoservice.fetchListOfCommonValues(payload).subscribe((response) => {
        if (response.responseCode === 'E400') {
          this.snackbar.open('Error in Fetching Common Values', 'UNDO', {
            duration: 1500
          });
        } else {
          this.temp = response.data;
          for (let i = 0; i <= this.temp.length - 1; i++) {
            this.documentsCategory = [...this.documentsCategory, ...this.temp[i].attributeCode];
          }
        }
      });
    }
  }
  initform() {
    this.date = new Date();
    this.date = this.datePipe.transform(this.date, 'yyyy-MM-dd');
    this.formGroup = this.formbuilder.group(
      {
        documentName: [null, Validators.required],
        documentType: [null, Validators.required],
        document: [null],
        Date: '',
        doctorName: null,
        providerName: null,
        Datepicker: [this.date]
      }
    );
  }
  isedit() {
    this.editData = this.filesuploadservice.getData();
    this.showFileIcon = false;
    this.showFilename = true;
    this.convdate = new Date(this.editData.documentDt)
    this.fileName = this.editData.documentUrl.split('/')[4]
    this.formGroup.patchValue(
      { documentName: this.editData.documentNm,
        doctorName: this.editData.doctorNm,
        documentType: this.editData.documentType,
        Datepicker: this.convdate,
        providerName: this.editData.providerNm });
    const payload = {
      orgId: this.userdata.orgId,
      authenticatedUserId: this.userdata.regId,
      masterType: 'Document Type',
    };
    this.emropthoservice.fetchListOfCommonValues(payload).subscribe((response) => {
      if (response.responseCode === 'E400') {
        this.snackbar.open('something Went wrong', 'Cancel', {
          duration: 1500
        });
      } else {
        this.temp = response.data;
        for (let i = 0; i <= this.temp.length - 1; i++) {
          this.documentsCategory = [...this.documentsCategory, ...this.temp[i].attributeCode];
        }
      }
    });
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  fileUploader() {
    document.getElementById('file').click();
  }
  /***
   *
   * @description save and route back to file viewer
   */
  SaveandClose() {

    if (this.patientParamData.isEdit === 'true') {
      this.update()
    } else {
      if (!this.file.nativeElement.files[0]) {
        this.formGroupinvalid = true;
      }
      let typeofthefile = this.file.nativeElement.files[0];
      typeofthefile = typeofthefile.name.split('.').pop();
      const bucketName = this.emrapiservice.getBucketName();
      const key = this.formGroup.get('documentName').value + '-' + '.' + typeofthefile;
      const params = {
        Bucket: bucketName.bucketURL,
        Key: key,
        Body: this.file.nativeElement.files[0]
      };
      const awsservice = this.bucket.putObject(params).promise();
      this.awssubscription = from(awsservice).subscribe((data) => {
        this.fileurl = bucketName.url + key;
        this.ConvertedDate = this.formGroup.get('Datepicker').value;
        this.ConvertedDate = this.datePipe.transform(this.ConvertedDate, 'yyyy-MM-dd');
        const payload = {
          orgId: this.userdata.orgId,
          authenticatedUserId: this.userdata.regId,
          specialityId: this.activatedRoute.snapshot.data.specialityId,
          patientId: this.patientParamData.patientId,
          orgGrpId: this.userdata.orgGroupId,
          patientVisitId: this.patientParamData.patientVisitId,
          documentNm: this.formGroup.get('documentName').value,
          providerNm: this.formGroup.get('providerName').value,
          doctorNm: this.formGroup.get('doctorName').value,
          isShareable: this.isSharable.value,
          documentType: this.formGroup.get('documentType').value,
          documentDt: this.ConvertedDate,
          documentUrl: this.fileurl,
          doctorId: this.patientParamData.doctorId,
        };
        this.insertpatientDocuments(payload);
        const navigationextras: NavigationExtras = {
          queryParams: {
            ...this.patientParamData,
            fromUploader: true,
          }
        };
        this.router.navigate(['emr-ophthalmology/emr'], navigationextras);
      });
    }
  }
  /**
   * @description save and continue
   */
  saveandContinue() {
    if (this.patientParamData.isEdit ==='true') {
      this.update();
    } else {
      let typeofthefile = this.file.nativeElement.files[0];
      typeofthefile = typeofthefile.name.split('.').pop();
      const bucketName = this.emrapiservice.getBucketName();
      const key = this.formGroup.get('documentName').value + '-' + '.' + typeofthefile;
      const params = {
        Bucket: bucketName.bucketURL,
        Key: key,
        Body: this.file.nativeElement.files[0]
      };
      const awsservice = this.bucket.putObject(params).promise();
      this.awssubscription = from(awsservice).subscribe((data) => {
        this.fileurl = bucketName.url + key;
        this.ConvertedDate = this.formGroup.get('Datepicker').value;
        this.ConvertedDate = this.datePipe.transform(this.ConvertedDate, 'yyyy-MM-dd');
        const payload = {
          orgId: this.userdata.orgId,
          authenticatedUserId: this.userdata.regId,
          specialityId: this.activatedRoute.snapshot.data.specialityId,
          patientId: this.patientParamData.patientId,
          orgGrpId: this.userdata.orgGroupId,
          patientVisitId: this.patientParamData.patientVisitId,
          documentNm: this.formGroup.get('documentName').value,
          providerNm: this.formGroup.get('providerName').value,
          doctorNm: this.formGroup.get('doctorName').value,
          isShareable: this.isSharable.value,
          documentType: this.formGroup.get('documentType').value,
          documentDt: this.ConvertedDate,
          documentUrl: this.fileurl,
          doctorId: this.patientParamData.doctorId,
        };
        this.insertpatientDocuments(payload);
        this.formGroup.reset();
      });
    }
  }
  insertpatientDocuments(payload) {
    this.emropthoservice.insertPatientUploadedDocuments(payload).subscribe((response) => {
      if (response.responseCode === 'E400') {
        this.snackbar.open('something Went wrong', 'Cancel', {
          duration: 1500
        });
      } else { (response.responseCode = 'E200'); }
      {
        this.snackbar.open('Uploaded successfully', 'OK', {
          duration: 1500
        });
        this.formGroupinvalid = false;
        this.showFileIcon = true;
        this.showFilename = false;
        this.routegranted = true;
      }
    });
  }
  uploadFile() {
    document.getElementById('fileuploading').click();
  }
  getfileName(filename) {
    console.log(this.formGroup.valid)
    this.fileName = this.fileuploading.nativeElement.value;
    this.fileName = this.fileName.replace(/.*[\/\\]/, '');
    if (this.fileName) {
      this.fileName = this.fileName;
      this.showFilename = true;
      this.showFileIcon = false;
    }
    let typeofthefile = this.file.nativeElement.files[0];
    typeofthefile = typeofthefile.type;
    if (typeofthefile === 'image/jpeg' || typeofthefile === 'application/pdf' || typeofthefile == 'image/png') {
      this.formGroup.get('document').setValidators(null);
      this.filetypeValidator = true;
    } else {
      this.snackbar.open('Invalid File Type', 'UNDO', {
        duration: 1500
      });
      this.filetypeValidator = false;
      this.showFilename = false;
      this.showFileIcon = true;
    }
    let sizeoftheFile = this.file.nativeElement.files[0];
    sizeoftheFile = sizeoftheFile.size;
    /**
     * CONVERSION FO BYTES INTO MB
     */
    sizeoftheFile = (sizeoftheFile / 1e+6);
    if (sizeoftheFile > 200) {
      this.snackbar.open('File Limit Size exceeded', 'Cancel', {
        duration: 1500
      });
      this.filesizeValidator = false;
    } else {
      this.filesizeValidator = true;
    }
    if(this.formGroup.valid)
    {
      if ( this.filetypeValidator && this.filesizeValidator) {
        this.formGroupinvalid = true;
      } else {
     this.formGroupinvalid=false;
      }
    }

  }
  backToDocuments() {
    const navigationextras: NavigationExtras = {
      queryParams: {
        ...this.patientParamData,
        fromUploader: true,
      }
    };
    this.router.navigate(['emr-ophthalmology/emr'], navigationextras);
  }
  update() {
 console.log(this.file.nativeElement.files[0])
    let typeofthefile = this.file.nativeElement.files[0];
    if (typeofthefile === undefined) {
      typeofthefile = this.editData.documentUrl;
    } else {
      typeofthefile = typeofthefile.name.split('.').pop();
    }
    const bucketName = this.emrapiservice.getBucketName();
    // if(this.file.nativeElement.files[0] === undefined)
    // {
    //   const key = this.editData.documentUrl.split('/')[4]
    // }
    // else{
    //   const key = this.formGroup.get('documentName').value + 'update' + '-' + '.' + typeofthefile;
    // }
    const key = this.formGroup.get('documentName').value + 'update' + '-' + '.' + typeofthefile;
    const params = {
      Bucket: bucketName.bucketURL,
      Key: key,
      Body: this.file.nativeElement.files[0]
    };
    const awsservice = this.bucket.putObject(params).promise();
    this.awssubscription = from(awsservice).subscribe((data) => {
      this.fileurl = bucketName.url + key;
      this.ConvertedDate = this.formGroup.get('Datepicker').value;
      this.ConvertedDate = this.datePipe.transform(this.ConvertedDate, 'yyyy-MM-dd');

    if(this.file.nativeElement.files[0] === undefined)
    {
     this.updatepayload = {
        orgId: this.userdata.orgId,
        authenticatedUserId: this.userdata.regId,
        specialityId: this.activatedRoute.snapshot.data.specialityId,
        patientId: this.patientParamData.patientId,
        orgGrpId: this.userdata.orgGroupId,
        patientVisitId: this.patientParamData.patientVisitId,
        documentNm: this.formGroup.get('documentName').value,
        providerNm: this.formGroup.get('providerName').value,
        doctorNm: this.formGroup.get('doctorName').value,
        isShareable: this.isSharable.value,
        documentType: this.formGroup.get('documentType').value,
        documentDt: this.ConvertedDate,
        documentUrl: this.editData.documentUrl,
        doctorId: this.patientParamData.doctorId,
        patientDocumentId: this.editData.patientDocumentsId
      };
    }
    else{
      this.updatepayload = {
        orgId: this.userdata.orgId,
        authenticatedUserId: this.userdata.regId,
        specialityId: this.activatedRoute.snapshot.data.specialityId,
        patientId: this.patientParamData.patientId,
        orgGrpId: this.userdata.orgGroupId,
        patientVisitId: this.patientParamData.patientVisitId,
        documentNm: this.formGroup.get('documentName').value,
        providerNm: this.formGroup.get('providerName').value,
        doctorNm: this.formGroup.get('doctorName').value,
        isShareable: this.isSharable.value,
        documentType: this.formGroup.get('documentType').value,
        documentDt: this.ConvertedDate,
        documentUrl: this.fileurl,
        doctorId: this.patientParamData.doctorId,
        patientDocumentId: this.editData.patientDocumentsId
      };
    }
      this.insertpatientDocuments(this.updatepayload);
      const navigationextras: NavigationExtras = {
        queryParams: {
          ...this.patientParamData,
          fromUploader: true,
        }
      };
      this.router.navigate(['emr-ophthalmology/emr'], navigationextras);
    });
  }
  validator()
  {
console.log(this.formGroup.valid)
   if(this.showFilename && this.formGroup.valid )
   {
     this.formGroupinvalid=true;
   } else if(this.showFilename && this.formGroup.invalid)
   {
     this.formGroupinvalid=false;
   }
  }
  radio()
  {
    console.log(this.formGroup.valid)
  }
}
