import { DoctorSpeciality } from '@core/models/user-session';
import {
  EmrOpthoActionTypes,
  EmrOpthoActions
} from '../actions/emr-ophthalmology-actions';

export interface State {
  speciality: DoctorSpeciality;
}

const INITIAL_STATE: State = {
  speciality: null
};

export function reducer(
  state: State = INITIAL_STATE,
  action: EmrOpthoActions
): State {
  switch (action.type) {
    case EmrOpthoActionTypes.SettingsSpecialtySelectorChanged: {
      return { speciality: { ...action.payload } };
    }
    default:
      return state;
  }
}
