  /**
   * @description This Method is used to show and hide the inner conetent in the panels
   * @function viewDetaisl
   */
  viewDetails(type) {
    switch (type) {
      case 'complaints':
        this.displayComplaintsContent = !this.displayComplaintsContent;
        this.displayComponentData = !this.displayComponentData;
        break;
      case 'pastMedical':
        this.displayPastMedicalContent = !this.displayPastMedicalContent;
        break;
      case 'vitals':
        this.displayVitalsContent = !this.displayVitalsContent;
        break;
      case 'systemic':
        this.displaySystemicContent = !this.displaySystemicContent;
        break;
      case 'iop':
        this.displayIopContent = !this.displayIopContent;
        break;
      case 'vision':
        this.displayVisionContent = !this.displayVisionContent;
        break;
      case 'dilated':
        this.displayDilatedContent = !this.displayDilatedContent;
        break;
      case 'undilated':
        this.displayUnDilatedContent = !this.displayUnDilatedContent;
        break;
      case 'autoRef':
        this.displayAutoRefContent = !this.displayAutoRefContent;
        break;
      case 'eye':
        this.displayEyeDiagContent = !this.displayEyeDiagContent;
        break;
      case 'diagnosis':
        this.displayDiagnosisContent = !this.displayDiagnosisContent;
        break;
      case 'glasses':
        this.displayGlassesContent = !this.displayGlassesContent;
        break;
      case 'drug':
        this.displayDrugContent = !this.displayDrugContent;
        break;
      case 'lab':
        this.displayLabContent = !this.displayLabContent;
        break;
      case 'radiology':
        this.displayRadiologyContent = !this.displayRadiologyContent;
        break;
      case 'post':
        this.displayPostOpContent = !this.displayPostOpContent;
        break;
    }
  }
