  //  while complaints change this function will call
  complaintsChange(data, event, index) {
    this.leftComplaintsIndex = index;
    this.type = this.summaryDetailsForm.get('complaintSelect').value;
    this.disableComplaints = this.summaryDetailsForm.get('complaintSelect');
    if (!event.target.checked) {
      this.complaintsList = this.complaintsList.filter(complaint => {
        return data.emrId !== complaint.emrId;
        this.disableComplaints.disable();
      });
      if (data['isMoveFromLeft']) {
        delete data['isMoveFromLeft'];
      }
    } else {
      data['isMoveFromLeft'] = true;
      this.complaintCheckStatus = event.target.checked;
      data.isInsertOrUpdate = this.isInsertOrUpdate;
      this.complaintsList.push(data);
    }
  }
  moveComplaintsComponent() {
    const complaintsArr1 = [...this.complaintsList];
    const type = this.summaryDetailsForm.get('complaintSelect').value;
    this.disableComplaints = this.summaryDetailsForm.get('complaintSelect');
    const key = 'complaints';
    if (this.complaintCheckStatus === true && type !== null) {
      this.disableComplaints.disable();
    }
    this.forComponentMapping(
      type,
      complaintsArr1,
      'Complaints',
      this.complaintCheckStatus
    );
    // this.insertCopyTypeSummaryData(key);
  }
