  // This method is used for the rename the keys of an obj those key names are being displayed as a labels in summary right side.
  renameKeys(obj, newKeys) {
    const keyValues = Object.keys(obj).map(key => {
      const newKey = newKeys[key] || key;
      return { [newKey]: obj[key] };
    });
    return Object.assign({}, ...keyValues);
  }
  systemicExaminationChange(data, event) {
    this.type = this.summaryDetailsForm.get('systemicSelect').value;
    this.disableSystematic = this.summaryDetailsForm.get('systemicSelect');
    const renamedObj = {
      respiratorySystemStatus: 'RESP System',
      gastrointestinalSystemStatus: 'Gastrointestinal System Status',
      cardiovascularSystemStatus: 'Cvs System',
      cranialNervesStatus: 'Cns System',
      localExamStatus: 'Local Examination',
      nutritionalStatus: 'Nutritional Status',
      generalExaminationStatus: 'General Examination',
      genitoUrinarySystemStatus: 'Genito Urinary Systems'
    };
    this.systemicData = this.renameKeys(data, renamedObj);
    this.systemicObj = this.systemicData;
    if (!event.target.checked) {
      delete this.systemicObj;
    } else {
      this.systemicData['isMoveFromLeft'] = true;
      this.isSystemicCheckStatus = event.target.checked;
      this.systemicList = [];
      this.systemicObj['isInsertOrUpdate'] = this.isInsertOrUpdate;
      // this.systemicObj['emrDefaultId'] = data.emrDefaultId
      //   ? data.emrDefaultId
      //   : null;
      this.systemicList.push(this.systemicObj);
    }
  }
