  /**
   * This method is for sending the checkbox value is check or not
   */

  insertCopyTypeSummaryData(key) {
    const payload = {
      authenticatedUserId: this.userData.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userData.orgId,
      orgGrpId: this.userData.orgGroupId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId,
      specialityId: this.specialityId,
      dischargeSummaryId: this.dischargeSummaryId,
      Complaints: key === 'complaints' ? this.complaintsList : null,
      PastMedicalHistory: key === 'pastMedical' ? this.pastMedicalList : null,
      Vitals: key === 'vital' ? this.vitalsList : null,
      SystemicExamination: key === 'systemic' ? this.systemicList : null,
      Iop: key === 'iop' ? this.iopList : null,
      Vision: key === 'vision' ? this.visionList : null,
      DilatedRefraction: key === 'dilated' ? this.refractionDilatedList : null,
      UnDilatedRefraction:
        key === 'unDilated' ? this.refractionUnDilatedList : null,
      Autorefraction: key === 'autoRef' ? this.autoRefractionList : null,
      GlassPrescription:
        key === 'glassPres' ? this.glassPrescriptionList : null,
      Canvas: key === 'canvas' ? this.canvasList : null,
      Diagnosis: key === 'diagnosis' ? this.diagnosisList : null,
      drugsList: key === 'drug' ? this.drugPrescriptionList : null,
      Lab: key === 'lab' ? this.labList : null,
      Imaging: key === 'imaging' ? this.imagingList : null,
      PostOperations: key === 'postOp' ? this.postOperationsList : null
    };
    this.emrApiService.insertCopyTypeSummaryData(payload).subscribe(
      (response: any) => {
        if (response.responseCode === 'E200') {
        } else if (response.responseCode === 'E400') {
          this.snackBar.open(response.responseMessage, 'Dismiss', {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'undo', {
          duration: 3000
        });
      }
    );
  }
