import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { MatDatepicker, MatDatepickerInputEvent } from '@angular/material';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject, of, iif } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { DatePipe } from '@angular/common';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-contact-lens-prescriptions',
  templateUrl: './contact-lens-prescriptions.component.html',
  styleUrls: ['./contact-lens-prescriptions.component.scss']
})
export class ContactLensPrescriptionsComponent
  implements OnInit, IDynamicComponent, OnDestroy {
  left_revisit_dt: any;
  right_revisit_dt: any;

  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: any;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  componentFetchData$: Observable<any>;

  contactLensForm: FormGroup;
  componentDestructionNotifier$ = new Subject();
  readonly CURRENT_USER = this.user.getUser();

  maxDate = new Date();

  LenseTypes = [
    ' Soft ',
    'RGP',
    'Disposable',
    'Extend Wear',
    'Rose-K',
    'Ortho-K',
    'Plastic',
    'Cosmetic',
    'Bi-Focal'
  ];
  patientParamData: any;
  componentData: any;
  message: string;
  constructor(
    private formBuilder: FormBuilder,
    private pipe: DatePipe,
    private readonly dialog: MatDialog,
    private readonly user: UserService,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) { }
  formvalidation = {
    formValidations: [
      {
        type: 'maxlength',
        message: '* Fields data cannot be more than 25 digits'
      },
      { type: 'pattern', message: 'Enter valid data' }
    ]
  };

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initform();
    this.initComponentDataFetchChangeHandler();
    if (this.parentFormGroup) {
      // console.log(this.parentFormGroup);
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.contactLensForm
      );
    }
    this.handleRefresh();
  }

  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {
    //  if (this.componentData) {
      if ( this.patientParamData.pastVisitDetails) {
        this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
      } else {
        
        this.message = 'Do you want to delete Contact Lens Prescription component data?';
      }
    // }
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // if (
    //   payload.data[this.componentId].emr_default_id &&
    //   payload.data[this.componentId].version_id
    // ) {
    //   return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // } else {
    //   this.contactLensForm.reset();
    // }
  }

  /**
   * @description This method is used for deleting the component date when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this.refreshForm$) {

      const predicate = () => {
        const formValue = this.contactLensForm.value;
        const { version_id, child_table } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this.refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this.openDataDeleteConfirmationModal('')),
          concatMap(_ => {
            return iif(
              predicate,
              of(this.makeDeleteRequestObject(this.contactLensForm.value))
                .pipe(
                  concatMap(request => {
                    return this.deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(<ApiResponse<null>>errorResponse.error),
                          of(new ApiResponse<null>(null, 'E400', []))
                        );
                      }));
                  }
                  )
                ),
              of(new ApiResponse<null>(null, 'E200', [])));
          }),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.contactLensForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this.contactLensForm.reset();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this.dataReset.emit(true);
          } else {
            this.snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  initform() {
    const validator = [
      null,
      [Validators.pattern('^[0-9.+-/]*$'), Validators.maxLength(25)]
    ];
    this.contactLensForm = this.formBuilder.group({
      emr_default_id: null,
      version_id: null,
      left_lens_bc: validator,
      left_lens_dia: validator,
      left_lens_sph: validator,
      left_lens_cyl: validator,
      left_lens_axis: validator,
      left_lens_add: validator,
      right_lens_bc: validator,
      right_lens_dia: validator,
      right_lens_sph: validator,
      right_lens_cyl: validator,
      right_lens_axis: validator,
      right_lens_add: validator,
      right_lens_type: null,
      right_lens_color: null,
      left_lens_type: null,
      left_lens_color: null,
      right_lens_advice: null,
      left_lens_advice: null,
      left_revisit_dt: null,
      right_revisit_dt: null
    });

    if (this.disabled) {
      this.contactLensForm.disable();
    } else {
      this.contactLensForm.enable();
    }


  }
  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.contactLensForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
  copyContactLensValues() {

    if (this.disabled) {
      return;
    }

    const rightLensBc = this.contactLensForm.get('right_lens_bc').value;
    const rightLensDia = this.contactLensForm.get('right_lens_dia').value;
    const rightLensSph = this.contactLensForm.get('right_lens_sph').value;
    const rightLensCyl = this.contactLensForm.get('right_lens_cyl').value;
    const rightLensAxis = this.contactLensForm.get('right_lens_axis').value;
    const rightLensAdd = this.contactLensForm.get('right_lens_add').value;
    const rightLensType = this.contactLensForm.get('right_lens_type').value;
    const rightLensColor = this.contactLensForm.get('right_lens_color').value;
    const rightRevisitDt = this.contactLensForm.get('right_revisit_dt').value;
    const rightLensAdvice = this.contactLensForm.get('right_lens_advice').value;
    this.contactLensForm.get('left_lens_bc').patchValue(rightLensBc);
    this.contactLensForm.get('left_lens_dia').patchValue(rightLensDia);
    this.contactLensForm.get('left_lens_sph').patchValue(rightLensSph);
    this.contactLensForm.get('left_lens_cyl').patchValue(rightLensCyl);
    this.contactLensForm.get('left_lens_axis').patchValue(rightLensAxis);
    this.contactLensForm.get('left_lens_add').patchValue(rightLensAdd);
    this.contactLensForm.get('left_lens_type').patchValue(rightLensType);
    this.contactLensForm.get('left_lens_color').patchValue(rightLensColor);
    this.contactLensForm.get('left_revisit_dt').patchValue(rightRevisitDt);
    this.contactLensForm.get('left_lens_advice').patchValue(rightLensAdvice);
  }
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }
  // updateDateTostring(event) {
  //   this.currentDt = this.pipe.transform(new Date(event), 'dd/mm/yyyy');
  //   this.contactLensForm.get('left_revisit_dt').patchValue(this.currentDt);
  //   this.contactLensForm.get('right_revisit_dt').patchValue(this.currentDt);
  //   console.log(this.currentDt);
  // }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
