import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Observable } from 'rxjs';
import { AbstractControl } from '@angular/forms';
import { GlassDetailsService } from '../../services/glass-details.service';

@Component({
  selector: 'app-surgery',
  templateUrl: './surgery.component.html',
  styleUrls: ['./surgery.component.scss']
})
export class SurgeryComponent implements OnInit {
  // @Input()
  // statisComponent$ = Observable as any;
  @Output() navigateToDiagnosis = new EventEmitter<any>();
  @Output() navigateToErx = new EventEmitter<any>();
  @Output() navigateToCpoe = new EventEmitter<any>();
  private readonly SURGERY_TAB = 10;
  private tabNm = 'SURGERY_TAB';
  public readonly PRE_OPERATION = 1;
  public readonly OPERATION = 2;
  public readonly POST_OPERATION = 3;
  public selectedComponent: number;
  public activeLinkName = 'preOperation';
  constructor(
    private readonly surgeryService: GlassDetailsService,

  ) {}

  ngOnInit() {
    this.selectedComponent = this.PRE_OPERATION;
    console.log(this.navigateToDiagnosis);
    // this.statisComponent$.subscribe(data=>{
    //   data.selectedComponent=this.selectedComponent;
    // })
  }
  onchangeButton(selectedValue, linkName) {
    this.selectedComponent = selectedValue;
    this.activeLinkName = linkName;
    // this.opDate = this.surgeryService.getOperationData();

  }
  changeRouteTab(event) {
    this.navigateToDiagnosis.emit(null);
  }
  changeRouteTabErx(event) {
    this.navigateToErx.emit(null);
  }
  changeRouteTabCpoe(event) {
    this.navigateToCpoe.emit(null);
  }
}
