import { Injectable } from '@angular/core';
import { IEmployeeTabDetailsRequest } from './models/employee-tab-details-request';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { IEmployeeTabDetailsResponse } from './models/employee-tab-details-response';
import { IInsertOrUpdateEmployeeTabsAndComponentsMappingRequest } from './models/insert-or-update-employee-tabs-and-component-mapping-request';
import { IFetchTabsPermissionedEmployeeRequest, IFetchTabsPermissionedEmployeeResponse } from './models/fetch-tabs-permissioned-employee-request';
import { IUnMapEmployeeFromTabRequest } from './models/un-map-employees-from-tab.request';

@Injectable({ providedIn: 'root' })
export class AssistantSettingService {

  constructor(private readonly emrOpthoService: EmrOpthoApiService) { }

  fetchEmployeeTabDetails(payload: IEmployeeTabDetailsRequest) {
    return this.emrOpthoService
      .fetchEmployeeTabsDetails<IEmployeeTabDetailsResponse>(payload);
  }

  insertOrUpdateEmployeeTabsAndComponentsMapping(payload: IInsertOrUpdateEmployeeTabsAndComponentsMappingRequest) {
    return this.emrOpthoService
      .insertOrUpdateEmployeeTabsAndComponentsMapping<IInsertOrUpdateEmployeeTabsAndComponentsMappingRequest>(payload);
  }

  fetchTabsPermissionedEmployee(payload: IFetchTabsPermissionedEmployeeRequest) {
    return this.emrOpthoService
      .fetchTabsPermissionedEmployee<IFetchTabsPermissionedEmployeeResponse>(payload);
  }

  unmapEmplyeeFromTabs(payload: IUnMapEmployeeFromTabRequest) {
    return this.emrOpthoService
      .unmapEmplyeeFromTabs<boolean>(payload);
  }

}
