import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { GlassDetailsService } from '@app/modules/emr-ophthalmology/services/glass-details.service';
import { ToastrService } from 'ngx-toastr';
import {
  FormGroupName,
  FormGroup,
  FormBuilder,
  Validators
} from '@angular/forms';
import { IDynamicComponent } from '../../models/dynamic-component';
import { Observable, Subject, of, iif } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-auto-refraction',
  templateUrl: './auto-refraction.component.html',
  styleUrls: ['./auto-refraction.component.scss']
})
export class AutoRefractionComponent
  implements OnInit, IDynamicComponent, OnDestroy {
  sphPlusData: any = [];
  sphMinusData: any = [];
  dilatedPlusAxis: any = [];
  dilatedMinusAxis: any = [];
  dilatedPlusCyl: any = [];
  dilatedMinusCyl: any = [];
  toggleData: any = [];
  type: string;
  isPlusMinusValues = false;
  minusDataValue: any;
  tableType: any;
  autoRefractionArray = [];
  rightRefractionData: any;
  refractionArray = [];
  public auto_Refraction_Form = {
    tableValidation: [
      { type: 'required', message: '* Field Data is required' },
      {
        type: 'maxlength',
        message: '* Fields data cannot be more than 12 characters'
      },
      {
        type: 'pattern',
        message: ' Only Alpha-Numeric,Positive,Negative,Decimal values'
      }
    ]
  };
  public isAccordionOpen = false;
  public lensData = [];
  public temModelName: string;
  dilated_right_sph;
  dilated_right_cyl;
  dilated_right_axis;
  undilated_right_sph1;
  undilated_right_cyl1;
  undilated_right_axis1;
  undilated_right_sph2;
  undilated_right_cyl2;
  undilated_right_axis2;
  undilated_right_sph3;
  undilated_right_cyl3;
  undilated_right_axis3;
  dilated_left_sph;
  dilated_left_cyl;
  dilated_left_axis;
  undilated_left_sph1;
  undilated_left_cyl1;
  undilated_left_axis1;
  undilated_left_sph2;
  undilated_left_cyl2;
  undilated_left_axis2;
  undilated_left_sph3;
  undilated_left_cyl3;
  undilated_left_axis3;

  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: any;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  @Input()
  refreshForm$: Subject<boolean>;
  componentFetchData$: Observable<any>;
  autoRefractionForm: FormGroup;
  componentDestructionNotifier$ = new Subject();
  readonly CURRENT_USER = this.user.getUser();
  patientParamData: any;
  componentData: any;
  message: string;

  constructor(
    private glassesData: GlassDetailsService,
    private fb: FormBuilder,
    private readonly dialog: MatDialog,
    private readonly user: UserService,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.toggleData = 'plus';
    this.sphPlusData = this.glassesData.dilatedPlusSph;
    this.sphMinusData = this.glassesData.dilatedMinusSph;
    this.dilatedPlusCyl = this.glassesData.dilatedPlusCyl;
    this.dilatedMinusCyl = this.glassesData.dilatedMinusCyl;
    this.dilatedPlusAxis = this.glassesData.dilatedPlusAxis;
    this.dilatedMinusAxis = this.glassesData.dilatedMinusAxis;
    this.lensData = this.glassesData.dilatedPlusSph;
    this.autoRefractionArray = this.lensData[0].array;
    this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
      return [...acc, ...val];
    }, []);

    this.initForm();
    this.initComponentDataFetchChangeHandler();
    if (this.parentFormGroup) {
      // console.log(this.parentFormGroup);
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.autoRefractionForm
      );
    }
    this.handleRefresh();
  }

  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {
   
      
   if ( this.patientParamData.pastVisitDetails === 'true') {
    this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
   
 } else {
  this.message = 'Do you want delete Auto Refraction component data?';
 }

    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // if (
    //   payload.data[this.componentId].emr_default_id &&
    //   payload.data[this.componentId].version_id
    // ) {
    //   return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // } else {
    //   this.autoRefractionForm.reset();
    // }
  }

  /**
   * @description This method is used for deleting the component date when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this.refreshForm$) {

      const predicate = () => {
        const formValue = this.autoRefractionForm.value;
        const { version_id, child_table } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this.refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this.openDataDeleteConfirmationModal('')),
          concatMap(_ => {
            return iif(
              predicate,
              of(this.makeDeleteRequestObject(this.autoRefractionForm.value))
                .pipe(
                  concatMap(request => {
                    return this.deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(<ApiResponse<null>>errorResponse.error),
                          of(new ApiResponse<null>(null, 'E400', []))
                        );
                      }));
                  }
                  )
                ),
              of(new ApiResponse<null>(null, 'E200', [])));
          }),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.autoRefractionForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this.autoRefractionForm.reset();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this.dataReset.emit(true);
          } else {
            this.snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  initForm() {
    const validator = [
      null,
      [Validators.pattern(/^[-PpnN0-9.+-/']*$/), Validators.maxLength(12)]
    ];
    this.autoRefractionForm = this.fb.group({
      emr_default_id: null,
      version_id: null,
      dilated_right_sph: validator,
      dilated_right_cyl: validator,
      dilated_right_axis: validator,
      undilated_right_sph1: validator,
      undilated_right_cyl1: validator,
      undilated_right_axis1: validator,
      undilated_right_sph2: validator,
      undilated_right_cyl2: validator,
      undilated_right_axis2: validator,
      undilated_right_sph3: validator,
      undilated_right_cyl3: validator,
      undilated_right_axis3: validator,
      dilated_left_sph: validator,
      dilated_left_cyl: validator,
      dilated_left_axis: validator,
      undilated_left_sph1: validator,
      undilated_left_cyl1: validator,
      undilated_left_axis1: validator,
      undilated_left_sph2: validator,
      undilated_left_cyl2: validator,
      undilated_left_axis2: validator,
      undilated_left_sph3: validator,
      undilated_left_cyl3: validator,
      undilated_left_axis3: validator,
      // auto_selectControl:'auto-plus-btn'
    });

    if (this.disabled) {
      this.autoRefractionForm.disable();
    } else {
      this.autoRefractionForm.enable();
    }

  }

  /**
   * @description This method is used to open accordion and getting the data from table
   */
  onClickLensFields(modelName, type) {

    if (this.disabled) {
      return;
    }

    this.isAccordionOpen = true;
    this.temModelName = modelName;

    if (type === 'sph') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.lensData = this.glassesData.dilatedMinusSph;
        this.tableType = type;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
        // console.log(this.refractionArray);

      } else {
        this.minusDataValue = 'plus';
        this.lensData = this.glassesData.dilatedPlusSph;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
        this.tableType = type;
      }
    }
    if (type === 'cyl') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.lensData = this.glassesData.dilatedMinusCyl;
        this.tableType = type;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      } else {
        this.minusDataValue = 'plus';
        this.lensData = this.glassesData.dilatedPlusCyl;
        this.tableType = type;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (type === 'axis') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.lensData = this.glassesData.dilatedMinusAxis;
        this.tableType = type;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      } else {
        this.minusDataValue = 'plus';
        this.lensData = this.glassesData.dilatedPlusAxis;
        this.tableType = type;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to open  and close accordion
   */
  onClickDownButton() {
    if (this.disabled) {
      return;
    }
    this.isAccordionOpen = !this.isAccordionOpen;
    this.autoRefractionArray = this.glassesData.dilatedPlusSph[0].array;

  }
  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.autoRefractionForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
  /**
   * @description This method is used to plus table data values
   */
  onClickPlus(plusvalue) {
    this.minusDataValue = plusvalue;
    // this.isSymbolPlus = true;
    if (this.tableType === 'sph') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.dilatedPlusSph;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
        // console.log(this.refractionArray);
      }
    }
    if (this.tableType === 'cyl') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.dilatedPlusCyl;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'axis') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.dilatedPlusAxis;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to minus table data values
   */
  // function for  start from here
  onClickMinus(minusvalue) {
    // this.isSymbolMinus = true;
    this.minusDataValue = minusvalue;
    if (this.tableType === 'sph') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.dilatedMinusSph;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'cyl') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.dilatedMinusCyl;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'axis') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.lensData = this.glassesData.dilatedMinusAxis;
        this.autoRefractionArray = this.lensData[0].array;
        this.refractionArray = this.autoRefractionArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to get table data values
   */
  onClickLensData(value) {
    this[this.temModelName] = value;
    this.autoRefractionForm.patchValue({ [this.temModelName]: value });
    // this.rightAutoRefractionForm.patchValue({ [this.temModelName]: value });
  }

  /**
   * @description This method is used to copy from left lens data to right
   */
  refractionFormCopy() {

    if (this.disabled) {
      return;
    }

    // this.rightRefractionData = this.leftAutoRefractionForm.controls;
    const dilatedRightSph = this.autoRefractionForm.get('dilated_right_sph')
      .value;
    const dilatedRightCyl = this.autoRefractionForm.get('dilated_right_cyl')
      .value;
    const dilatedRightAxis = this.autoRefractionForm.get('dilated_right_axis')
      .value;
    const undilatedRightSph1 = this.autoRefractionForm.get(
      'undilated_right_sph1'
    ).value;
    const undilatedRightCyl1 = this.autoRefractionForm.get(
      'undilated_right_cyl1'
    ).value;
    const undialtedRightAxis1 = this.autoRefractionForm.get(
      'undilated_right_axis1'
    ).value;
    const undilatedRightSph2 = this.autoRefractionForm.get(
      'undilated_right_sph2'
    ).value;
    const undilatedRightCyl2 = this.autoRefractionForm.get(
      'undilated_right_cyl2'
    ).value;
    const undialtedRightAxis2 = this.autoRefractionForm.get(
      'undilated_right_axis2'
    ).value;
    const undilatedRightSph3 = this.autoRefractionForm.get(
      'undilated_right_sph3'
    ).value;
    const undilatedRightCyl3 = this.autoRefractionForm.get(
      'undilated_right_cyl3'
    ).value;
    const undialtedRightAxis3 = this.autoRefractionForm.get(
      'undilated_right_axis3'
    ).value;
    this.autoRefractionForm.get('dilated_left_sph').patchValue(dilatedRightSph);
    this.autoRefractionForm.get('dilated_left_cyl').patchValue(dilatedRightCyl);
    this.autoRefractionForm
      .get('dilated_left_axis')
      .patchValue(dilatedRightAxis);
    this.autoRefractionForm
      .get('undilated_left_sph1')
      .patchValue(undilatedRightSph1);
    this.autoRefractionForm
      .get('undilated_left_cyl1')
      .patchValue(undilatedRightCyl1);
    this.autoRefractionForm
      .get('undilated_left_axis1')
      .patchValue(undialtedRightAxis1);
    this.autoRefractionForm
      .get('undilated_left_sph2')
      .patchValue(undilatedRightSph2);
    this.autoRefractionForm
      .get('undilated_left_cyl2')
      .patchValue(undilatedRightCyl2);
    this.autoRefractionForm
      .get('undilated_left_axis2')
      .patchValue(undialtedRightAxis2);
    this.autoRefractionForm
      .get('undilated_left_sph3')
      .patchValue(undilatedRightSph3);
    this.autoRefractionForm
      .get('undilated_left_cyl3')
      .patchValue(undilatedRightCyl3);
    this.autoRefractionForm
      .get('undilated_left_axis3')
      .patchValue(undialtedRightAxis3);
  }
  /**
   * @description This method is used to refresh the data in the table.
   */
  refreshData() {
    // this.rightAutoRefractionForm.reset(this.finalGlassesrightLensForm.value);
  }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
