import {
  Component,
  OnInit,
  OnDestroy,
  ViewChild,
  ElementRef
} from '@angular/core';
import { AbstractControl, FormBuilder } from '@angular/forms';
import { IFetchDepartmentsRequest } from './models/fetch-departments-request';
import { UserService } from '@core/services';
import { UserSession, DoctorSpeciality } from '@core/models/user-session';
import { AssistantListService } from './assistants-list.service';
import { Subject, Observable, of, iif } from 'rxjs';
import {
  takeUntil,
  tap,
  filter,
  catchError,
  finalize
} from 'rxjs/operators';
import { IFetchDepartmentsResponse } from './models/fetch-departments-response';
import { ToastrService } from 'ngx-toastr';
import { IFetchEmployeesEmrRequest } from './models/fetch-employees-emr-request';
import { Store, select } from '@ngrx/store';
import {
  IEmrOpthoState,
  getCurrentSelectedSpeciality,
  getAssistantsData
} from '../../reducers';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { IFetchEmployeesEmrResponse } from './models/fetch-employees-emr-response';
import {
  FetchAssistantsList
} from '../../actions/emr-ophthalmology-actions';

import * as fromCurrentSelectedSpecialityReducer from '../../reducers/speciality-value-change.reducer';
import * as fromAssistantsReducer from '../../reducers/assistant-list.reducer';
import { Router, NavigationExtras} from '@angular/router';

@Component({
  selector: 'app-assistants-list',
  templateUrl: './assistants-list.component.html',
  styleUrls: ['./assistants-list.component.scss']
})
export class AssistantsListComponent implements OnInit, OnDestroy {
  assistantList: Array<IFetchEmployeesEmrResponse>;
  departmentSelector: AbstractControl;
  user: UserSession = this.userService.getUser();

  departmentsList: Array<IFetchDepartmentsResponse>;

  componentDestructionNotifier$ = new Subject();

  currentSelectedSpeciality$: Observable<
    fromCurrentSelectedSpecialityReducer.State
  > = this.store.pipe(select(getCurrentSelectedSpeciality));

  assistantsData$: Observable<fromAssistantsReducer.State> = this.store.pipe(
    select(getAssistantsData)
  );

  currentSelectedSpeciality: DoctorSpeciality;

  readonly ASSISTANT_ITEM_HEIGHT = 73; // height of each assistant element

  isLoadingAssistants = false;

  ghostElemnts: Array<number>;

  @ViewChild('assistantListElement')
  assistantListElement: ElementRef<HTMLDivElement>;

  constructor(
    private readonly fb: FormBuilder,
    private readonly store: Store<IEmrOpthoState>,
    private readonly toastr: ToastrService,
    private readonly userService: UserService,
    private readonly assistantService: AssistantListService,
    private router: Router

  ) {}

  assistantTrackingFn(_, item: IFetchEmployeesEmrResponse) {
    return item.empId;
  }

  createGhotElements() {
    const assistantElementsList = this.assistantListElement.nativeElement;
    const approxItemsCanFitIn = Math.round(
      assistantElementsList.offsetHeight / this.ASSISTANT_ITEM_HEIGHT
    );
    this.ghostElemnts = Array.from(Array(approxItemsCanFitIn)).map(
      (_, index) => {
        return 1 - (index * 1) / approxItemsCanFitIn;
      }
    );
  }

  departmentValueComparator(
    departmentFromTheList: IFetchDepartmentsResponse,
    departmentToBind: IFetchDepartmentsResponse
  ) {
    return departmentFromTheList && departmentToBind
      ? departmentFromTheList.deptId === departmentToBind.deptId
      : departmentFromTheList === departmentToBind;
  }

  fetchDepartments() {
    const departmentsErrorToastr = () =>
      this.toastr.error('Unable to fetch Departments list', 'Error');

    const payload: IFetchDepartmentsRequest = {
      authenticatedUserId: `${this.user.regId}`,
      orgGrpId: `${this.user.orgGroupId}`,
      orgId: `${this.user.orgId}`
    };

    this.assistantService
      .fetchDepartments(payload)
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(response => {
        if (response.responseCode === 'E200') {
          if (this.user.doctorSpecialityList.length) {
          this.departmentsList = response.data;
          // this .router.navigate(['/emr-ophthalmology/settings/tab/9']);
          this.initControls();
          this.initDepartmentValueChangeHandler();
          }
        } else {
          departmentsErrorToastr();
        }
      }, departmentsErrorToastr);
  }

  getAssistantPlaceholderImage(assistant: IFetchEmployeesEmrResponse) {
    if (assistant.gender === 'female') {
    const basePath = 'https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons';
    return `${basePath}/female-ico.png`;
    } else {
      const basePath = 'https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons';
      return `${basePath}/male-ico.png`;
    }
    // const basePath = 'assets/images';
    // return `${basePath}/emr-${assistant.gender || 'male'}.svg`;
  }

  fetchEmployeesHavingAccessOnEmr(deptId: number, specialityId: string) {
    const payload: IFetchEmployeesEmrRequest = {
      authenticatedUserId: `${this.user.regId}`,
      deptId,
      orgId: `${this.user.orgId}`,
      specialityId
    };
    return this.assistantService.fetchEmployeesHavingAccessOnEmr(payload).pipe(
      finalize(() => (this.isLoadingAssistants = false)),
      catchError((errorResponse: HttpErrorResponse) => {
        return iif(
          () => errorResponse.status > 0,
          of(<ApiResponse<null>>errorResponse.error),
          of(new ApiResponse<null>(null, 'E400', []))
        );
      })
    );
  }
  initControls() {
    if (this.departmentsList && this.departmentsList.length) {
      this.departmentSelector = this.fb.control(this.departmentsList ? this.departmentsList[0] : null);
    } else {
      this.departmentSelector = this.fb.control(null);
    }
  }

  initSpecialityValueChangeHandler() {
    this.currentSelectedSpeciality$
      .pipe(
        tap(({ speciality }) => {
          if (!speciality) {
            this.departmentSelector.disable();
          } else {
            this.departmentSelector.enable();
          }
        }),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(({ speciality }) => {
        this.currentSelectedSpeciality = speciality;
      });
  }

  initDepartmentValueChangeHandler() {
    this.departmentSelector.valueChanges
      .pipe(
        filter(value => value),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe((department: IFetchDepartmentsResponse) => {
        const payload: IFetchEmployeesEmrRequest = {
          authenticatedUserId: `${this.user.regId}`,
          deptId: department.deptId,
          orgId: `${this.user.orgId}`,
          specialityId: `${this.currentSelectedSpeciality.specialityId}`
        };
        this.store.dispatch(new FetchAssistantsList(payload));
      });
  }

  initAssistantDataChangeHandler() {
    this.assistantsData$
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(data => {
        if (data.isLoading) {
          this.createGhotElements();
        } else {
          this.ghostElemnts = [];
        }
        this.assistantList = data.assistantList;
        // this .router.navigate(['/emr-ophthalmology/settings/tab/9']);
        this.isLoadingAssistants = data.isLoading;
        if (data.errorOccurred) {
          this.toastr.error('Unable to fetch Assistants', 'Error');
        }
      });
  }

  ngOnInit() {
    this.fetchDepartments();
    this.initControls();
    this.initSpecialityValueChangeHandler();
    this.initDepartmentValueChangeHandler();
    this.initAssistantDataChangeHandler();
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }

}
