export class ApiValidationsMessage {
  errorCode: string;
  message: string;
  constructor(errorCode: string, message: string) {
    this.errorCode = errorCode;
    this.message = message;
  }
}

export class ApiResponse<T> {
  data: T;
  responseCode: string;
  responseMessage: string;
  validations: Array<ApiValidationsMessage>;

  constructor(data: T, responseCode: string, validatons: Array<ApiValidationsMessage>) {
    this.data = data;
    this.responseCode = responseCode;
    this.validations = validatons;
  }
}
