import {
  EmrOpthoActionTypes,
  EmrOpthoActions
} from '../actions/emr-ophthalmology-actions';
import { IFetchDynamicTabsResponse } from '../components/tabs-list/models/fetch-dynamic-tabs-response';

export interface State {
  isLoading: boolean;
  errorOccurred: boolean;
  tabsList: Array<IFetchDynamicTabsResponse>;
}

export const INITIAL_STATE: State = {
  tabsList: [],
  errorOccurred: false,
  isLoading: false
};

function copyTabs(tabsList: Array<IFetchDynamicTabsResponse>) {
  return tabsList.map(tab => ({...tab}));
}


function updateTabName(tabsList: Array<IFetchDynamicTabsResponse>, tabId: number, tabName: string): Array<IFetchDynamicTabsResponse> {
  const newTabList = copyTabs(tabsList);
  const currentTab = newTabList.find(tab => tab.tabId === tabId);
  currentTab.tabNm = tabName;
  return newTabList;
}

export function reducer(
  state: State = INITIAL_STATE,
  action: EmrOpthoActions
): State {
  switch (action.type) {
    case EmrOpthoActionTypes.FetchTabList: {
      return {
        tabsList: [],
        isLoading: true,
        errorOccurred: false,
      };
    }
    case EmrOpthoActionTypes.FetchTabListError: {
      return {
        tabsList: [],
        isLoading: false,
        errorOccurred: true,
      };
    }
    case EmrOpthoActionTypes.FetchTabListSuccess: {
      return {
        tabsList: copyTabs(action.payload),
        isLoading: false,
        errorOccurred: false,
      };
    }
    case EmrOpthoActionTypes.UpdateDynamicTabName: {
      return {
        tabsList: updateTabName(state.tabsList, action.payload.tabId, action.payload.tabNm),
        isLoading: state.isLoading,
        errorOccurred: state.errorOccurred
      };
    }
    default:
      return state;
  }
}
