import {
  ActionReducerMap,
  createFeatureSelector,
  createSelector
} from '@ngrx/store';

import * as fromSpecialySelectorChangedReducer from './speciality-value-change.reducer';
import * as fromTabsListDataReducer from './tabs-list.reducer';
import * as fromAssistantsListReducer from './assistant-list.reducer';
import * as fromLandingPageReducer from './patient-visit-details.reducer';
import * as fromSideNav from './sideNav.reducer';


export interface IEmrOpthoState {
  currentSelectedSpeciality: fromSpecialySelectorChangedReducer.State;
  tabsListData: fromTabsListDataReducer.State;
  assistantsListData: fromAssistantsListReducer.State;
  landingPageCurrentPatientVisitDetails: fromLandingPageReducer.State;
  sideNavDetails: fromSideNav.State;
}

export const reducers: ActionReducerMap<IEmrOpthoState> = {
  currentSelectedSpeciality: fromSpecialySelectorChangedReducer.reducer,
  landingPageCurrentPatientVisitDetails: fromLandingPageReducer.reducer,
  tabsListData: fromTabsListDataReducer.reducer,
  assistantsListData: fromAssistantsListReducer.reducer,
  sideNavDetails: fromSideNav.reducer
};

export const getEmrOpthoState = createFeatureSelector<IEmrOpthoState>('EMR_OPTHO');

export const getCurrentSelectedSpeciality = createSelector(getEmrOpthoState, state => state.currentSelectedSpeciality);

export const getTabsData = createSelector(getEmrOpthoState, state => state.tabsListData);

export const getAssistantsData = createSelector(getEmrOpthoState, state => state.assistantsListData);

export const getLandingPageCurrentSelectedAssistantDetails = createSelector(getEmrOpthoState, state => state.landingPageCurrentPatientVisitDetails);

export const getSideNavData = createSelector (getEmrOpthoState, state => state.sideNavDetails);

