import {
  Component,
  ViewChild,
  ElementRef,
  ExistingProvider,
  forwardRef,
  OnInit,
  ViewEncapsulation,
  OnDestroy, Input
} from '@angular/core';
import {
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
  FormControl
} from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

export const EMR_OPTHO_CHECKBOX_INPUT_CONTROL_VALUE_ACCESSOR: ExistingProvider = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => EmrOpthoCheckBoxComponent),
  multi: true
};

@Component({
  selector: 'app-emr-optho-checkbox',
  template: `
    <label class="checkcontainer " [class.checkcontainer--checked]="checkBoxState" [ngClass] = "{'cursor-notallowed':isDisabbled}">
      <ng-content></ng-content>
      <input  type="checkbox" class="checkcontainer__checkbox" [formControl]="control" (change)="changeListener()"/>
    </label>
  `,
  styleUrls: ['./checkbox.component.scss'],
  providers: [EMR_OPTHO_CHECKBOX_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class EmrOpthoCheckBoxComponent implements ControlValueAccessor, OnInit, OnDestroy {
  @ViewChild('checkbox') checkboxElement: ElementRef;
  _onChange: (_: any) => void;
  _onTouched: (_: any) => void;

  @Input() isDisabbled;
  control: FormControl;

  checkBoxState;

  componentDestructionNotifier$ = new Subject();

  changeListener() {
    this._onChange(this.control.value);
  }

  ngOnInit() {
    this.control = new FormControl(false);

    this.control
      .valueChanges
      .pipe(
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe(val => {
        this.checkBoxState = val;
      });
  }

  registerOnChange(fn) {
    this._onChange = fn;
  }

  registerOnTouched(fn) {
    this._onTouched = fn;
  }

  setDisabledState(isDisabled: boolean) {
    if (isDisabled) {
      this.control.disable();
    } else {
      this.control.enable();
    }
  }

  writeValue(value) {
    if (typeof(value) === 'string') {
      value = value === 'true' ? true : false;
    }
    this.control.setValue(value);
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
}
