import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { TemperatureAddEditComponent } from '../temperature-add-edit-dialog/temperature-add-edit-dialog.component';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-blood-pressure-add-edit-dialog',
  templateUrl: './blood-pressure-add-edit-dialog.component.html',
  styleUrls: ['./blood-pressure-add-edit-dialog.component.scss']
})

export class AddBloodPressureAddEditDialogComponent implements OnInit , OnDestroy {
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();

  BptableData: FormGroup;

  maxDate = new Date();

  isEdit = false;
  formValue: any;

  constructor(
    private readonly fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef<AddBloodPressureAddEditDialogComponent>,
    private datePipe: DatePipe,
  ) {
    this.isEdit = injectedData.edit;
  }
  // getTime() {
  //   return timer(0, 1000).pipe(
  //     map(_ => new Date()),
  //     takeUntil(
  //       this.componentDestructionNotifier$
  //     )
  //   );
  // }
  ngOnInit() {
    const { required, pattern } = Validators;
    this.BptableData =  this.fb.group({
      version_id: null,
      emr_default_id: null,
      blood_pressure_capture_dttm: [new Date(), Validators.required],
      blood_pressure_systolic: [null, [required, pattern(/^\d+$/)]],
      blood_pressure_dystolic: [null, [required, pattern(/^\d+$/)]],
      blood_pressure_score: [null, [required, pattern(/^\d+$/)]]
    });
    this.BptableData.get('blood_pressure_score').disable();

    this.BptableData.get('blood_pressure_systolic').valueChanges.subscribe(data => {
      if (data <= 90) {
        this.BptableData.get('blood_pressure_score').patchValue('3');
        } else if (91 <= data && data <= 100) {
        this.BptableData.get('blood_pressure_score').patchValue('2');
        } else if (101 <= data && data <= 110) {
        this.BptableData.get('blood_pressure_score').patchValue('1');
        } else if (111 <= data && data <= 219) {
        this.BptableData.get('blood_pressure_score').patchValue('0');
        } else if (data >= 220) {
        this.BptableData.get('BptableData').patchValue('3');
        }
    });

    // if (this .isEdit) {
    //   const injectedData = this .injectedData.rowData;
    //   injectedData.blood_pressure_capture_dttm = new Date(+injectedData.blood_pressure_capture_dttm);
    //   this .injectedData.patchValue(injectedData);
    // }
    if (this .isEdit) {
      const injectedData = this .injectedData.rowData;
      injectedData.blood_pressure_capture_dttm = new Date(+injectedData.blood_pressure_capture_dttm);
      this .BptableData.patchValue(injectedData);
    }
    // this.getTime()
    //    .pipe(
    //      takeUntil(this.componentDestructionNotifier$)
    //    )
    //   .subscribe(date => {
    //     this.injectedData.patchValue({
    //       blood_pressure_capture_dttm : date
    //     });
    // });
   }

  add() {
    if (this.BptableData.valid) {
      this.formValue = this.BptableData.value;
      this.formValue['blood_pressure_score'] = this.BptableData.get('blood_pressure_score').value;
      this.formValue.blood_pressure_capture_dttm = this.formValue.blood_pressure_capture_dttm.getTime();
      this.dialogRef.close(this.formValue);
    }
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }

  close() {
    this.dialogRef.close();
  }
}
