import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class FileuploadService {
documentData
  constructor() { }
  setData(data)
  {
    // return this.documentData=new Observable()
    this.documentData=data;
  }
  getData()
  {
    return this.documentData;
  }
}
