import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import {
  trigger,
  transition,
  state,
  animate,
  style,
  AnimationEvent
} from '@angular/animations';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { IDynamicComponent } from '@app/modules/emr-ophthalmology/models/dynamic-component';
@Component({
  selector: 'app-anteriaor',
  templateUrl: './anteriaor.component.html',
  styleUrls: ['./anteriaor.component.scss']
})
export class AnteriaorComponent implements OnInit {

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  anteriorsectionForm: FormGroup;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.anteriorsectionForm = this.form;
    if (this.disabled) {
      this.anteriorsectionForm.disable();
    } else {
      this.anteriorsectionForm.enable();
    }
  }

}
