import { Component, OnInit, Input } from '@angular/core';
import { Router, NavigationExtras, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-cpoe-header',
  templateUrl: './cpoe-header.component.html',
  styleUrls: ['./cpoe-header.component.scss']
})
export class CpoeHeaderComponent implements OnInit {
  public componentList = [];
  constructor(private router: Router) {}
  public selectedComponentId = '1';
@Input()
statisComponent$ = Observable;
  ngOnInit() {
    this.componentList = [
      {
        name: 'laboratory',
        label: 'LABORATORY',
        id: 'lab',
        componentId: 1
      },
      {
        name: 'radiology',
        label: 'RADIOLOGY',
        id: 'radiology',
        componentId: 2
      }
    ];
  }
  selectedValue(selectedComponent) {
    this.selectedComponentId = selectedComponent;
  }
}
