import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-emr-settings-empty',
  templateUrl: 'settings-empty.component.html',
  styleUrls: ['./settings-empty.component.scss']
})

export class SettingsEmptyComponent implements OnInit {
  assistantParamData: any;
  constructor(private route: ActivatedRoute) { }
ngOnInit() {
  this.route.queryParams.subscribe(res => {
    this.assistantParamData = res;
    console.log(this.assistantParamData);
  });
  }
}
