import {
  Component,
  InjectionToken,
  Inject,
  OnInit,
  ExistingProvider,
  forwardRef,
  OnDestroy
} from '@angular/core';
import {
  ControlValueAccessor,
  FormGroup,
  FormBuilder,
  NG_VALUE_ACCESSOR,
  FormArray
} from '@angular/forms';
import { Subject } from 'rxjs';
import { takeUntil, debounceTime } from 'rxjs/operators';

export interface IOptionControlModel {
  option: string;
  selected: boolean;
}

export const COMPLAINTS_OPTION_COMPONENT_ACCESSOR: ExistingProvider = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => ComplaintsOptionComponent),
  multi: true
};

@Component({
  templateUrl: './options.component.html',
  selector: 'app-complaints-option',
  styleUrls: ['./options.component.scss'],
  providers: [COMPLAINTS_OPTION_COMPONENT_ACCESSOR]
})
export class ComplaintsOptionComponent
  implements ControlValueAccessor, OnInit, OnDestroy {
  onChange;
  onTouchedChange;

  componentDestructionNotifier$ = new Subject();

  optionsForm: FormGroup;

  constructor(
    private readonly fb: FormBuilder
  ) {}

  initOptionsForm() {
    this.optionsForm = this.fb.group({
      options: this.fb.array([])
    });
  }

  get optionsFormArrayControls() {
    if (this.optionsForm) {
      const optionsFormArray = <FormArray>this.optionsForm.get('options');
      return optionsFormArray.controls;
    } else {
      return [];
    }
  }

  optionsValueChangeListener() {
    const optionsFormArray = <FormArray>this.optionsForm.get('options');
    optionsFormArray.valueChanges
      .pipe(
        debounceTime(200),
        takeUntil(this.componentDestructionNotifier$)
      )
      .subscribe((options: Array<IOptionControlModel>) => {
        if (this.onChange) {
          this.onChange(options ? JSON.stringify(options) : null);
        }
      });
  }

  ngOnInit() {
    this.initOptionsForm();
    this.optionsValueChangeListener();
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }

  registerOnChange(fn) {
    this.onChange = fn;
  }

  registerOnTouched(fn) {
    this.onTouchedChange = fn;
  }

  writeValue(obj: string) {
    const optionsArray = JSON.parse(obj);
    const optionsFormArray = <FormArray>this.optionsForm.get('options');
    if (Array.isArray(optionsArray)) {
      while (optionsFormArray.length !== 0) {
        optionsFormArray.removeAt(optionsFormArray.length);
      }
      optionsArray.forEach((option) => {
        optionsFormArray.push(this.fb.group(option));
      });
    } else if (obj === null) {
      optionsFormArray.controls.forEach((control)  => {
        control.get('selected').reset();
      });
    }
  }
}
