import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Observable, Subject } from 'rxjs';
import { KeyValue } from '@angular/common';
import { GlassDetailsService } from '@app/modules/emr-ophthalmology/services/glass-details.service';
import { UserService } from '@core/services';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { UserSession } from '@core/models/user-session';
import { MatSnackBar, MatDialog } from '@angular/material';
import { takeUntil } from 'rxjs/operators';
import { select, Store } from '@ngrx/store';
import { IEmrOpthoState, getLandingPageCurrentSelectedAssistantDetails } from '@app/modules/emr-ophthalmology/reducers';
import * as fromLandingPagePatientVisitDetails from '../../../../reducers/patient-visit-details.reducer';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-summary-common-component',
  templateUrl: './summary-common-component.component.html',
  styleUrls: ['./summary-common-component.component.scss']
})
export class SummaryCommonComponentComponent implements OnInit {

  @Input()
  commonData = Observable as any;
  @Input()
  patientDemographicData: any;
  @Input()
  specialityId: number;
  patientParamData: any;
  userData: UserSession = this.user.getUser();
  currentPatientVisitDetails$: Observable<
  fromLandingPagePatientVisitDetails.State
> = this.store.pipe(select(getLandingPageCurrentSelectedAssistantDetails));
componentDestructionNotifier$ = new Subject();
  @Input()
  tabId: number;
  public complaints = {};
  public medicalHistory = {};
  public occularDatails = {};
  public finalDiagnosis = {};
  public postOperation = {};
  public glassesPrescription = {};
  public drugPrescription = {};
  public tests = {};
  public Complaints = [];
  deleteObj: any;
  res: any;
  isfinalized: any;
  dialogRef: any;

  constructor(
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackBar: MatSnackBar,
    private emrApiService: EmrOpthoApiService,
    private readonly user: UserService,
    private serviceFile: GlassDetailsService,
    private readonly store: Store<IEmrOpthoState>,
    public dialog: MatDialog,
    private readonly route: ActivatedRoute
  ) { }

  ngOnInit() {
    this.getPatientVisitDetails();
    this.serviceFile.data.subscribe(val => {
      this.isfinalized = val;
    });
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
  }

    // this is used for ordering the values based on id
  // by default keyValue pipe orders the keys in ascending order
  indexOrderAsc = (akv: KeyValue<string, any>, bkv: KeyValue<string, any>): number => {
    const a = (akv && akv.value) ? akv.value.index : null ;
    const b = (bkv && bkv.value) ? bkv.value.index : null;
    return a > b ? 1 : (b > a ? -1 : 0);
}
  // getting patient visit details from the store
  getPatientVisitDetails() {
    this.currentPatientVisitDetails$
      .pipe(takeUntil(this.componentDestructionNotifier$))
      .subscribe(data => {
        if (data.patientVisitDetails) {
          this.patientDemographicData = data.patientVisitDetails;
        }
      });
  }
  deleteComponentData(data) {

    console.log(data);
   if (this.patientParamData.pastVisitDetails === 'true' && data.value[0].emrDefaultId) {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`
        },
        disableClose: true
      });
    } else {
      this.dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: 'Do You Want Delete The Data?'
        },
        disableClose: true
      });
    this.dialogRef.afterClosed().subscribe(result => {
      if (result === true) {
        const payload = {
          authenticatedUserId: this.userData.regId,
          doctorId: this.patientDemographicData.doctorId,
          orgId: this.userData.orgId,
          orgGrpId: this.userData.orgGroupId,
          patientVisitId: this.patientDemographicData.patientVisitId,
          patientId: this.patientDemographicData.patientId,
          specialityId: this.specialityId,
          complaintsList: data['key'] === 'Complaints' ? data.value : null,
          pastMedicalHistoryList: data['key'] === 'PastMedicalHistory' ? data.value : null,
          vitalsList: data['key'] === 'Vitals' ? data.value : null,
          systamaticExaminationList: data['key'] === 'SystemicExamination' ? data.value : null,
          iopList: data['key'] === 'Iop' ? data.value : null,
          visionList: data['key'] === 'Vision' ? data.value : null,
          dialitedList: data['key'] === 'DilatedRefraction' ? data.value : null,
          unDialitedList: data['key'] === 'UnDilatedRefraction' ? data.value : null,
          autoRefractionList: data['key'] === 'Autorefraction' ? data.value : null,
          canvasList: data['key'] === 'Canvas' ? data.value : null,
          glassesPrescriptionList: data['key'] === 'GlassPrescription' ? data.value : null,
          diagnosisList: data['key'] === 'Diagnosis' ? data.value : null,
          drugList: data['key'] === 'DrugPrescription' ? data.value : null,
          labList: data['key'] === 'Lab' ? data.value : null,
          imagingList: data['key'] === 'Imaging' ? data.value : null,
          postOperationList: data['key'] === 'PostOperations' ? data.value : null,
          // Lab: data['key'] === 'Lab' ? data : null,
        };
        this.emrApiService.deleteSummaryData(payload).subscribe(
          (response: any) => {
            if (response.responseCode === 'E200') {
              delete data.value;
              delete data.key;
              const data1 = true;
              this.serviceFile.fetchDataApi.next(data1);
            } else if (response.responseCode === 'E400') {
              this.snackBar.open(response.responseMessage, 'Dismiss', {
                duration: 3000
              });
            }
          },
          () => {
            this.snackBar.open('something went wrong', 'Dismiss', {
              duration: 3000
            });
          }
        );
      }
    });
  }
  }
}
