import { Component, OnInit, OnDestroy, Input, OnChanges, Output, EventEmitter } from '@angular/core';
import { UserService } from '@core/services';
import { Observable, Subject, of, iif } from 'rxjs';
import {
  takeUntil,
  switchMap,
  filter,
  finalize,
  debounceTime,
  concatMap,
  catchError,
  tap
} from 'rxjs/operators';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { IDynamicComponent } from '../../models/dynamic-component';
import { EmrOpthalmologyMasterDataService } from '../../services/emr-ophthalmology-master-data.service';
import { IFetchMasterDataRequest } from '../../models/fetch-master-data.request';
import { ApiResponse } from '@core/models/api-response';
import {
  IFetchMasterDataResponse,
  IFetchMasterDataObject
} from '../../models/fetch-master-data.response';
import { ToastrService } from 'ngx-toastr';
import { ITableData } from './models/form-data-value';
import { MatSnackBar, MatDialog } from '@angular/material';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { HttpErrorResponse } from '@angular/common/http';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-ophthalmic-history',
  templateUrl: './ophthalmic-history.component.html',
  styleUrls: ['./ophthalmic-history.component.scss']
})
export class OphthalmicHistoryComponent
  implements OnInit, OnDestroy, IDynamicComponent {
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  componentFetchData$: Observable<any>;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: number;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  componentDestructionNotifier$ = new Subject();

  ophthalmicHistoryForm: FormGroup;

  userData = this.userService.getUser();

  masterData: Array<IFetchMasterDataObject> = [];

  masterDataForm: FormGroup;

  tableData; // store the data for the table below the master data section

  readonly TABLE_DATA_FORM_ARRAY_NAME = 'tableData';
  readonly MASTER_DATA_FORM_ARRAY_NAME = 'masterData';
  readonly FORM_VALUE_CHANGE_DEBOUNCE_TIME = 300;

  isLoadingMasterData = false;
  isDisable: boolean;
  patientParamData: any;
  componentData: any;
  message: string;

  constructor(
    private readonly fb: FormBuilder,
    private readonly userService: UserService,
    private readonly masterDataApi: EmrOpthalmologyMasterDataService,
    private readonly toaster: ToastrService,
    private readonly snackbar: MatSnackBar,
    private readonly dialog: MatDialog,
    private readonly emrOpthService: EmrOpthoApiService,
    private readonly route: ActivatedRoute
  ) {}

  /**
   * copies right duration to left duration
   */
  copyDuration(tableGroup: FormGroup) {
    const rightDurationValue = tableGroup.get('right_eye_duration').value;
    const rightDurationUnit = tableGroup.get('right_eye_duration_units').value;
    tableGroup.get('left_eye_duration').patchValue(rightDurationValue);
    tableGroup.get('left_eye_duration_units').patchValue(rightDurationUnit);
  }

  initComponentDataFetchChangeListener() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(
          takeUntil(this.componentDestructionNotifier$),
          filter(Boolean)
        )
        .subscribe(componentsData => {
          // console.log(componentsData[this.componentId]);
          const componentData = componentsData[this.componentId];
          if (componentData) {
            this.componentData = componentData;
          }
        });
    }
  }

  fetchMasterData() {
    this.isLoadingMasterData = true;
    const { regId, orgId } = this.userData;
    const payload: IFetchMasterDataRequest = {
      authenticatedUserId: `${regId}`,
      componentIds: [this.componentId],
      orgId: `${orgId}`,
      specialityId: this.specialityId,
      doctorId: this.patientParamData.doctorId
    };

    if (this.ophthalmicHistoryForm) {
      this.ophthalmicHistoryForm.updateValueAndValidity();
    }

    return this.masterDataApi.fetchMasterData(payload).pipe(
      finalize(() => {
        this.isLoadingMasterData = false;
        this.ophthalmicHistoryForm.updateValueAndValidity();
      })
    );
  }

  openMasterDataDeleteConfirmationModal(message: string) {
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: message
        }
      })
      .afterClosed();
  }

  getVersionedIdTableElements(data) {
    const { tableData } = data;
    return tableData
      .filter(row => row.version_id)
      .map(({ emr_default_id, version_id }) => ({ emr_default_id, version_id }));
  }

  makeComponentDeletePayload(componentData) {
    const { regId, orgGroupId, orgId  } = this.userData;
    const { emr_default_id, version_id, child_table } = componentData;

    const request = {
      specialityId: this.specialityId,
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      // bedId: this.patientDemographicData.bedId,
      data: {
        [this.componentId]: {
          child_table: [{
            childComponentId: this.componentId,
            tableData: this.getVersionedIdTableElements(child_table[0])
          }],
          emr_default_id: emr_default_id ? emr_default_id : null,
          version_id: version_id ? version_id : null
        }
      }
    };
    return request;
  }

  data(){
    if ( this.patientParamData.pastVisitDetails === 'true') {
     return `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
    } else {
     return `Do you want to delete ophthalmic history  data  `;
    }

  }
  handleRefresh() {

    if (this.refreshForm$) {

      const predicate = () => {
        const formValue = this.ophthalmicHistoryForm.value;
        const { version_id, child_table: [ { tableData } ] } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        doDelete = doDelete || tableData.some(val => val.version_id);
        return doDelete;
      };

      this.refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this.openMasterDataDeleteConfirmationModal(this.data())),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this.makeComponentDeletePayload(this.ophthalmicHistoryForm.value))
                .pipe(
                  concatMap(request => {
                    return this.deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(<ApiResponse<null>>errorResponse.error),
                          of(new ApiResponse<null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse<null>(null, 'E200', [])));
          }),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.masterDataControls.forEach(formGroup => {
              formGroup.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            });
            const tableFormArray = this.getTableDataFormArray();
            while (tableFormArray.length) {
              tableFormArray.removeAt(tableFormArray.length - 1);
            }
            this.ophthalmicHistoryForm.get('global_comments').setValue(null);
            this .ophthalmicHistoryForm.get('version_id').setValue(null);
            this .ophthalmicHistoryForm.get('emr_default_id').setValue(null);
            this.dataReset.emit(true);
          } else {
            this.snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }

  initComponentFetchDataChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentFetchDataChangeHandler.bind(this));
    }
  }

  // componentFetchDataChangeHandler(componentsData) {
  //   if (componentsData) {
  //     const componentData = componentsData[this.componentId];
  //     const childTable = componentData ? componentData.child_table[0] : {};
  //     const tableData = (childTable && childTable.tableData) ? childTable.tableData : [];
  //     const childDataFormArray = <FormArray>(this.ophthalmicHistoryForm.get('child_table'));
  //     const tableDataFormArray = <FormArray>(childDataFormArray.at(0).get(this.TABLE_DATA_FORM_ARRAY_NAME));

  //     if (componentData) {

  //       const {
  //         emr_default_id,
  //         global_comments,
  //         version_id
  //       } = componentData;

  //       this.ophthalmicHistoryForm.patchValue({ emr_default_id, global_comments, version_id });
  //     }

  //     while (tableDataFormArray.length) {
  //       tableDataFormArray.removeAt(tableDataFormArray.length - 1 );
  //     }

  //     tableData.forEach(row => {
  //       const formGroup = this.initTableDataFormGroup(row);
  //       tableDataFormArray.push(formGroup);
  //     });

  //     const eye_diseases = tableDataFormArray.value.map(val => val.eye_disease);

  //     this.masterDataControls.forEach(group => {
  //       const { description } = group.value;
  //       const isIncluded = eye_diseases.includes(description);
  //       group.patchValue({ $$isMasterDataSelected: isIncluded });
  //     });

  //   }
  // }
  componentFetchDataChangeHandler(componentsData) {
    if (componentsData) {
      const componentData = componentsData[this .componentId];
      const childTable = componentData ? componentData.child_table[0] : {};
      const tableData =
        childTable && childTable.tableData ? childTable.tableData : [];

      if (componentData) {
        const { emr_default_id, global_comments, version_id } = componentData;

        this .ophthalmicHistoryForm.patchValue({
          emr_default_id: emr_default_id ? emr_default_id : null,
          global_comments: global_comments ? global_comments : null,
          version_id: version_id ? version_id : null
        });
      }

      if (tableData.length) {
             const childDataFormArray = <FormArray>(this.ophthalmicHistoryForm.get('child_table'));
      const tableDataFormArray = <FormArray>(childDataFormArray.at(0).get(this.TABLE_DATA_FORM_ARRAY_NAME));

        while (tableDataFormArray.length) {
          tableDataFormArray.removeAt(tableDataFormArray.length - 1);
        }
        tableData.forEach(row => {
          const formGroup = this .initTableDataFormGroup(row);
          tableDataFormArray.push(formGroup);
        });

        const eye_diseases = tableDataFormArray.value.map(
          val => val.eye_disease
        );

        this .masterDataControls.forEach(group => {
          const { description } = group.value;
          const isIncluded = eye_diseases.includes(description);
          group.patchValue({ $$isMasterDataSelected: isIncluded });
        });
      }
    }
  }

  get masterDataControls() {
    const masterDataFormArray = <FormArray>(
      this.masterDataForm.get(this.MASTER_DATA_FORM_ARRAY_NAME)
    );

    if (this.ophthalmicHistoryForm && masterDataFormArray) {
      return masterDataFormArray.controls;
    } else {
      return [];
    }
  }

  get childTableFormArray() {
    const childTableFormArray = <FormArray>(
      this.ophthalmicHistoryForm.get('child_table')
    );

    if (this.ophthalmicHistoryForm && childTableFormArray) {
      return childTableFormArray.controls;
    } else {
      return [];
    }
  }

  initMasterDataForm() {
    this.masterDataForm = this.fb.group({
      [this.MASTER_DATA_FORM_ARRAY_NAME]: this.fb.array([])
    });
  }

  initForm() {
    this.ophthalmicHistoryForm = this.fb.group({
      child_table: this.fb.array([
        this.fb.group({
          childComponentId: this.componentId,
          [this.TABLE_DATA_FORM_ARRAY_NAME]: this.fb.array([])
        })
      ]),
      emr_default_id: null,
      global_comments: null,
      version_id: null
    });

    if (this.disabled) {
      this.isDisable = true;
      this.ophthalmicHistoryForm.disable();
    } else {
      this.ophthalmicHistoryForm.enable();
    }

    if (this.parentFormGroup) {
      this.parentFormGroup.addControl(`${this.componentId}`, this.ophthalmicHistoryForm);
    }
  }

  initMasterDataControlValueChangeHandler() {
    this.masterDataForm
      .get(this.MASTER_DATA_FORM_ARRAY_NAME)
      .valueChanges.pipe(
        takeUntil(this.componentDestructionNotifier$),
        debounceTime(this.FORM_VALUE_CHANGE_DEBOUNCE_TIME)
      )
      .subscribe(this.makeTableDataFormArray.bind(this));
  }

  initTableDataFormGroup(data: ITableData) {
    const { required, pattern } = Validators;
    return this.fb.group({
      description: data.description,
      left_eye_duration_units: [data.left_eye_duration_units, required],
      right_eye_duration_units: [data.right_eye_duration_units, required],
      left_eye_duration: [data.left_eye_duration, [required, pattern(/^\d+$/)]],
      right_eye_duration: [data.right_eye_duration, [required, pattern(/^\d+$/)]],
      eye_disease: data.eye_disease,
      version_id: data.version_id,
      emr_default_id: data.emr_default_id
    });
  }

  makeTableDataFormArray(masterDataArray: Array<IFetchMasterDataObject>) {
    const childDataFormArray = <FormArray>(this.ophthalmicHistoryForm.get('child_table'));
    const tableDataFormArray = <FormArray>(childDataFormArray.at(0).get(this.TABLE_DATA_FORM_ARRAY_NAME));

    const tableDataFormArrayControls = tableDataFormArray.controls;

    masterDataArray.forEach((masterData, masterDataIndex) => {
      const index = tableDataFormArrayControls.findIndex(control => {
        const value = control.value as ITableData;
        return value.eye_disease === masterData.description;
      });
      if (masterData.$$isMasterDataSelected && index === -1) {
        tableDataFormArray.push(this.initTableDataFormGroup({  eye_disease: masterData.description }));
      }
      if (!masterData.$$isMasterDataSelected && index > -1) {
        const { emr_default_id, version_id } = tableDataFormArray.at(index).value;

        if (version_id) {

          if (version_id && this.patientParamData.pastVisitDetails === 'true') {
            this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ${masterData.description }?`;
          } else {
            this.message = `Do you want to delete  ${
              masterData.description
            } `;
          }

          this.openMasterDataDeleteConfirmationModal(this.message)
            .pipe(
              tap(result => {
                if (!result) {
                  this.handleDeleteError(masterDataIndex);
                }
              }),
              filter(Boolean),
              concatMap(_ =>
                this.deleteField({
                  emr_default_id,
                  version_id,
                  patientId: this.patientParamData.patientId,
                  patientVisitId: this.patientParamData.patientVisitId,
                  orgId: this.userData.orgId,
                  componentId: this.componentId,
                  authenticatedUserId: this.userData.regId
                })
              ),
              takeUntil(this.componentDestructionNotifier$)
            )
            .subscribe((response) => {
              if (response.responseCode === 'E200') {
                tableDataFormArray.removeAt(index);
                 this.dataReset.emit(true);
              } else {
                this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
              }
            });
          } else {
            tableDataFormArray.removeAt(index);
          }
        }
      });
  }

  deleteField(payload) {
    return this.emrOpthService.deleteDataOfDynamicField(payload);
  }


  handleDeleteError(masterDataIndex) {
    const masterDataFormArray =  <FormArray>this.masterDataForm.get(this.MASTER_DATA_FORM_ARRAY_NAME);
    const masterDataControl = masterDataFormArray.at(masterDataIndex);
    if (masterDataControl) {
      masterDataControl.patchValue({ $$isMasterDataSelected: true }, { emitEvent: false });
    }
  }

  masterDataLoadingValidator() {
    if (this.isLoadingMasterData) {
      return { loadingMasterData: true };
    } else {
      return null;
    }
  }

  makeMasterDataFormArray(masterData: Array<IFetchMasterDataObject>) {
    const masterDataFormArray = <FormArray>(
      this.masterDataForm.get(this.MASTER_DATA_FORM_ARRAY_NAME)
    );
    while (masterDataFormArray.length) {
      masterDataFormArray.removeAt(0);
    }
    let i = 0;
    while (i < masterData.length) {
      masterDataFormArray.push(this.fb.group(masterData[i++]));
    }
    if (this.disabled) {
      this.isDisable = true;
      this.masterDataForm.disable();
    } else {
      this.masterDataForm.enable();
    }
  }

  masterDataChangeHandler(response: ApiResponse<IFetchMasterDataResponse | null>) {
    if (response.responseCode === 'E200') {
      const masterData = response.data[this.componentId]
        .filter(data => data.isApplicable)
        .map(data => ({ ...data, $$isMasterDataSelected: false }));

      if (masterData) {
        this.makeMasterDataFormArray(masterData);
      }
    } else {
      this.toaster.error('Unable to fetch the master data', 'Error!');
    }
  }

  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();

    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.shouldAllowComponent();
    this.refetchValueChangeHandler();
    this.initMasterDataForm();
    this.initForm();
    this.initMasterDataControlValueChangeHandler();

    this.initComponentFetchDataChangeHandler();

    this.fetchMasterData().subscribe(
      this.masterDataChangeHandler.bind(this),
      _ => { this.toaster.error('Error occurred while fetching ophthalmic master data', 'Error!'); }
    );
    this.handleRefresh();
  }

  refetchValueChangeHandler() {
    if (this.refetchMasterData$) {
      this.refetchMasterData$
        .pipe(
          filter(Boolean),
          switchMap(_ => this.fetchMasterData()),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe(console.log);
    }
  }

  shouldAllowComponent() {
    if (!this.componentId) {
      throw new Error('Please provide the component id to this component');
    }
    if (!this.specialityId) {
      throw new Error('Please provide the speciality id to this component');
    }
  }

  getTableDataFormArray() {
    const childTableFormArray = <FormArray>(
      this.ophthalmicHistoryForm.get('child_table')
    );
    const tableDataFormArray = <FormArray>(
      childTableFormArray.at(0).get(this.TABLE_DATA_FORM_ARRAY_NAME)
    );
    return tableDataFormArray;
  }

  get tableDataControls() {
    const tableDataFormArray = this.getTableDataFormArray();
    if (this.ophthalmicHistoryForm && tableDataFormArray) {
      return tableDataFormArray.controls;
    } else {
      return [];
    }
  }
}
