import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { GlassDetailsService } from '@app/modules/emr-ophthalmology/services/glass-details.service';
import { Observable, Subject, of, iif } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { IDynamicComponent } from '../../models/dynamic-component';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-pmt',
  templateUrl: './pmt.component.html',
  styleUrls: ['./pmt.component.scss']
})
export class PmtComponent implements OnInit, IDynamicComponent, OnDestroy {
  pmtForm: FormGroup;
  public isAccordionOpen = false;
  public pmtData = [];
  public pmtDataArray = [];
  public temModelName: any;
  public isPlusMinusValues = false;
  public isTableDataDisable = true;
  public distantSph: any;
  public distantCyl: any;
  public distantAxis: any;
  public distantVision: any;
  public addSph: any;
  public addVision: any;
  public nearSph: any;
  public nearCyl: any;
  public nearAxis: any;
  public nearVision: any;
  public tableType: any;
  public plusDatavalue: any;
  public minusDataValue: any;
  public leftDistantSph: any;
  public leftDistantCyl: any;
  public leftDistantAxis: any;
  public leftDistantVision: any;
  public leftAddSph: any;
  public leftAddVision: any;
  public leftNearSph: any;
  public leftNearCyl: any;
  public leftNearAxis: any;
  public leftNearVision: any;
  public rightLensData: any;
  public selectedLens: any;
  refractionArray = [];
  public undilated_Form_Validation = {
    validate: [
      { type: 'required', message: '* Field Data is required' },
      {
        type: 'maxlength',
        message: '* Fields data cannot be more than 12 characters'
      },
      {
        type: 'pattern',
        message: ' Only Alpha-Numeric,Positive,Negative,Decimal values'
      }
    ],
    advice: [
      {
        type: 'maxlength',
        message: '* Field Data allows upto 250 characters'
      }
    ]
  };
  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  currentTabId: any;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  patientDemographicData: any;

  @Input()
  componentFetchData$: Observable<any>;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();
  readonly CURRENT_USER = this.user.getUser();
  componentDestructionNotifier$ = new Subject();
  patientParamData: any;
  message: string;
  componentData: any;
  constructor(
    private readonly dilatedpmtData: GlassDetailsService,
    private readonly fb: FormBuilder,
    private readonly dialog: MatDialog,
    private readonly user: UserService,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) { }
  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    if (this.parentFormGroup) {
      this.parentFormGroup.addControl(`${this.componentId}`, this.pmtForm);
    }
    this.initComponentDataFetchChangeHandler();
    this.handleRefresh();
  }
  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {
    
    if (this.patientParamData.pastVisitDetails === 'true') {
      this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
    } else {
      
      this.message = 'Do you want to delete PMT component data?';
    }
  
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // if (
    //   payload.data[this.componentId].emr_default_id &&
    //   payload.data[this.componentId].version_id
    // ) {
    //   return this.emrOphthoService.deleteDataOfDynamicField(payload);
    // } else {
    //   this.pmtForm.reset();
    // }
  }

  /**
   * @description This method is used for deleting the component date when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this.refreshForm$) {

      const predicate = () => {
        console.log(this.pmtForm.value);
        const formValue = this.pmtForm.value;
        console.log(formValue + 'pmt');
        const { version_id, child_table } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this.refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this.openDataDeleteConfirmationModal('')),
          concatMap(_ => {
            return iif(
              predicate,
              of(this.makeDeleteRequestObject(this.pmtForm.value))
                .pipe(
                  concatMap(request => {
                    return this.deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(<ApiResponse<null>>errorResponse.error),
                          of(new ApiResponse<null>(null, 'E400', []))
                        );
                      }));
                  }
                  )
                ),
              of(new ApiResponse<null>(null, 'E200', [])));
          }),
          takeUntil(this.componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this.pmtForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this.pmtForm.reset();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this.dataReset.emit(true);
          } else {
            this.snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  initForm() {
    const validator = [
      null,
      [Validators.pattern(/^[-pPnN0-9.+-/']*$/), Validators.maxLength(12)]
    ];
    this.pmtForm = this.fb.group({
      emr_default_id: null,
      version_id: null,
      distance_right_sph: validator,
      distance_right_cyl: validator,
      distance_right_axis: validator,
      distance_right_vision: null,
      add_right_sph: validator,
      add_right_cyl: validator,
      add_right_axis: validator,
      add_right_vision: validator,
      near_right_sph: validator,
      near_right_cyl: validator,
      near_right_axis: validator,
      near_right_vision: validator,
      distance_left_sph: validator,
      distance_left_cyl: validator,
      distance_left_axis: validator,
      distance_left_vision: null,
      add_left_sph: validator,
      add_left_cyl: validator,
      add_left_axis: validator,
      add_left_vision: validator,
      near_left_sph: validator,
      near_left_cyl: validator,
      near_left_axis: validator,
      near_left_vision: validator,
      // pmt_selectControl:'pmt-plus-btn'
    });

    if (this.disabled) {
      this.pmtForm.disable();
    } else {
      this.pmtForm.enable();
    }

  }
  /**
   * @description This method is used to open accordion and getting the data from table
   */
  onClickLensFields(modelName, type) {

    if (this.disabled) {
      return;
    }

    this.isAccordionOpen = true;
    this.temModelName = modelName;

    if (type === 'sph') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.pmtData = this.dilatedpmtData.distMinusSphData;
        this.tableType = type;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      } else {
        this.minusDataValue = 'plus';
        this.pmtData = this.dilatedpmtData.distPlusSphData;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
        this.tableType = type;
      }
    }
    if (type === 'cyl') {
      this.isPlusMinusValues = true;
      if (this.minusDataValue === 'minus') {
        this.pmtData = this.dilatedpmtData.distMinusCylData;
        this.tableType = type;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      } else {
        this.minusDataValue = 'plus';
        this.pmtData = this.dilatedpmtData.distPlusCylData;
        this.tableType = type;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (type === 'axis' || type === 'leftaxis') {
      this.isPlusMinusValues = false;

      this.pmtData = this.dilatedpmtData.distAxisData;
      this.tableType = type;
      this.pmtDataArray = this.pmtData[0].array;
      this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'vision' || type === 'leftvision') {
      this.isPlusMinusValues = false;
      this.pmtData = this.dilatedpmtData.distVaData;
      this.tableType = type;
      this.pmtDataArray = this.pmtData[0].array;
      this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'addsph' || type === 'leftaddsph') {
      this.isPlusMinusValues = false;
      this.pmtData = this.dilatedpmtData.addSphData;
      this.tableType = type;
      this.pmtDataArray = this.pmtData[0].array;
      this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'nearsph' || type === 'leftnearsph') {
      this.isPlusMinusValues = false;
      this.pmtData = this.dilatedpmtData.nearSphData;
      this.pmtDataArray = this.pmtData[0].array;
      this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (type === 'nearvision' || type === 'leftnearvision') {
      this.isPlusMinusValues = false;
      this.pmtData = this.dilatedpmtData.nearVaData;
      this.pmtDataArray = this.pmtData[0].array;
      this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
        return [...acc, ...val];
      }, []);
    }
    if (
      type === 'nearcyl' ||
      type === 'addcyl' ||
      type === 'addaxis' ||
      type === 'nearaxis' ||
      type === 'addvision' ||
      type === 'leftnearcyl' ||
      type === 'leftaddcyl' ||
      type === 'leftaddaxis' ||
      type === 'leftnearaxis' ||
      type === 'leftaddvision'
    ) {
      this.pmtDataArray = [];
      this.isAccordionOpen = false;
    }
  }
  /**
   * @description This method is used to open  and close accordion
   */
  onClickDownButton() {
    if (this.disabled) {
      return;
    }
    this.isAccordionOpen = !this.isAccordionOpen;
    this.pmtDataArray = this.pmtData[0].array;

  }
  onClickRightButton() {
    if (this.disabled) {
      return;
    }
    this.isAccordionOpen = !this.isAccordionOpen;
    this.onClickLensFields('distance_right_sph', 'sph');
  }
  /**
   * @description This method is used to plus table data values
   */
  onClickPlus(plusvalue) {
    this.minusDataValue = plusvalue;
    if (this.tableType === 'sph') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.pmtData = this.dilatedpmtData.distPlusSphData;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'cyl') {
      if (plusvalue === 'plus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.pmtData = this.dilatedpmtData.distPlusCylData;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to minus table data values
   */
  // function for  start from here
  onClickMinus(minusvalue) {
    this.minusDataValue = minusvalue;
    if (this.tableType === 'sph') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.pmtData = this.dilatedpmtData.distMinusSphData;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
    if (this.tableType === 'cyl') {
      if (minusvalue === 'minus') {
        this.onClickLensFields(this.temModelName, this.tableType);
        this.pmtData = this.dilatedpmtData.distMinusCylData;
        this.pmtDataArray = this.pmtData[0].array;
        this.refractionArray = this.pmtDataArray.reduce((acc, val) => {
          return [...acc, ...val];
        }, []);
      }
    }
  }
  /**
   * @description This method is used to get table data values
   */
  onClickLensData(value) {
    this[this.temModelName] = value;
    this.pmtForm.patchValue({ [this.temModelName]: value });
  }
  /**
   * @description This method is used to copy from left lens data to right
   */
  onClickCopyTableData() {

    if (this.disabled) {
      return;
    }

    const leftsph = this.pmtForm.get('distance_right_sph').value;
    const leftcyl = this.pmtForm.get('distance_right_cyl').value;
    const leftaxis = this.pmtForm.get('distance_right_axis').value;
    const leftvision = this.pmtForm.get('distance_right_vision').value;
    const leftAddSph = this.pmtForm.get('add_right_sph').value;
    const leftAddVision = this.pmtForm.get('add_right_vision').value;
    const leftNearSph = this.pmtForm.get('near_right_sph').value;
    const leftNearCyl = this.pmtForm.get('near_right_cyl').value;
    const leftNearaxis = this.pmtForm.get('near_right_axis').value;
    const leftNearvision = this.pmtForm.get('near_right_vision').value;
    this.pmtForm.get('distance_left_sph').patchValue(leftsph);
    this.pmtForm.get('distance_left_cyl').patchValue(leftcyl);
    this.pmtForm.get('distance_left_axis').patchValue(leftaxis);
    this.pmtForm.get('distance_left_vision').patchValue(leftvision);
    this.pmtForm.get('add_left_sph').patchValue(leftAddSph);
    this.pmtForm.get('add_left_vision').patchValue(leftAddVision);
    this.pmtForm.get('near_left_sph').patchValue(leftNearSph);
    this.pmtForm.get('near_left_cyl').patchValue(leftNearCyl);
    this.pmtForm.get('near_left_axis').patchValue(leftNearaxis);
    this.pmtForm.get('near_left_vision').patchValue(leftNearvision);
  }
  /**
   * @description This method is used to refresh the data in the table.
   */
  refreshData() { }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.pmtForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
}
