import { Injectable } from '@angular/core';
import { IFetchComponentsRequest } from './models/fetch-components-request';
import { IFetchComponentsResponse } from './models/fetch-components-response';
import { IShiftComponentRequest } from './models/shift-component-request';
import { IShiftComponentReponse } from './models/shift-component.response';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { catchError } from 'rxjs/operators';
import { HttpErrorResponse } from '@angular/common/http';
import { iif, of } from 'rxjs';
import { ApiResponse } from '@core/models/api-response';

@Injectable({ providedIn: 'root' })
export class DynamicTabSettingsService {
  constructor(private readonly emropthoservice: EmrOpthoApiService) { }

  fetchListOfComponents(payload: IFetchComponentsRequest) {
    return this.emropthoservice
      .fetchListOfComponents<IFetchComponentsResponse>(payload)
      .pipe(
        catchError((errorResponse: HttpErrorResponse) => {
          return iif(
            () => errorResponse.status > 0,
            of(<ApiResponse<null>>errorResponse.error),
            of(new ApiResponse<null>(null, 'E400', []))
          );
        })
      );
  }

  shiftComponent(payload: IShiftComponentRequest) {
    return this.emropthoservice
      .insertOrUpdateComponent<IShiftComponentReponse>(payload)
      .pipe(
        catchError((errorResponse: HttpErrorResponse) => {
          return iif(
            () => errorResponse.status > 0,
            of(<ApiResponse<null>>errorResponse.error),
            of(new ApiResponse<null>(null, 'E400', []))
          );
        })
      );
  }

}
