  save() {
    if (this.patientParamData.pastVisitDetails === 'true') {
      const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: `It is an earlier visit ${this.patientParamData.visitDate}, do you want to save ?`
        },
        disableClose: true
      });
      dialogRef.afterClosed().subscribe(result => {
        if (result) {
          this.InsertOrUpdatePostOperationData();
        }
      });
    } else {
      this.InsertOrUpdatePostOperationData();
    }

  }
  visitLinkToOtherTab(type) {
    if (type === 'diagnosis') {
      this.navigateToDiagnosis.emit(null);
    }
    if (type === 'erx') {
      this.navigateToErx.emit(null);
    }
    if (type === 'cpoe') {
      this.navigateToCpoe.emit(null);
    }
  }
  /**
   * @description This method is used to display the data when the date button is clicked
   * @function displayDrugInfo
   */
  displayDrugInfo(obj) {
    this.dialogData = obj;
    this.dialog.open(this.displayDrugInfoDialog);
  }
  /**
   * @description This Method will return the details that was diplayed in left side menu in post operation
   * @function fetchPostOperationDetails
   */
  fetchPostOperationDetails() {
    const payload: IFetchPostOperationData = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId,
      operatioKey: 'post'
    };
    this.EmrOphthalmologyApiService.fetchPostOperationDetails(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          if (this.surgeryPostOpForm.get('progressDtt').value) {
         this.isbuttonDisableRef.nativeElement.disabled = false;
          } else {
         this.isbuttonDisableRef.nativeElement.disabled = true;
         this.isbuttonDisableRef.nativeElement.style.cursor = 'not-allowed';

          }
          this.postOpData = response.data;
          this.showMainForm = false;
          this.postOpData.forEach(ele => {
            // here we are binding form data recieved from the api to the form values to get update the data while fetch is called
            ele.formvalue = ele.postOperationsList;
            this.postEmrId = ele.formvalue['emrDefaultId'];
            this.postDataList = ele.postOperationsList;
          });
          // this.postOpData.filter(data=> {
          //   if(this.postOpData)
          // })
        } else if (response.responseCode === 'E400') {
          this.snackBar.open('error', response.responseMessage, {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Dismiss', {
          duration: 3000
        });
      }
    );
  }
  /**
   * @description This Method is used to get the date wise post operation data.
   * @function fetchPostOperationDatewiseDetails
   */
  fetchPostOperationDatewiseDetails(item, index) {
    const progressDt = this.surgeryPostOpForm.get('progressDtt').value;
    const payload: IFetchPostOperationDatewiseData = {
      authenticatedUserId: this.userSession.regId,
      doctorId: this.patientParamData.doctorId,
      orgId: this.userSession.orgId,
      orgGrpId: this.userSession.orgGroupId,
      patientVisitId: this.patientDemographicData.patientVisitId,
      patientId: this.patientDemographicData.patientId,
      progressDt: progressDt ? progressDt : item.progressDttm,
      operationKey: 'post'
    };
    this.EmrOphthalmologyApiService.fetchPostOperationDatewiseDetails(
      payload
    ).subscribe(
      response => {
        if (response.responseCode === 'E200') {
          this.postOpDatewiseData = response.data;
          if (this.postOpDatewiseData.labList && this.postOpDatewiseData.labList.length) {
            this.selectedLabDate(this.postOpDatewiseData.labList[0]);
          }
          if (this.postOpDatewiseData.imagingList && this.postOpDatewiseData.imagingList.length) {
            this.selectedimagingDate(this.postOpDatewiseData.imagingList[0]);
          }
          if (this.postOpDatewiseData.drugList && this.postOpDatewiseData.drugList.length) {
            this.selectedDatee(this.postOpDatewiseData.drugList[0]);
          }
          // this.postDataTime.push(this.postOpDatewiseData.progressTime);
          // this.postDataTime = this.postOpDatewiseData.progressTime;
          this.druglistData = this.postOpDatewiseData.drugList;
          if (this.druglistData && this.druglistData.length) {
            this.druglistData.forEach(ele => {
              this.drugsListData = ele.drugsList;
            });
          }
          this.labListData = this.postOpDatewiseData.labList;
          if (this.labListData && this.labListData.length) {
            this.labListData.forEach(ele => {
              this.panelData = ele.panelList;
              this.testData = ele.testList1;
            });
          }
          this.postOpDigData = this.postOpDatewiseData.postDiagnosisList;
          this.postOpData.unshift(this.postOpDatewiseData);
        } else if (response.responseCode === 'E400') {
          this.snackBar.open('error', response.responseMessage, {
            duration: 3000
          });
        }
      },
      () => {
        this.snackBar.open('something went wrong', 'Dismiss', {
          duration: 3000
        });
      }
    );
  }
  /**
   * @description This method is used to close the modal dialog
   * @function close
   */
