import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { EmrOpthoActionTypes, FetchAssistantsList, FetchAssistantsListSuccess, FetchAssistantsListError } from '../../actions/emr-ophthalmology-actions';
import { mergeMap, map, catchError } from 'rxjs/operators';
import { AssistantListService } from './assistants-list.service';
import { of } from 'rxjs';

@Injectable()
export class AssistantsListEffect {

  @Effect()
  fetchAssistantList$ = this.actions$
    .pipe(
      ofType<FetchAssistantsList>(EmrOpthoActionTypes.FetchAssistantsList),
      mergeMap(
        (action) => this.assistListService.fetchEmployeesHavingAccessOnEmr(action.payload)
          .pipe(
            map(response => {
              if (response.responseCode === 'E200') {
                return new FetchAssistantsListSuccess(response.data);
              } else {
                return new FetchAssistantsListError();
              }
            }),
            catchError(_ => of(new FetchAssistantsListError()))
          )
      )
    );

  constructor(
    private readonly actions$: Actions,
    private readonly assistListService: AssistantListService
  ) {}
}
