import { Component, OnInit, Inject, OnDestroy } from '@angular/core';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material';
import { timer, merge, Subject } from 'rxjs';
import { map, takeUntil } from 'rxjs/operators';
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-pulse-add-edit',
  templateUrl: './pulse-add-edit-dialog.component.html',
  styleUrls: ['./pulse-add-edit-dialog.component.scss']
})
export class PulseAddEditComponent implements OnInit , OnDestroy {
  isEdit = false;

  PulsetableData: FormGroup;

  maxDate = new Date();
  componentDestructionNotifier$ = new Subject();
  endTimer = new Subject();
  formData: any;

  constructor(
    private readonly fb: FormBuilder,
    @Inject(MAT_DIALOG_DATA)
    private readonly injectedData,
    private readonly dialogRef: MatDialogRef<PulseAddEditComponent>,
    private datePipe: DatePipe,
  ) {
    this.isEdit = injectedData.edit;
  }
  // getTime() {
  //   return timer(0, 1000).pipe(
  //     map(_ => new Date()),
  //     takeUntil(
  //       this.componentDestructionNotifier$
  //     )
  //   );
  // }
  ngOnInit() {
    const { required, pattern } = Validators;
    this.PulsetableData = this.fb.group({
      version_id: null,
      emr_default_id: null,
      pulse_capture_dttm: [new Date(), required],
      pulse_score: [null, [required, pattern(/^\d+$/)]],
      pulse_rate: [null, [required, pattern(/^\d+$/)]]
    });
        this.PulsetableData.get('pulse_score').disable();
    this.PulsetableData.get('pulse_rate').valueChanges.subscribe(data => {
      if (data <= 40) {
        this.PulsetableData.get('pulse_score').patchValue('3');
        } else if (41 <= data && data <= 50) {
        this.PulsetableData.get('pulse_score').patchValue('1');
        } else if (51 <= data && data <= 90) {
        this.PulsetableData.get('pulse_score').patchValue('0');
        } else if (91 <= data && data <= 110) {
        this.PulsetableData.get('pulse_score').patchValue('1');
        } else if (110 <= data && data <= 130) {
        this.PulsetableData.get('pulse_score').patchValue('2');
        } else if (131 <= data) {
        this.PulsetableData.get('pulse_score').patchValue('3');
        }
    });
    if (this .isEdit) {
      const injectedData = this .injectedData.rowData;
      injectedData.pulse_capture_dttm = new Date(+injectedData.pulse_capture_dttm);
      this .PulsetableData.patchValue(this.injectedData.rowData);
    }
    // this.getTime()
    //    .pipe(
    //      takeUntil(this.componentDestructionNotifier$)
    //    )
    //   .subscribe(date => {
    //     this.PulsetableData.patchValue({
    //       pulse_capture_dttm : date
    //     });
    // });
  }

  add() {
    if (this.PulsetableData.valid) {
      this.formData = this.PulsetableData.value;
      this.formData['pulse_score'] =  this.PulsetableData.get('pulse_score').value;
      this.formData.pulse_capture_dttm = this.formData.pulse_capture_dttm.getTime();
      this.dialogRef.close(this.formData);
    }
  }
  ngOnDestroy() {
    this.componentDestructionNotifier$.next();
    this.componentDestructionNotifier$.complete();
  }
  close() {
    this.dialogRef.close();
  }
}
