import { Component, OnInit, Input } from '@angular/core';
import { FormGroup, FormArray, FormBuilder, Validators } from '@angular/forms';
@Component({
  selector: 'app-appearence',
  templateUrl: './appearence.component.html',
  styleUrls: ['./appearence.component.scss']
})
export class AppearenceComponent implements OnInit {
  appearenceSectionForm: FormGroup;

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.appearenceSectionForm = this.form;
    if (this.disabled) {
      this.appearenceSectionForm.disable();
    } else {
      this.appearenceSectionForm.enable();
    }
  }

}
