import {
  Component,
  OnInit,
  OnDestroy,
  Input,
  Output,
  EventEmitter
} from '@angular/core';
import { UserService } from '@core/services';
import { UserSession } from '@core/models/user-session';
import {
  FormBuilder,
  FormGroup,
  FormArray,
  Validators,
  FormControl
} from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { Subject, Observable, of, iif } from 'rxjs';
import { IDynamicComponent } from '../../models/dynamic-component';
import { EmrOpthalmologyMasterDataService } from '../../services/emr-ophthalmology-master-data.service';
import {
  IFetchMasterDataObject,
  IFetchMasterDataResponse
} from '../../models/fetch-master-data.response';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { IFetchMasterDataRequest } from '../../models/fetch-master-data.request';
import { MatSnackBar, MatDialog } from '@angular/material';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';
import {
  takeUntil,
  filter,
  switchMap,
  finalize,
  tap,
  concatMap,
  catchError
} from 'rxjs/operators';
import { IAllergiesTableData } from './models/table-data-values';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { HttpErrorResponse } from '@angular/common/http';
import { formControlBinding } from '@angular/forms/src/directives/ng_model';

@ Component({
  selector: 'app-allergies',
  templateUrl: './allergies.component.html',
  styleUrls: ['./allergies.component.scss']
})
export class AllergiesComponent
  implements OnInit, OnDestroy, IDynamicComponent {
  @ Input()
  parentFormGroup: FormGroup;

  @ Input()
  disabled: boolean;

  @ Input()
  refetchMasterData$: Observable< boolean>;

  @ Input()
  componentId: number;

  @ Input()
  specialityId: number;

  @ Input()
  isParentFormSubmitted: boolean;

  @ Input()
  refreshForm$: Subject< boolean>;

  @ Input()
  componentFetchData$: Observable< any>;

  @ Input()
  patientDemographicData: any;

  @ Input()
  currentTabId: number;

  @ Output()
  dataReset: EventEmitter< boolean> = new EventEmitter();

  componentDestructionNotifier$ = new Subject();

  allergiesForm: FormGroup;

  userData: UserSession = this .userService.getUser();

  masterData: Array< IFetchMasterDataObject> = [];

  masterDataForm: FormGroup;
  allergiesData: any;

  tableData; // store the data for the table below the master data section

  readonly TABLE_DATA_FORM_ARRAY_NAME = 'tableData';
  readonly MASTER_DATA_FORM_ARRAY_NAME = 'masterData';
  readonly FORM_VALUE_CHANGE_DEBOUNCE_TIME = 300;

  isLoadingMasterData = false;
  isDisable: boolean;
  patientParamData: any;
  // CURRENT_USER: { orgId: any; orgGroupId: any; regId: any; };
  readonly CURRENT_USER = this.userService.getUser();
  componentData: any;
  message: string;
  constructor(
    private readonly fb: FormBuilder,
    private readonly userService: UserService,
    private readonly masterDataApi: EmrOpthalmologyMasterDataService,
    private readonly toastr: ToastrService,
    private readonly dialog: MatDialog,
    private readonly emrOpthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) {}

  initForm() {
    this.allergiesForm = this.fb.group({
      description: new FormControl(''),
      emr_default_id: null,
      version_id: null
    });
  }

 initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    // const componentData = componentsData[this.componentId];
    // if (componentData) {
    //   this.allergiesForm.patchValue(componentData);
    // }
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.allergiesForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initComponentDataFetchChangeHandler();
    this.initForm();
    if (this.parentFormGroup) {
      console.log(this.parentFormGroup);
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.allergiesForm
      );
    }
    this.handleRefresh();
  }

  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {

 if (this.patientParamData.pastVisitDetails) {
  this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
 } else {
  this.message = 'Do you want delete Allergies data?';
 }
 
    return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    return this.emrOpthoService.deleteDataOfDynamicField(payload);
  }

  /**
   * @description This method is used for deleting the component data when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .allergiesForm.value;
        const { version_id } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDataDeleteConfirmationModal('Do you want to delete allergies component data')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeleteRequestObject(this .allergiesForm.value))
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this .allergiesForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this .allergiesForm.reset();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this .dataReset.emit(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }

  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
}
