import { Component, OnInit, OnDestroy, Input } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
 selector: 'app-appendages',
 templateUrl: './appendages.component.html',
 styleUrls: ['./appendages.component.scss']
})

export class AppendagesComponent implements OnInit, OnDestroy {
  public Test1;
  public isShowEyeLashes = false;
  public isShowSyringing = false;
  public isShowLacrimalSac = false;
  public isShowEyeLids = true;

  @Input()
  form: FormGroup;

  @Input()
  disabled: boolean;

  appendagesSectionForm: FormGroup;

  ngOnInit() {
    this.initForm();
  }

  initForm() {
    this.appendagesSectionForm = this.form;
    if (this.disabled) {
      this.appendagesSectionForm.disable();
    } else {
      this.appendagesSectionForm.enable();
    }
  }

  ngOnDestroy() {}

  selecteType(type: any) {
    if (type) {
      if (type === 'left_appendages_lacrimal') {
        this.Test1 = document.getElementById('lLacrimalSac');
        if (this .Test1.checked) {
          this.isShowLacrimalSac = false;
        } else {
         this.isShowLacrimalSac = true;
        }
      }
      if (type === 'left_appendages_eyelashes') {
        this.Test1 = document.getElementById('leyeLashes');
        if (this.Test1.checked) {
          this.isShowEyeLashes = false;
        } else {
          this.isShowEyeLashes = true;
        }
      }
      if (type === 'left_appendages_syringingbox') {
        this.Test1 = document.getElementById('lsyringing');
        if (this.Test1.checked) {
          this.isShowSyringing = false;
        } else {
          this.isShowSyringing = true;
        }
      }
      if (type === 'left_appendages_eyelids') {
        this.Test1 = document.getElementById('lEyelids');
        if (this.Test1.checked) {
          this.isShowEyeLids = false;
        } else {
          this.isShowEyeLids = true;
        }
      }
    } else {
      this.isShowEyeLids = false;
    }
  }
}
