import {
  Component,
  OnInit,
  Input,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { Observable, Subject, of, iif } from 'rxjs';
import { MatDialog, MatSnackBar } from '@angular/material';
import { ConfirmationDialogComponent } from '@shared/components/confirmation-dialog/confirmation-dialog.component';
import { IDynamicComponent } from '../../models/dynamic-component';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { takeUntil, filter, concatMap, catchError } from 'rxjs/operators';
import { UserService } from '@core/services';
import { HttpErrorResponse } from '@angular/common/http';
import { ApiResponse } from '@core/models/api-response';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-retinoscopy',
  templateUrl: './retinoscopy.component.html',
  styleUrls: ['./retinoscopy.component.scss']
})
export class RetinoscopyComponent
  implements OnInit, OnDestroy, IDynamicComponent {
  // variable for drug name in select dropdown
  drugNames = [
    'Tropicamide',
    'Tropicamide-p',
    'Atropine',
    'Cyclopentolate',
    'Homide',
    'Dry'
  ];
  retinoscopyForm: FormGroup;
  readonly CURRENT_USER = this.user.getUser();
  // global form validation object
  formvalidation = {
    formValidations: [
      {
        type: 'maxlength',
        message: '* Fields Data Cannot Be More Than 500 Characters'
      },
      { type: 'pattern', message: 'Enter Valid Data' }
    ],
    textBox: [
      { type: 'maxlength', message: 'Field Data Allows Upto 25 Digits' },
      {
        type: 'pattern',
        message: '* Fields Data Allows Only Numbers And Decimal Values'
      }
    ],
    textarea: [
      { type: 'maxlength', message: 'Field Data Allows Upto 25 Digits' },
      {
        type: 'pattern',
        message: '* enter valid data  eg: 30MT'
      }
    ]
  };
  // global form validation pattern
  validator = [
    null,
    [Validators.pattern('^[0-9/.+-]*$'), Validators.maxLength(25)]
  ];
  TextValidator = [null, Validators.maxLength(500)];
  DistanceValidator = [null, [Validators.pattern(/^(([0-9])|([0-9])+\.(([0-9])))+(((MT)|(mt)|(CM)|(cm)){0,1})+$/),
  Validators.maxLength(25)]];
  LodCopyValues: any;
  RodCopyValues: any;

  @Input()
  parentFormGroup: FormGroup;

  @Input()
  disabled: boolean;

  @Input()
  refetchMasterData$: Observable<boolean>;

  @Input()
  componentId: number;

  @Input()
  specialityId: number;

  @Input()
  patientDemographicData: any;

  @Input()
  currentTabId: any;

  @Input()
  isParentFormSubmitted: boolean;

  @Input()
  refreshForm$: Subject<boolean>;

  @Input()
  componentFetchData$: Observable<any>;

  @Output()
  dataReset: EventEmitter<boolean> = new EventEmitter();

  componentDestructionNotifier$ = new Subject();
  patientParamData: any;
  componentData: any;
  message: string;

  constructor(
    private readonly formBuilder: FormBuilder,
    private readonly dialog: MatDialog,
    private readonly user: UserService,
    private readonly emrOphthoService: EmrOpthoApiService,
    private readonly snackbar: MatSnackBar,
    private readonly route: ActivatedRoute
  ) {}
  /**
   * @description This method is for open modal pop up
   */
  openDataDeleteConfirmationModal(message: string) {
    // if (this.componentData) {
      if ( this.patientParamData.pastVisitDetails) {
        this.message = `It is an earlier visit ${this.patientParamData.visitDate}, do you want to delete ?`;
        //  }
      } else {
        this.message = 'Do You want delete Retinoscopy component data?';

   }
        return this.dialog
      .open(ConfirmationDialogComponent, {
        data: {
          title: 'Confirm',
          data: this.message
        }
      })
      .afterClosed();
  }

  /**
   * @description This function is used for constructing payload for delete api
   */
  makeDeleteRequestObject(componentData) {
    const { version_id, emr_default_id } = componentData;
    const { orgId, orgGroupId, regId } = this.CURRENT_USER;

    const request = {
      // patientVisitId: this.patientDemographicData.patientVisitId,
      // patientId: this.patientDemographicData.patientId,
      doctorId: this.patientParamData.doctorId,
      orgGrpId: orgGroupId,
      orgId,
      authenticatedUserId: regId,
      // tabId: this.currentTabId,
      specialityId: this.specialityId,
      data: {
        [this.componentId]: {
          emr_default_id,
          version_id
        }
      }
    };
    return request;
  }

  /**
   * @description getting an api from service file
   */
  deleteField(payload) {
    if (
      payload.data[this.componentId].emr_default_id &&
      payload.data[this.componentId].version_id
    ) {
      return this.emrOphthoService.deleteDataOfDynamicField(payload);
    } else {
      this.retinoscopyForm.reset();
    }
  }

  /**
   * @description This method is used for deleting the component data when the user click the reset button
   * based on key we are calling delete api if emr_default_id and version_id not avoilable then data will
   * be reset from ui side without calling an api
   */
  handleRefresh() {

    if (this .refreshForm$) {

      const predicate = () => {
        const formValue = this .retinoscopyForm.value;
        const { version_id } = formValue;
        let doDelete = false;
        doDelete = doDelete || !!version_id;
        // doDelete = doDelete || child_table.some(val => val.version_id);
        return doDelete;
      };

      this .refreshForm$
        .pipe(
          filter(Boolean),
          concatMap(_ => this .openDataDeleteConfirmationModal('')),
          filter(Boolean),
          concatMap(_ => {
            return iif(
              predicate,
              of(this .makeDeleteRequestObject(this .retinoscopyForm.value))
                .pipe(
                  concatMap(request => {
                    return this .deleteField(request)
                      .pipe(catchError((errorResponse: HttpErrorResponse) => {
                        return iif(
                          () => errorResponse.status > 0,
                          of(< ApiResponse< null>>errorResponse.error),
                          of(new ApiResponse< null>(null, 'E400', []))
                        );
                      }));
                  }
                )
              ),
              of(new ApiResponse< null>(null, 'E200', [])));
          }),
          takeUntil(this .componentDestructionNotifier$)
        )
        .subscribe((response) => {
          if (response.responseCode === 'E200') {
            this .retinoscopyForm.patchValue({ $$isMasterDataSelected: false }, { emitEvent: false });
            this .retinoscopyForm.reset();
            // const tableFormArray = this.getTableDataFormArray();
            // while (tableFormArray.length) {
            //   tableFormArray.removeAt(tableFormArray.length - 1);
            // }
            this .dataReset.emit(true);
          } else {
            this .snackbar.open('Unable to delete the record', 'Dismiss', { duration: 3000 });
          }
        });
    }
  }
  // retinoscopy main form group starts from here
  initForm() {
    this.retinoscopyForm = this.formBuilder.group({
      version_id: null,
      emr_default_id: null,
      right_eye_top: this.validator,
      right_eye_left: this.validator,
      right_eye_right: this.validator,
      right_eye_bottom: this.validator,
      right_eye_va: this.validator,
      right_eye_ha: this.validator,
      right_eye_distance: this.DistanceValidator,
      right_eye_drug_used: null,
      right_eye_remarks: this.TextValidator,
      left_eye_top: this.validator,
      left_eye_left: this.validator,
      left_eye_right: this.validator,
      left_eye_bottom: this.validator,
      left_eye_va: this.validator,
      left_eye_ha: this.validator,
      left_eye_distance: this.DistanceValidator,
      left_eye_drug_used: null,
      left_eye_remarks: this.TextValidator
    });

    if (this.disabled) {
      this.retinoscopyForm.disable();
    } else {
      this.retinoscopyForm.enable();
    }

  }

  copyValues() {
    // console.log(this.retinoscopyForm);
    const rightTopEye = this.retinoscopyForm.get('right_eye_top').value;
    const rightLeftEye = this.retinoscopyForm.get('right_eye_left').value;
    const rightRightEye = this.retinoscopyForm.get('right_eye_right').value;
    const rightBottomEye = this.retinoscopyForm.get('right_eye_bottom').value;
    const rightEyeHa = this.retinoscopyForm.get('right_eye_ha').value;
    const rightEyeva = this.retinoscopyForm.get('right_eye_va').value;
    const rightEyeDistance = this.retinoscopyForm.get('right_eye_distance')
      .value;
    const rightEyeDrugUsed = this.retinoscopyForm.get('right_eye_drug_used')
      .value;
    const RightEyeRemarks = this.retinoscopyForm.get('right_eye_remarks').value;
    this.retinoscopyForm.get('left_eye_top').patchValue(rightTopEye);
    this.retinoscopyForm.get('left_eye_left').patchValue(rightLeftEye);
    this.retinoscopyForm.get('left_eye_right').patchValue(rightRightEye);
    this.retinoscopyForm.get('left_eye_bottom').patchValue(rightBottomEye);
    this.retinoscopyForm.get('left_eye_ha').patchValue(rightEyeHa);
    this.retinoscopyForm.get('left_eye_va').patchValue(rightEyeva);
    this.retinoscopyForm.get('left_eye_distance').patchValue(rightEyeDistance);
    this.retinoscopyForm.get('left_eye_drug_used').patchValue(rightEyeDrugUsed);
    this.retinoscopyForm.get('left_eye_remarks').patchValue(RightEyeRemarks);
    // const leftTopEye = this.retinoscopyForm.get('left_eye_top').value;
    // const leftLeftEye = this.retinoscopyForm.get('left_eye_left').value;
    // const leftRightEye = this.retinoscopyForm.get('left_eye_right').value;
    // const leftBottomEye = this.retinoscopyForm.get('left_eye_bottom').value;
    // const leftEyeHa =  this.retinoscopyForm.get('left_eye_ha').value;
    // const leftEyeva = this.retinoscopyForm.get('left_eye_va').value;
    // const leftEyeDistance = this.retinoscopyForm.get('left_eye_distance').value;
    // const leftEyeDrugUsed = this.retinoscopyForm.get('left_eye_drug_used').value;
    // const leftEyeRemarks = this.retinoscopyForm.get('left_eye_remarks').value;
    // this.retinoscopyForm.get('right_eye_top').patchValue(leftTopEye);
    // this.retinoscopyForm.get('right_eye_left').patchValue(leftLeftEye);
    // this.retinoscopyForm.get('right_eye_right').patchValue(leftRightEye);
    // this.retinoscopyForm.get('right_eye_bottom').patchValue(leftBottomEye);
    // this.retinoscopyForm.get('right_eye_ha').patchValue(leftEyeHa);
    // this.retinoscopyForm.get('right_eye_va').patchValue(leftEyeva);
    // this.retinoscopyForm.get('right_eye_distance').patchValue(leftEyeDistance);
    // this.retinoscopyForm.get('right_eye_drug_used').patchValue(leftEyeDrugUsed);
    // this.retinoscopyForm.get('right_eye_remarks').patchValue(leftEyeRemarks);
  }

  ngOnInit() {
    this.route.queryParams.subscribe(res => {
      this.patientParamData = res;
    });
    this.initForm();
    if (this.parentFormGroup) {
      // console.log(this.parentFormGroup);
      this.parentFormGroup.addControl(
        `${this.componentId}`,
        this.retinoscopyForm
      );
    }
    this.initComponentDataFetchChangeHandler();
    this.handleRefresh();
  }
  ngOnDestroy() {
    if (this.parentFormGroup) {
      this.parentFormGroup.removeControl(`${this.componentId}`);
    }
  }
  initComponentDataFetchChangeHandler() {
    if (this.componentFetchData$) {
      this.componentFetchData$
        .pipe(takeUntil(this.componentDestructionNotifier$))
        .subscribe(this.componentDataFetchChangeHandler.bind(this));
    }
  }

  componentDataFetchChangeHandler(componentsData) {
    const componentData = componentsData[this.componentId];
    if (componentData) {
      this.retinoscopyForm.patchValue(componentData);
      this.componentData = componentData;
    }
  }
}
