import {  Injectable } from '@angular/core';
import { EmrOpthoApiService } from '@core/services/emr-optho.service';
import { IFetchDynamicTabsRequest } from './models/fetch-dynamic-tabs-request';
import { IFetchDynamicTabsResponse } from './models/fetch-dynamic-tabs-response';
import { IUpdateTabRequest } from './models/update-tab-request';

@Injectable({
  providedIn: 'root'
})
export class TabsListService {
  constructor(private readonly emrOpthoApi: EmrOpthoApiService) { }

  getTabs(payload: IFetchDynamicTabsRequest) {
    return this.emrOpthoApi.fetchDynamicTabs<IFetchDynamicTabsResponse[]>(payload);
  }

  updateTabs(request: IUpdateTabRequest) {
    return this.emrOpthoApi.insertOrUpdateTabs<number>(request);
  }

}
